<template>
  <cali-quiz
    v-if="renderedHTML.length > 0"
    v-bind="{selectedItem: selectedItem, htmlContent: renderedHTML}"
    :data-cali-ViewItem="selectedItem.ID"
    data-cali-data-type="html"
    ref="htmlComp"
    class="cali-component"
    :data-cali-index="dataIndex"
  ></cali-quiz>
</template>

<script>
import Vue from "vue";
import { CaliTripExerciseControls } from "./controls";
import { Data } from "../../../California.Data";
import CaliQuiz from "../../CaliQuiz/CaliQuiz.vue";

export default {
  components: {
    CaliQuiz
  },
  name: "cali-tripexercise",
  props: {
    dataIndex: {
      default: 0,
      type: Number
    },
    selectedItem: {
      default: null,
      type: Object
    }
  },
  data() {
    return {
      renderedHTML: ""
    };
  },
  computed: {},
  methods: {
    htmlSrc() {
      return (
        this.$ContentFolder +
        Data.ParseDataURL(
          this.selectedItem,
          this.selectedItem.Data[this.dataIndex]
        ).url
      );
    },

    RenderHTML() {
      const comp = this;
      this.TripExerciseProtocol.Render(this.htmlSrc()).then(function(data) {
        comp.renderedHTML = data;
      });
    }
  },
  created() {
    this.TripExerciseProtocol = new CaliTripExerciseControls(this.selectedItem);
  },
  mounted() {
    this.RenderHTML();
  },
  updated() {},
  beforeDestroy() {}
};
</script>