<template>
  <section v-if="CaliforniaCourse.CurrentItem.ChildCount  >  0" class="cali-collection">
    <h1 v-html="CaliforniaCourse.CurrentItem.Name" class="cali-collection-title"></h1>
    <section class="cali-component-area">
      <template v-for="(url, i) in CaliforniaCourse.CurrentItem.Data">
        <component
          :is="selectComponent(CaliforniaCourse.CurrentItem, url)"
          class="cali-component"
          :data-cali-ViewItem="CaliforniaCourse.CurrentItem.ID"
          v-bind="{selectedItem: CaliforniaCourse.CurrentItem, dataIndex: i}"
          :data-cali-data-type="selectComponent(CaliforniaCourse.CurrentItem, url)"
          :data-cali-index="getIndex()"
          :key="CaliforniaCourse.CurrentItem.ID + '-' + i "
        />
      </template>
      <navigation-children :ParentItem="CaliforniaCourse.CurrentItem"></navigation-children>
    </section>
  </section>
</template>

<script>
import { Data } from "../../../California.Data";
import NavigationChildren from "../Parts/Navigation/Children.vue";
import Vue from "vue";

let comps = Object.assign(
  {
    NavigationChildren
  },
  Data.LoadComponentFolder(
    require.context("../../../California.ContentTypes", true, /\.vue$/)
  )
);

export default {
  name: "collection",
  data() {
    return {
      CaliforniaCourse: this.$CaliforniaCourse
    };
  },
  components: comps,
  beforeRouteUpdate: (to, from, next) => {
    Vue.prototype.$Navigation.FromRoute(to, from, next);
  },
  beforeRouteEnter: (to, from, next) => {
    Vue.prototype.$Navigation.FromRoute(to, from, next);
  },
  created() {
    this.ChildIndex = 0;
  },
  mounted() {
    this.ChildIndex = 0;
  },
  updated() {
    this.ChildIndex = 0;
  },
  methods: {
    selectComponent(child, url) {
      return Data.ParseDataURL(child, url).contenttype;
    },
    getIndex() {
      this.ChildIndex += 1;
      return this.ChildIndex - 1;
    }
  },
  watch: {
    ViewItem: function(newVal, oldVal) {
      this.ChildIndex = 0;
    }
  }
};
</script>
<style lang="less">
.cali-component-area {
  position: relative;
}
</style>
