<template>
  <div id="cali-settings" :data-cali-language="this.$VARIABLES.Language">
    <header>
      <navigation-exit/>
    </header>
    <section class="cali-component-area">
      <section class="cali-component cali-settings">
        <template v-for="(variable, i) in settingsObj">
          <section class="cali-variables-panel" :key="i" v-if="variable.label">
            <h1 v-html="variable.label"></h1>
            <ul class="cali-variable-values">
              <template v-for="(val, j) in variable.values">
                <li v-if="!val.disabled" :key="`li ${j}`">
                  <div class="cali-variable-panel" :key="`${i}${j}`" @click="markButtons($event)">
                    <label>
                      <span v-html="val.label"/>
                      <input
                        v-if="variable.type == 'selectone'"
                        type="radio"
                        :name="`cali-variable-${i}`"
                        :id="`cali-variable-${i}-${j}`"
                        :value="val.value"
                        :data-variable-id="i"
                      >
                      <input
                        v-else-if="variable.type == 'selectmultiple'"
                        type="checkbox"
                        :name="`cali-variable-${i}-${j}`"
                        :id="`cali-variable-${i}-${j}`"
                        :value="val.value"
                        :data-variable-id="i"
                      >
                    </label>
                  </div>
                </li>
              </template>
            </ul>
          </section>
        </template>
        <button id="confirm-settings-button" class="cali-button hidden" ref="continue">
          <span class="label">Continue</span>
        </button>
      </section>
    </section>
  </div>
</template>

<script>
import * as Core from "california_core";
import Vue from "vue";
import { Data } from "../../../California.Data";
import * as Startup from "../../../App.startup";
import NavigationExit from "../Parts/Navigation/Exit";

let comps = {
  NavigationExit
};

export default {
  name: "settings",
  data() {
    return {
      settingsObj: Vue.prototype.$VARIABLES
    };
  },
  computed: {},
  created() {
    this.markButtons = e => {
      let btns = this.$el.querySelectorAll(`.cali-variable-panel`);
      for (let i = 0; i < btns.length; i++) {
        const btn = btns[i];
        const input = btn.querySelector(`input`);
        if (input.checked) {
          btn.classList.add("selected");
        } else {
          btn.classList.remove("selected");
        }
      }
    };

    this.updateVars = () => {
      for (let key in this.settingsObj) {
        if (typeof this.settingsObj[key] === "string") continue;
        this.settingsObj[key].value = [];
        let inputs = this.$el.querySelectorAll(
          `input[data-variable-id="${key}"]:checked`
        );
        for (let j = 0; j < inputs.length; j++) {
          this.settingsObj[key].value.push(inputs[j].value);
        }
      }
      window.California.Settings = this.settingsObj;
    };
    this.contentFolder = () => {
      let contentFolder = Core.Net.Path.FromRoot("");

      return contentFolder;
    };
    this.preSelect = () => {
      for (let key in this.settingsObj) {
        let varObj = this.settingsObj[key];
        if (!varObj.default) continue;
        for (let i = 0; i < varObj.default.length; i++) {
          let inputField = this.$el.querySelector(
            `input[value="${varObj.default[i]}"]`
          );
          if (inputField) {
            varObj.value = [].concat(varObj.default[i]);
            inputField.setAttribute("checked", "checked");
          }
        }
      }
    };

    this.startCourse = () => {
      this.$CaliforniaSettingsApp.$destroy();
      this.$CaliforniaSettingsApp.$el.parentElement.removeChild(
        this.$CaliforniaSettingsApp.$el
      );

      const courseLang = Vue.prototype.$VARIABLES.Language
        ? "_" + Vue.prototype.$VARIABLES.Language
        : "";
      const courseFile = `${this.contentFolder()}courseJSON.json`;

      const localizedCourseFile = `${this.contentFolder()}courseJSON${courseLang}.json`;
      new Core.IO.Reader().Exists(localizedCourseFile).then(result => {
        new Startup.Content(
          this.contentFolder(),
          result ? localizedCourseFile : courseFile
        );
      });
    };

    this.confirmSettings = () => {
      this.updateVars();
      if (
        Object.keys(this.settingsObj).length == 1 &&
        this.settingsObj.Language
      ) {
        this.settingsObj = Object.assign(
          {},
          { Language: this.settingsObj.Language.value[0] }
        );
        Vue.prototype.$VARIABLES = Object.assign({}, this.settingsObj);

        const settingsFile = `${this.contentFolder()}variables.json`;
        const localizedSettingsFile = `${this.contentFolder()}variables_${
          this.settingsObj.Language
        }.json`;
        new Core.IO.Reader()
          .Exists(localizedSettingsFile)
          .then(result => {
            return new Startup.PreSettings(
              this.contentFolder(),
              result ? localizedSettingsFile : settingsFile
            ).Load();
          })
          .then(data => {
            if (Object.keys(data).length > 0) {
              this.settingsObj = Object.assign({}, this.settingsObj, data);
              Vue.prototype.$VARIABLES = Object.assign({}, this.settingsObj);
            } else {
              this.startCourse();
            }
          });
      } else {
        this.startCourse();
      }
    };
    this.init = () => {
      this.$refs.continue.classList.add("hidden");
      this.preSelect();
      this.markButtons();

      const varPanels = this.$el.querySelectorAll(`.cali-variables-panel`);
      if (varPanels.length > 0) {
        const settingButton = this.$el.querySelector(
          `#confirm-settings-button`
        );
        if (settingButton) {
          settingButton.addEventListener("click", this.confirmSettings);
          this.$refs.continue.classList.remove("hidden");
        }
      }
    };
  },
  mounted() {
    this.init();
  },
  updated() {
    this.init();
  },
  methods: {},
  components: comps
};
</script>

<style lang="less" scoped>
#confirm-settings-button {
  &.hidden {
    display: none;
  }
}
</style>
