import EventBus from "../../Vue.EventBus";
import Vue from "vue";

export class CaliMediaControlKeyframes {
  private element: HTMLElement;
  private mediaElement: HTMLVideoElement;
  private keyframeTrackTag: HTMLTrackElement;
  private onSubPageChange: Function;
  private onLoad: any;
  private onPlay: any;
  private onTimeUpdate: any;
  private cuedKeyframe: number = -1;

  constructor() {
    this.element = document.createElement("div");
    this.mediaElement = document.createElement("video");
    this.keyframeTrackTag = document.createElement("track");
    this.onSubPageChange = this.gotoKeyframe.bind(this);
    this.onLoad = this.onLoadHandler.bind(this);
    this.onPlay = this.onPlayHandler.bind(this);
    this.onTimeUpdate = this.onTimeUpdateHandler.bind(this);
  }

  public Attach(element: HTMLElement, mediaElement: HTMLVideoElement) {
    this.element = element;
    this.mediaElement = mediaElement;
  }

  private onLoadHandler() {
    if (this.cuedKeyframe > -1) {
      this.gotoKeyframe(this.cuedKeyframe);
    } else if (Vue.prototype.$Navigation.SubPageLocation > -1) {
      this.gotoKeyframe(Vue.prototype.$Navigation.SubPageLocation);
    }
  }

  private onPlayHandler() {
    this.element.classList.remove("paused-by-keyframe");
  }

  private onTimeUpdateHandler() {
    this.checkForKeyframe();
  }

  private execKeyframeCommand(command: string) {
    if (this.mediaElement.currentSrc == "") return;

    var isPlaying = !this.mediaElement.paused && !this.mediaElement.ended;
    var isShowing =  !this.element.classList.contains("cali-media-hidden");

    command = command.toLowerCase();

    if (command == "firsttime") {
      const item = Vue.prototype.$CaliforniaCourse.CurrentItem;
      let willPlay = Vue.prototype.$CaliforniaCourse.CurrentItem.Progress == 0;

      if (item.TotalChildCount > 0) {
        willPlay = item.Progress < 1 / (item.TotalChildCount + 1);
      }
      command = willPlay ? "play" : "pause";
    }
    let wait: string = "0";
    let waitNum: number = 0;
    switch (command) {
      case "pause":
        this.element.classList.remove("cali-media-hidden");
        this.element.classList.remove("cali-media-invisible");
        this.element.classList.add("cali-media-showing");
        this.element.classList.add("paused-by-keyframe");
        if (isPlaying) {
          this.mediaElement.pause();
        }
        break;
      case "play":
        this.element.classList.remove("cali-media-hidden");
        this.element.classList.remove("cali-media-invisible");
        this.element.classList.remove("paused-by-keyframe");
        this.element.classList.add("cali-media-showing");
        wait = window.getComputedStyle(this.element).transitionDuration;
        waitNum = parseFloat(wait);
        waitNum = waitNum > 0 ? waitNum : 0.2;
        if (!isPlaying) {
          setTimeout(() => {
            this.mediaElement.play();
          }, (isShowing ? 0 : waitNum * 1000));
        }
        break;
      case "hide":
        this.element.classList.add("cali-media-hidden");
        this.element.classList.remove("cali-media-showing");
        if (!isPlaying) {
          this.element.classList.add("paused-by-keyframe");
          this.mediaElement.pause();
        }
        break;
      case "invisible":
        this.element.classList.remove("cali-media-hidden");
        this.element.classList.remove("cali-media-showing");
        this.element.classList.remove("paused-by-keyframe");
        this.element.classList.add("cali-media-invisible");
        wait = window.getComputedStyle(this.element).transitionDuration;
        waitNum = parseFloat(wait);
        waitNum = waitNum > 0 ? waitNum : 1;
        if (!isPlaying) {
          setTimeout(() => {
            this.mediaElement.play();
          }, (isShowing ? 0 : waitNum * 1000));
        }
        break;
      default:
        this.element.classList.remove("cali-media-hidden");
        this.element.classList.remove("cali-media-invisible");
        this.element.classList.add("cali-media-showing");
        let commandParts = command.split("|");
        Vue.prototype.$Navigation.FromExternalCommand(commandParts[0], commandParts[1]);
    }
  }

  private gotoKeyframe(index: number) {
    if (this.mediaElement.currentSrc == "") return;

    const cues: TextTrackCueList = this.keyframeTrackTag.track.cues;
    if (!cues || index >= cues.length) {
      if (!cues && this.cuedKeyframe < 0 && index > 0) this.cuedKeyframe = index;
      return;
    }

    const cue: TextTrackCue = cues[index];
    if (cue) {
      if (cue.text.toLowerCase() == "silent") {
        this.mediaElement.dataset.currentState = "silent";
        this.execKeyframeCommand("hide");
        return;
      }
      if (this.mediaElement.dataset.silent !== "true") {
        this.mediaElement.dataset.silent = "true";
        this.mediaElement.currentTime = cue.startTime;
      }
      this.mediaElement.dataset.currentState = cue.text.trim().length > 0 ? cue.text : "undefined";
      this.execKeyframeCommand(cue.text);
    }
  }

  private checkForKeyframe() {
    if (this.mediaElement.dataset.silent == "true") {
      this.mediaElement.dataset.silent = "false";
      return;
    }
    const currentTime = parseFloat(this.mediaElement.currentTime.toFixed(1));
    const cues = this.keyframeTrackTag.track.cues || [];
    for (let i = 0; i < cues.length; i++) {
      const cue = cues[i];
      if (cue.startTime <= currentTime && cue.endTime > currentTime) {
      }
      if (
        cue.startTime <= currentTime &&
        cue.endTime > currentTime &&
        this.mediaElement.dataset.currentKey != i.toString() &&
        (this.mediaElement.dataset.currentState || "").toLowerCase() !== "silent"
      ) {
        this.mediaElement.dataset.currentKey = i.toString();
        if (Vue.prototype.$Navigation.SubPageLocation != i) {
          this.mediaElement.dataset.silent = "true";
          Vue.prototype.$Navigation.ToSubPage(i);
        }
        break;
      }
    }
  }

  private keyFrameTimer: any;

  public Render(data: string) {
    const blob = new Blob([data as BlobPart], { type: "text/vtt" });
    const blobUrl = URL.createObjectURL(blob);

    this.keyframeTrackTag = document.createElement("track");
    this.keyframeTrackTag.setAttribute("kind", "chapters");
    this.keyframeTrackTag.setAttribute("default", "default");
    this.keyframeTrackTag.setAttribute("src", blobUrl);
    this.mediaElement.appendChild(this.keyframeTrackTag);

    this.keyframeTrackTag.addEventListener("load", this.onLoad);
    this.mediaElement.addEventListener("play", this.onPlay);
    this.keyFrameTimer = (<any>window).setInterval(this.onTimeUpdate, 10);
    EventBus.$on("ToMediaSubPage", this.onSubPageChange);
  }

  public GotoKeyframe(index: number) {
    this.gotoKeyframe(index);
  }

  public Destroy() {
    (<any>window).clearInterval(this.keyFrameTimer);

    this.keyframeTrackTag.removeEventListener("load", this.onLoad);
    if (this.keyframeTrackTag.parentElement) {
      this.keyframeTrackTag.parentElement.removeChild(this.keyframeTrackTag);
    }

    this.mediaElement.removeEventListener("play", this.onPlay);
    (<any>window).clearInterval(this.onTimeUpdate);
    EventBus.$off("ToMediaSubPage", this.onSubPageChange);
  }
}
