import * as Core from "california_core";
import { ContentResize, ContentResizeModes } from "../../California.ContentSize";
import EventBus from "../../Vue.EventBus";

export class CaliImageControls {
  private item: Core.StructureItem;

  constructor(item: Core.StructureItem) {
    this.item = item;
  }

  private resizeToFixed(imageContainerElement: HTMLElement) {
    let size = this.item.Settings.Size;

    if (window.innerWidth < 769 && this.item.Settings.SmallSize) {
      size = this.item.Settings.SmallSize;
    }

    let index = parseInt(imageContainerElement.dataset.caliIndex || "", 10);
    if (!isNaN(index)) size = ContentResize.GetPropForDataIndex(size, index);
    imageContainerElement.style.display = "inline-block";
    imageContainerElement.style.width = size.width;
    imageContainerElement.style.height = size.height;
  }

  private resizeToContent(imageContainerElement: HTMLElement) {
    imageContainerElement.style.width = "auto";
    imageContainerElement.style.height = "auto";
  }

  private resizeToParent(imageContainerElement: HTMLElement) {
    if (!imageContainerElement.parentElement) return;
    imageContainerElement.style.width = imageContainerElement.parentElement.offsetWidth + "px";
    imageContainerElement.style.height = imageContainerElement.parentElement.offsetHeight + "px";
  }

  private resize(imageContainerElement: HTMLElement) {
    const mode = this.item.Settings.ResizeMode
      ? this.item.Settings.ResizeMode.toLowerCase()
      : ContentResizeModes.Parent;
    switch (mode) {
      case ContentResizeModes.Content:
        this.resizeToContent(imageContainerElement);
        break;
      case ContentResizeModes.Parent:
        this.resizeToParent(imageContainerElement);
        break;
      case ContentResizeModes.Fixed:
        this.resizeToFixed(imageContainerElement);
        break;
    }
  }

  private place(imageContainerElement: HTMLElement) {
    const index = parseInt(imageContainerElement.dataset.caliIndex || "", 10);
    let position = !isNaN(index)
      ? ContentResize.GetPropForDataIndex(this.item.Settings.Position, index)
      : null;

    if (window.innerWidth < 769 && this.item.Settings.SmallPosition) {
      position = ContentResize.GetPropForDataIndex(this.item.Settings.SmallPosition, index);
    }
    if (position) {
      imageContainerElement.style.position = "absolute";
      imageContainerElement.style.top = position.y;
      imageContainerElement.style.left = position.x;
    }
  }

  private layout(imageContainerElement: HTMLElement) {
    let fit = this.item.Settings.BoxFit || null;
    if (window.innerWidth < 769 && this.item.Settings.SmallBoxFit) {
      fit = this.item.Settings.SmallBoxFit;
    }
    if (fit) {
      imageContainerElement.style.backgroundSize = fit;
    }
    
    let placement = this.item.Settings.BoxAlign || null;
    if (window.innerWidth < 769 && this.item.Settings.SmallBoxAlign) {
      placement = this.item.Settings.SmallBoxAlign;
    }
    if (placement) {
      imageContainerElement.style.backgroundPosition = placement;
    }
  }

  public Render(url: string, imageContainerElement: HTMLElement) {
    const img = new Image();

    EventBus.$on("windowResize", () => {
      this.resize(imageContainerElement);
      this.place(imageContainerElement);
    });
    img.addEventListener("load", () => {
      this.resize(imageContainerElement);
      this.place(imageContainerElement);
      this.layout(imageContainerElement);
      imageContainerElement.style.backgroundImage = `url("${url}")`;
      imageContainerElement.dataset.loaded = "true";
    });
    img.src = url;
  }
}
