<template>
  <button
    class="cali-button navigation-exit"
    :title="$t('message.EXIT_BUTTON_LABEL', $i18n.locale)"
    @click="this.exitCourse"
  >
    <span class="label" v-html="$t('message.EXIT_BUTTON_LABEL', $i18n.locale)"></span>
  </button>
</template>

<script>

import Vue from "vue";

export default {
  name: "navigation-exit",
  data() {
    return {
            SCORM: Vue.prototype.$SCORM
    };
  },
  methods: {

//Section 2.1.3 of the SCORM 2004 3rd & 4th Edition Specs puts it like this:

// If the window in which the SCO was launched is a top-level window (i.e., the
// window has no parent window, but it has an opener) then the SCO may attempt to
// close the window after calling Terminate().

// []

// If the window is not a top-level window (i.e., the window has a parent window),
// the SCO may not act on the parent window or any window in the chain of parents.
// For example, a SCO is not allowed to attempt to close the top window, unless it is
// its own window.

    exitCourse: function() {
      if (!confirm(this.$t("message.EXIT_BUTTON_MESSAGE", this.$i18n.locale))) {
        return;
      }
      if (window.top == window.self) {
               this.SCORM.ExitLMS();
        window.close();
      } else {
               this.SCORM.ExitLMS();
        window.top.close();
      }
      if (!window.closed) {
 
      }
    }
  },
  computed: {}
};
</script>