<template>
  <nav class="cali-dropdown">
    <button
      class="cali-button navigation-toggle-menu"
      @click="this.toggleMenu"
      ref="dropDownButton"
      :title="$t('message.TOGGLE_MENU_BUTTON_LABEL', $i18n.locale)"
    >
      <span class="label" v-html="$t('message.TOGGLE_MENU_BUTTON_LABEL', $i18n.locale)"></span>
    </button>
    <navigation-tree :ParentItem="ParentItem" ref="dropDownMenu"></navigation-tree>
  </nav>
</template>

<script>
import NavigationTree from "./Tree.vue";

export default {
  name: "navigation-dropdown",
  props: ["ParentItem"],
  components: {
    NavigationTree
  },
  methods: {
    toggleMenu() {
      const menuElement = this.$refs.dropDownMenu.$el;
      const buttonElement = this.$refs.dropDownButton;

      if (menuElement.classList.contains("active")) {
        menuElement.classList.remove("active");
        buttonElement.classList.remove("active");
      } else {
        menuElement.classList.add("active");
        buttonElement.classList.add("active");
      }
    }
  }
};
</script>

<style lang="less">
.cali-dropdown {
  ul {
    position: absolute;
    top: 0px;
    left: 100%;
    height: 0px;
    list-style: none;
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
  }
  li {
    position: relative;
    box-sizing: border-box;
    overflow: hidden;
  }
  .cali-has-sub-menu:hover {
    overflow: visible;
    > ul {
      height: auto;
      opacity: 1;
    }
  }
  button {
    text-align: left;
    overflow: hidden;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  > ul {
    position: relative;
    top: 0px;
    left: 0px;
    width: 100%;
    height: auto;
    table-layout: fixed;
    white-space: nowrap;
    opacity: 1;
    > li {
      display: inline-block;
      height: auto;
      vertical-align: top;
      > ul {
        position: absolute;
        top: 100%;
        left: 0px;
        z-index: 99;
      }
    }
  }
}
</style>