import * as Core from "california_core";
import Vue from "vue";
import { Data } from "../../../California.Data";

export class CaliTripQuizControls {
  private fileReader: Core.IO.Reader;
  private caliItem: Core.StructureItem;
  private urlTopicID: string = "";
  private errorMsgCount: number;
  private dataLength: number = 0;

  constructor(item: Core.StructureItem) {
    this.errorMsgCount = 0;
    this.fileReader = new Core.IO.Reader();
    this.caliItem = item;
  }

  private getItemIDFromURL(url: string = ""): string {
    let fileName = url.substring(url.lastIndexOf("/") + 1);
    const TRIP_GUID_LENGTH = 32;
    const GUIDS = fileName.split("-").filter(part => part.length == TRIP_GUID_LENGTH);
    const GUID = GUIDS[GUIDS.length - 1];
    return decodeURI(GUID);
  }

  private setTypeClasses(doc: HTMLElement) {
    const pageTitle = doc.querySelector("h1");
    if (pageTitle) pageTitle.classList.add("page-title");
  }

  private removeEmptySegments(doc: HTMLElement) {
    const segments = Array.from(doc.querySelectorAll(".segment"));
    segments.forEach((seg: Element, index: number) => {
      if (!seg.textContent || seg.textContent.replace(/\s/g, "").length < 1) {
        if (seg.parentElement) {
          seg.parentElement.removeChild(seg);
        }
      }
    });
  }

  private insertFeedbackScores(doc: HTMLElement) {
    const existingScore = doc.querySelector("[data-california-quiz-score]");
    const existingScoreParagraph = existingScore && existingScore.parentElement;
    if (!existingScoreParagraph) return;

    const segments = Array.from(doc.querySelectorAll(".segment"));
    segments.forEach((seg: Element, index: number) => {
      const scoreInSegment = seg.querySelector("[data-california-quiz-score]");
      if (!scoreInSegment) {
        seg.insertBefore(existingScoreParagraph.cloneNode(true), seg.firstChild);
      }
    });
  }

  private convertlinks(segment: Element) {
    const links = Array.from(segment.querySelectorAll(".xref"));
    links.forEach((link: Element) => {
      const anchor = link.querySelector("a");
      const href = anchor ? anchor.getAttribute("href") : "";
      let target = this.getItemIDFromURL(href || "");

      const btn = document.createElement("button");
      btn.classList.add("cali-button");
      const lbl = document.createElement("span");
      lbl.classList.add("label");
      lbl.innerHTML = (anchor && anchor.textContent) || "";
      btn.appendChild(lbl);

      // HACK OHOY!!!
      console.log(this.urlTopicID);
      if (this.urlTopicID == "d4b6887637d9705ec0a8018857a89af0" && target == "a90d0e4caba73317c0a801887d6e20c2") {
        console.log("HACK");
        target = "c78ecbaf37e77d1ec0a8018808ad7fa0";
      }

      if (Data.Course.GetItemByID(target)) {
        btn.setAttribute("data-california-navigation", "navToID|" + target);
      } else {
        btn.setAttribute("data-california-navigation", "navToParent");
      }

      if (link.parentElement) {
        link.parentElement.insertBefore(btn, link);
        if (link.parentElement) {
          link.parentElement.removeChild(link);
        }
      }
    });
  }

  private renderFeedback(doc: HTMLElement) {
    const article = doc.querySelector(".article-ref");
    if (article) (<HTMLElement>article).dataset.californiaQuizFeedback = "standard";
    const segments = Array.from(doc.querySelectorAll(".segment"));
    segments.forEach((seg: Element, index: number) => {
      if (index == 0) {
        (<HTMLElement>seg).dataset.californiaQuizResult = "passed";
      } else {
        (<HTMLElement>seg).dataset.californiaQuizResult = "failed";
      }

      this.removelinks(seg);
      this.cleanEmptyParagraphs(seg);
      this.convertlinks(seg);
    });
  }

  private removelinks(segment: Element) {
    const links = Array.from(segment.querySelectorAll(".xref"));
    links.forEach((link: Element) => {
      const anchor = link.querySelector("a");
      const href = anchor ? anchor.getAttribute("href") : "";
      const target = this.getItemIDFromURL(href || "");
      if (target != this.urlTopicID || !anchor) return;
      if (anchor.parentElement && anchor.parentElement.childElementCount == 1) {
        if (anchor.parentElement.parentElement) {
          anchor.parentElement.parentElement.removeChild(anchor.parentElement);
        }
      } else {
        if (anchor.parentElement) {
          anchor.parentElement.removeChild(anchor);
        }
      }
    });
  }

  private addCloseButton() {
    return `
            <button class="cali-button secondary" data-california-navigation="navToSubPage|0">
            <span class"label">${Vue.prototype.$CaliforniaApp.$t(
              "message.CLOSE_BUTTON_LABEL",
              Vue.prototype.$CaliforniaApp.$i18n.locale
            )}</span>
            </button>
            `;
  }

  private cleanEmptyParagraphs(segment: Element, panel?: Element) {
    const ps = Array.from(segment.querySelectorAll("p"));
    ps.forEach((p: Element) => {
      if (p.parentElement != segment) return;
      if (panel) {
        if (p.textContent && p.textContent.replace(/\s/g, "").length > 0) {
          panel.innerHTML += p.outerHTML;
        }
        if (p.parentElement) {
          p.parentElement.removeChild(p);
        }
      } else if (p.textContent && p.textContent.replace(/\s/g, "").length == 0) {
        if (p.parentElement) {
          p.parentElement.removeChild(p);
        }
      }
    });
  }

  private async renderAlternatives(segment: Element, index: number, recursive: boolean = false) {
    const alternatives = Array.from(segment.querySelectorAll("li"));
    if (alternatives.length == 0 && !recursive) {
      try {
        const extData = await this.fileReader.Text(
          `content/trip/${this.urlTopicID}-${index + 1}.html`
        );
        segment.innerHTML = extData + segment.innerHTML;
        if (extData.indexOf("<iframe") != -1) {
          segment.classList.add("iframe-question");
        }
        this.renderAlternatives(segment, index, true);
      } catch (er) {}
    } else {
      if (alternatives[0] && alternatives[0].parentElement) {
        alternatives[0].parentElement.classList.add("cali-alternatives");
      }
      alternatives.forEach((alternative: Element, i: number) => {
        alternative.setAttribute("data-california-quiz-alternative", i.toString());
        if (alternative.querySelector("img")) {
          alternative.classList.add("contains-image");
        }
        const isBoldCorrect = alternative.querySelector("b.b-element");
        const isClassCorrect = alternative.querySelector(".correct");
        let iscorrectAnswer: boolean = isBoldCorrect != null || isClassCorrect != null;
        if (!iscorrectAnswer) {
          const correctValues = this.caliItem.Settings.CorrectAnswers || [];
          iscorrectAnswer = correctValues[index] == i + 1;
        }
        if (iscorrectAnswer) {
          alternative.setAttribute("data-california-quiz-correct", "true");
          if (isBoldCorrect) {
            isBoldCorrect.outerHTML = isBoldCorrect.innerHTML;
          }
        }
      });
    }
  }

  private renderInputBoxes(doc: Element, index: number) {
    const inputBoxPanel = document.createElement("div");
    inputBoxPanel.classList.add("cali-quiz-input-boxes");
    let subindex = 0;

    const children = Array.from(doc.querySelectorAll("p"));
    children.forEach((child: Element) => {
      if ((child.textContent || "").indexOf("%VALUE%") != -1) {
        const inputContainer = document.createElement("div");
        const correctValues = this.caliItem.Settings.CorrectAnswers || ["N/A"];
        let correctValue = correctValues[index] || correctValues[0];
        if (typeof correctValue != "string") {
          correctValue = correctValue[subindex];
        }

        inputContainer.setAttribute("data-california-quiz-input", "true");
        inputContainer.setAttribute("data-california-quiz-input-correct-value", correctValue);
        inputContainer.innerHTML = child.innerHTML.replace(
          /%VALUE%/g,
          `<input type="text"></input>`
        );
        inputBoxPanel.appendChild(inputContainer);
        if (child.parentElement) {
          child.parentElement.removeChild(child);
        }
        subindex += 1;
      }
    });

    doc.appendChild(inputBoxPanel);
  }

  private async parseQuestions(doc: HTMLElement) {
    const segments = Array.from(doc.querySelectorAll(".segment"));
    let index = 0;
    for (const seg of segments) {
      if ((seg.textContent || "").length > 0) {
        let link = seg.querySelector(".xref a");
        if (
          link &&
          (link.textContent || "")
            .toLowerCase()
            .trim()
            .indexOf("error") == 0
        ) {
          const feedbacks = ["ampError", "ohmError"];
          const wrapper = document.createElement("div");
          wrapper.setAttribute("data-california-quiz-feedback", feedbacks[this.errorMsgCount]);
          wrapper.setAttribute(
            "data-california-sub-page",
            (this.dataLength + this.errorMsgCount + 1).toString()
          );
          if (seg.parentElement) {
            seg.parentElement.insertBefore(wrapper, seg);
            seg.parentElement.setAttribute("data-california-sub-page", "0");
            wrapper.appendChild(seg);
          }

          this.removelinks(seg);
          seg.innerHTML += this.addCloseButton();
          this.errorMsgCount += 1;
          continue;
        }

        const wrapper = document.createElement("div");
        wrapper.setAttribute("data-california-question", index.toString());

        if (seg.parentElement) {
          seg.parentElement.insertBefore(wrapper, seg);
          wrapper.appendChild(seg);
        }

        this.removelinks(seg);
        const panel = document.createElement("div");
        panel.classList.add("question-panel");
        seg.insertBefore(panel, seg.firstChild);
        this.cleanEmptyParagraphs(seg, panel);
        this.renderInputBoxes(panel, index);
        await this.renderAlternatives(seg, index);

        index += 1;
      }
    }
    return doc;
  }

  public Render(url: string, dataIndex: number, dataLength: number): Promise<string> {
    this.dataLength = dataLength;
    return new Promise((resolve, reject) => {
      this.fileReader
        .Text(url)
        .then((data: string) => {
          this.urlTopicID = this.getItemIDFromURL(url);
          const tmpDoc = document.createElement("section");
          data = data
            .replace(/%SCORE%/gi, `<span data-california-quiz-score="true"></span>`)
            .replace(/%MAX%/gi, `<span data-california-quiz-max-score="true"></span>`)
            .replace(/%PASS%/gi, `<span data-california-quiz-passing-score="true"></span>`);
          tmpDoc.innerHTML = data;
          this.setTypeClasses(tmpDoc);
          this.removeEmptySegments(tmpDoc);
          if (dataIndex == dataLength - 2) {
            this.renderFeedback(tmpDoc);
            this.insertFeedbackScores(tmpDoc);
            return tmpDoc;
          } else {
            return this.parseQuestions(tmpDoc);
          }
        })
        .then((doc: HTMLElement) => {
          resolve(`${doc.innerHTML}`);
        })
        .catch((reason: any) => {
          reject(reason);
        });
    });
  }
}
