<template>
  <div class="cali-navigation-breadcrumbs">
      <template v-for="(item, index) in itemPath">
        <navigation-to-child :LinkItem="item" :key="item.ID"/>
        <span class="divider" v-if="index < itemPath.length-1" :key="item.ID + 'divider'"></span>
      </template>
  </div>
</template>

<script>
import EventBus from "../../../../Vue.EventBus";
import NavigationToChild from "./ToChild";

export default {
  name: "navigation-breadcrumbs",
  props: [],
  components: {
    NavigationToChild
  },
  data() {
    return {
      CaliforniaCourse: this.$CaliforniaCourse
    };
  },
  computed: {
    itemPath() {
      let itemList = [];
      let item = this.CaliforniaCourse.CurrentItem;
      while (item) {
        itemList.push(item);
        item = item.Parent;
      }
      return itemList.reverse();
    }
  },
  created() {},
  mounted() {}
};
</script>