export class CaliMediaControlUtil{

public static FolderPath(url: string) {
    let path = url;
    return path.substr(0, path.lastIndexOf("/"));
  }

  public static FileNemWoExtension(url: string) {
    let name = url;
    return name.substring(name.lastIndexOf("/") + 1, name.lastIndexOf("."));
  }

}
