<template>
  <button
    class="cali-button navigation-to-subpage"
    :class="[directionClassname]"
    :title="CaliforniaCourse.CurrentItem.Name"
    :disabled="isDisabled"
    @click="navigate"
  >
    <span class="label" v-html="directionLabel"></span>
  </button>
</template>

<script>
export default {
  name: "navigation-to-subpage",
  props: ["NavigationDirection"],
  data() {
    return {
      CaliNavigation: this.$Navigation,
      CaliforniaCourse: this.$CaliforniaCourse
    };
  },
  created() {
    this.navigate = () => {
      this.NavigationDirection == "back"
        ? this.$Navigation.ToPreviousSubPage()
        : this.$Navigation.ToNextSubPage();
    };
  },
  computed: {
    directionClassname() {
      return this.NavigationDirection == "back" ? "prev" : "next";
    },
    directionLabel() {
      return this.NavigationDirection == "back"
        ? this.$t("message.PREVIOUS_BUTTON_LABEL", this.$i18n.locale)
        : this.$t("message.NEXT_BUTTON_LABEL", this.$i18n.locale);
    },
    isDisabled() {
      if (
        this.CaliNavigation.NumberOfSubPages == 0 ||
        (this.CaliNavigation.SubPageLocation == 0 && this.NavigationDirection == "back")
      ) {
        return true;
      }
      return false;
    }
  }
};
</script>