import * as Core from "california_core";
import Vue from "vue";
import VueI18n from "vue-i18n";
import { Data } from "../California.Data";
import { Localization } from "../California.Localization";
import { RouterConfig } from "../California.Router/SlideShow";
import CaliApp from "../Vue.Components/Slideshow-CaliApp.vue";
import SCORM from "../California.SCORM";
import Progress from "../California.Progress";
import Navigation from "../California.Navigation";
import "custom-event-polyfill";

export class Content {
  private SCORMInstance: SCORM = new SCORM();
  private contentFolder: string;
  private structureFile: string;
  private i18n: VueI18n;

  constructor(contentFolder: string, structureFile: string) {
    this.contentFolder = contentFolder;
    this.structureFile = structureFile;
    this.SCORMInstance.StartLMS();
    Vue.prototype.$SCORM = this.SCORMInstance;
    Vue.prototype.$Navigation = new Navigation();
    Vue.prototype.$ContentFolder = this.contentFolder;
    this.i18n = this.initLocalization();
    this.StartVueApp();
  }

  private initLocalization(): VueI18n {
    Vue.use(VueI18n);
    return new VueI18n({
      locale: "en",
      fallbackLocale: "en",
      messages: Localization.UIStrings
    });
  }

  private loadStructure(structureFile: string): Promise<boolean> {
    return new Promise((resolve, reject) => {
      let retry = false;
      let filterValue =
        (Vue.prototype.$VARIABLES &&
          Vue.prototype.$VARIABLES.contentfilter &&
          Vue.prototype.$VARIABLES.contentfilter.value) ||
        [];

      let loadAndMerge = () => {
        Data.LoadCourse(structureFile, filterValue)
          .then(() => {
            window.dispatchEvent(new CustomEvent("CaliforniaStartup"));
            Data.Course = Progress.Setup(Data.Course);
            resolve(true);
          })
          .catch(reason => {
            Progress.ClearProgress();
            if (!retry) {
              retry = true;
              loadAndMerge();
            } else {
              reject(false);
            }
          });
      };
      loadAndMerge();
    });
  }

  public StartVueApp() {
    (<any>window).California.Variables = Vue.prototype.$VARIABLES;
    this.loadStructure(this.structureFile).then(() => {
      (<any>window).California.Course = Data.Course;
      (<any>window).California.ExternalCommand = Vue.prototype.$Navigation.FromExternalCommand;
      Vue.prototype.$CaliforniaCourse = Data.Course;
      Vue.prototype.$CaliforniaCore = Core;
      let router = new RouterConfig().Create();
      let i18n = this.i18n;
      if (Vue.prototype.$VARIABLES.Language) {
        i18n.locale = Vue.prototype.$VARIABLES.Language.split("-")[0].toLowerCase();
      }
      Vue.prototype.$CaliforniaApp = new Vue({
        el: "#cali-app",
        router,
        i18n,
        template: "<CaliApp/>",
        components: { CaliApp }
      });
    });
  }
}
