import * as Core from "california_core"

export class CaliXylemeControls {

    private fileReader: Core.IO.Reader
    private win: any = <any>window
    constructor() {
        this.fileReader = new Core.IO.Reader();
        this.win.fileCache = this.win.fileCache || {}
        this.win.topicCache = this.win.topicCache || {}
    }

    private unwrap(element: Element) {
        let parent = element.parentNode;

        if (!parent) return;
        while (element.firstChild) {
            parent.insertBefore(element.firstChild, element);
        }
        parent.removeChild(element);
    }



    private findNonRoleElement(element: Element, nodesToRemove: Array<Element>): Element {

        if (element.parentElement && element.hasAttribute("xy:guid")) {

            let myFilterSelector = "[*|guid=\"" + element.getAttribute("xy:guid") + "\"] > FilterMetadata"

            let targetOutput: Element | null = element.parentElement.querySelector(`${myFilterSelector} > TargetOutputs > TargetOutput`)
            let targetOutputValue: string = targetOutput && targetOutput.textContent ? targetOutput.textContent.trim().toLowerCase() : "";
            let targetAudience: Element | null = element.parentElement.querySelector(`${myFilterSelector} > TargetAudiences > TargetAudience`)
            let targetAudienceValue: string = targetAudience && targetAudience.textContent ? targetAudience.textContent.trim().toLowerCase() : "";


            let outputValuesToKeep: Array<string> = ["slidedeck(powerpoint)"]
            let audienceValuesToKeep: Array<string> = ["", "student"]
            let outputValuesToAvoid: Array<string> = ["printedexam", "handouts", "observation"]
            let audienceValuesToAvoid: Array<string> = []

            let shouldKeep = outputValuesToKeep.indexOf(targetOutputValue) > -1 || audienceValuesToKeep.indexOf(targetAudienceValue) > -1
            let shouldAvoid = outputValuesToAvoid.indexOf(targetOutputValue) > -1 || audienceValuesToAvoid.indexOf(targetAudienceValue) > -1

            if (!shouldKeep || shouldAvoid) {
                nodesToRemove.push(element);
            }

        }
        return element;
    }


    private getListType(element: Element): string {

        if (element.hasAttribute("ListMarker")) {
            let marker = element.getAttribute("ListMarker") || ""
            switch (marker.toLowerCase()) {
                case "numeric":
                    return "ol"
            }
        }
        return "ul";
    }

    private createTable(table: Element): Element {
        let newTable = document.createElement("table")
        newTable.setAttribute("style",
            `width: ${Math.min(parseInt(table.getAttribute("width") || "100", 10), 100)}%`
        )
        return newTable
    }

    private createImage(img: Element): Element {
        let newImage = document.createElement("img")

        let imgSrc = img.getAttribute("mediaUri")
        if (imgSrc) newImage.setAttribute("colspan", imgSrc)

        return newImage
    }

    private createCol(col: Element): Element {
        let newCol = document.createElement("col")
        newCol.setAttribute("style",
            `width: ${col.getAttribute("width")}%`
        )
        return newCol
    }

    private createCell(cell: Element): Element {
        let newCell = document.createElement("td")
        let colspan = cell.getAttribute("colspan")
        if (colspan) newCell.setAttribute("colspan", colspan)
        let rowspan = cell.getAttribute("rowspan")
        if (rowspan) newCell.setAttribute("rowspan", rowspan)
        return newCell
    }

    private createImg(cell: Element): Element {
        let newCell = document.createElement("img")
        let src = cell.getAttribute("uri") || undefined
        if (src) newCell.setAttribute("src", "content/media/" + this.xmlFileName + "/" + src)
        return newCell
    }

    private replaceElement(element: Element) {

        let newElement: Element | null = null

        switch (element.nodeName.toLowerCase()) {
            case "title":
                let titleLevel = element.parentElement && element.parentElement.nodeName.toLowerCase() == "titledblock" ? "h3" : "h2"
                newElement = document.createElement(titleLevel)
                break
            case "richtext":
            case "introduction":
                newElement = document.createElement("p")
                break
            case "italic":
                newElement = document.createElement("i")
                break
            case "emph":
                newElement = document.createElement("em")
                break
            case "inlineimage":
                newElement = this.createImage(element)
                break
            case "list":
                newElement = document.createElement(this.getListType(element))
                break
            case "educationalobjective":
                newElement = document.createElement("figure")
                break
            case "objectiveintro":
                newElement = document.createElement("figcaption")
                break
            case "item":
            case "enablingobjective":
                newElement = document.createElement("li")
                break
            case "table":
                newElement = this.createTable(element)
                break
            case "tblcol":
                newElement = this.createCol(element)
                break
            case "tblheader":
                newElement = document.createElement("thead")
                break
            case "tblbody":
                newElement = document.createElement("tbody")
                break
            case "tablerow":
                newElement = document.createElement("tr")
                break
            case "cell":
                newElement = this.createCell(element)
                break
            case "web":
                newElement = this.createImg(element)
                break
            default:
                newElement = document.createElement("div")
                newElement.classList.add("removeNonContent")
                break
        }

        let addTo: Element | null = element.parentElement;

        if (newElement && addTo) {
            if (newElement.classList.contains("removeNonContent")) {
                element.classList.add("removeNonContent")
            }
            else {
                newElement.innerHTML = element.innerHTML;
                addTo.replaceChild(newElement, element);
            }
        }

    }

    private removeNonHTML(element: Element) {
        let deleteElems = element.querySelectorAll(".removeNonContent");
        for (let i = 0; i < deleteElems.length; i++) {
            let deleteElem = deleteElems[i]
            if (deleteElem.parentElement) {
                deleteElem.parentElement.removeChild(deleteElem);
            }
        }
    }

    private getNonRoleNodes(rootElement: Element, nodesToRemove: Array<Element> = []): Array<Element> {
        nodesToRemove = nodesToRemove || []
        rootElement = this.findNonRoleElement(rootElement, nodesToRemove)
        for (let i = 0; i < rootElement.children.length; i++) {
            let childElement = rootElement.children[i]
            if (childElement.children.length > 0) {
                this.getNonRoleNodes(childElement, nodesToRemove)
            }
        }
        return nodesToRemove;
    }

    private filterXMLToRole(rootElement: Element): Element | null {
        let filterAwayNodes: Array<Element> = this.getNonRoleNodes(rootElement)
        while (filterAwayNodes[0]) {
            let parent = filterAwayNodes[0].parentElement;
            if (parent && filterAwayNodes[0] == rootElement) {
                parent.removeChild(rootElement)
                return null
            }
            else if (parent) {
                parent.removeChild(filterAwayNodes[0])
                filterAwayNodes.shift()
            }
        }
        return rootElement;
    }


    private cleanXML(rootElement: Element): Element {

        let tagsToRemove: Array<string> = ["IntroBlock", "ParaBlock", "Figure", "ObjectiveStatement", "Renditions", "MediaObject", "TitledBlock", "ItemBlock", "ItemPara", "TblGroup"]
        for (let i = 0; i < tagsToRemove.length; i++) {

            let docNodes = rootElement.querySelectorAll(tagsToRemove[i])

            for (let j = 0; j < docNodes.length; j++) {
                this.unwrap(docNodes[j])
            }

        }

        return rootElement;
    }


    private transformToHTML(rootElement: Element): Element {
        for (let i = 0; i < rootElement.children.length; i++) {
            let childElement = rootElement.children[i]
            if (childElement.children.length > 0) {
                childElement = this.transformToHTML(childElement)
            }
            this.replaceElement(childElement)
        }
        return rootElement;
    }

    private getTopicID(url: string): string {
        let parts = url.split("?topic=")
        return parts[1] || ""
    }

    private getFileNameFromURL(url: string = ""): string {
        let fileName = url.substring(url.lastIndexOf('/') + 1)
        let withoutQuery = url.substring(0, (url.indexOf('?') || url.length))
        return decodeURI(withoutQuery)
    }

    private getFile(url: string): Promise<string> {

        let cacheKey = this.getFileNameFromURL(url)

        return new Promise((resolve, reject) => {
            if (this.win.fileCache[cacheKey]) {
                resolve(this.win.fileCache[cacheKey])
            }
            else {
                this.fileReader.Text(url).then((data: string) => {
                    this.win.fileCache[cacheKey] = data
                    resolve(data)
                })
            }
        })
    }

    private renderArticle(data: string, topicID: string): string {
        let parser = new DOMParser();
        let xmlDoc = parser.parseFromString(data, "application/xml")
        let topicNode = xmlDoc.querySelector(`[*|guid="${topicID}"]`)

        if (topicNode) {
            let filteredTopicNode = topicNode;// this.filterXMLToRole(topicNode)
            if (filteredTopicNode) {
                this.removeNonHTML(this.transformToHTML(this.cleanXML(filteredTopicNode)))
            }
            return filteredTopicNode ? filteredTopicNode.innerHTML : ""
        }

        return ""
    }

    private xmlFileName: string = "";

    public Render(url: string): Promise<string> {

        this.xmlFileName = this.getFileNameFromURL(url);
        this.xmlFileName = this.xmlFileName.substring(this.xmlFileName.lastIndexOf('/') + 1)
        this.xmlFileName = this.xmlFileName.substring(0, this.xmlFileName.lastIndexOf('.'))

        return new Promise((resolve, reject) => {
            let article = ""

            this.getFile(url).then((data: string) => {

                let topic = this.getTopicID(url)

                if (this.win.topicCache[topic]) {
                    article = this.win.topicCache[topic]
                }
                else {
                    article = this.renderArticle(data, topic)
                    this.win.topicCache[topic] = article
                }

                resolve(article)
            })
        })
    }



}