<template>
  <iframe ref="iframeElement" src class="cali-iframe"/>
</template>

<script>
import * as Core from "california_core";
import { Data } from "../../California.Data";
import EventBus from "../../Vue.EventBus";
import { Navigation } from "../../California.Navigation";
import { CaliIframeControls } from "./controls";
import { Localization } from "../../California.Localization";

export default {
  name: "cali-iframe",

  props: {
    dataIndex: {
      default: 0,
      type: Number
    },
    selectedItem: {
      default: null,
      type: Object
    }
  },

  data() {
    return {};
  },

  computed: {},
  created() {
    this.iframeSrc = () => {
      return (
        this.$ContentFolder +
        Data.ParseDataURL(
          this.selectedItem,
          this.selectedItem.Data[this.dataIndex]
        ).url
      );
    };
    this.fileReader = new Core.IO.Reader();
    this.iframeProtocol = null;
    this._renderIframe = () => {
      const url = this.iframeSrc();
      const localizedUrl = Localization.LocalizeURL(url);
      this.fileReader.Exists(localizedUrl).then(result => {
        this.iframeProtocol = null;
        this.iframeProtocol = new CaliIframeControls(
          this.$refs.iframeElement,
          this.selectedItem.Settings.ResizeMode,
          this.selectedItem.Settings.Position,
          this.selectedItem.Settings.Size,
          this.selectedItem.Settings.SmallPosition,
          this.selectedItem.Settings.SmallSize
        );
        this.$refs.iframeElement.src = result ? localizedUrl : url;
      });
    };
    this.onSubPageChange = i => {
      if (this.iframeProtocol) {
        this.iframeProtocol.DisplaySubPage(i);
      }
    };
  },
  mounted() {
    this._renderIframe();
    EventBus.$on("ToSubPage", this.onSubPageChange);
  },
  beforeDestroy() {
    EventBus.$off("ToSubPage", this.onSubPageChange);
  }
};
</script>