<template>
  <cali-html
    v-if="renderedHTML.length > 0"
    v-bind="{selectedItem: selectedItem, htmlContent: renderedHTML}"
    :data-cali-ViewItem="selectedItem.ID"
    data-cali-data-type="html"
    ref="htmlComp"
    class="cali-component"
    :data-cali-index="dataIndex"
  ></cali-html>
</template>

<script>
import Vue from "vue";
import { CaliTripControls } from "./controls";
import { Data } from "../../../California.Data";
import caliHtml from "../../CaliHtml/CaliHtml.vue";

export default {
  components: {
    caliHtml
  },
  name: "cali-trip",
  props: {
    dataIndex: {
      default: 0,
      type: Number
    },
    selectedItem: {
      default: null,
      type: Object
    }
  },
  data() {
    return {
      renderedHTML: ""
    };
  },
  computed: {},
  methods: {
    htmlSrc() {
      return (
        this.$ContentFolder +
        Data.ParseDataURL(
          this.selectedItem,
          this.selectedItem.Data[this.dataIndex]
        ).url
      );
    },
    RenderHTML() {
      const comp = this;
      this.TripProtocol.Render(this.htmlSrc()).then(function(data) {
        comp.renderedHTML = data;
      });
    }
  },
  created() {
    this.TripProtocol = new CaliTripControls();
  },
  mounted() {
    this.RenderHTML();
  },
  updated() {},
  beforeDestroy() {
    // if (this.htmlTripProtocol) this.htmlTripProtocol.RemoveEvents();
  }
};
</script>