import * as Core from "california_core";
import { CaliMediaControlLayout } from "./controls-layout";
import { CaliMediaControlEvents } from "./controls-events";
import { CaliMediaControlUtil } from "./controls-util";
import { Localization } from "../../California.Localization";

export class CaliMediaControls {
  private fileReader: Core.IO.Reader;
  private item: Core.StructureItem | null = null;
  private element: HTMLElement;
  private mediaElement: HTMLVideoElement;
  private onCanPlayThrough: any;
  private onEnded: any;
  private onResize: any;
  private onFirstPlay: any;
  private onWaiting: any;
  private onStopWaiting: any;

  constructor() {
    this.element = document.createElement("div");
    this.mediaElement = document.createElement("video");
  }

  public Attach(item: Core.StructureItem, element: HTMLElement, mediaElement: HTMLVideoElement) {
    this.item = item;
    this.fileReader = new Core.IO.Reader();
    this.element = element;
    this.mediaElement = mediaElement;
    this.hookEvents();
  }

  private hookEvents() {
    this.onCanPlayThrough = CaliMediaControlEvents.onCanPlayThrough.bind(this);
    this.mediaElement.addEventListener("canplaythrough", this.onCanPlayThrough);
    this.onFirstPlay = CaliMediaControlEvents.onFirstPlay.bind(this);
    this.mediaElement.addEventListener("play", this.onFirstPlay);
    this.onEnded = CaliMediaControlEvents.onEnded.bind(this);
    this.mediaElement.addEventListener("ended", this.onEnded);
    this.onWaiting = this.onWaitingHandler.bind(this);
    this.onStopWaiting = this.stopBuffering.bind(this);
    this.mediaElement.addEventListener("waiting", this.onWaiting);
    this.onResize = CaliMediaControlEvents.onResize.bind(this);
    window.addEventListener("resize", this.onResize);
  }

  private onWaitingHandler() {
    console.log("START BUFFER")
    const bufferInfo: HTMLButtonElement = this.element.querySelector(".cali-media-buffering") as HTMLButtonElement;
    bufferInfo.classList.add("active");
    this.mediaElement.addEventListener("canplay", this.onStopWaiting);
    this.mediaElement.addEventListener("playing", this.onStopWaiting);

  }
  private stopBuffering() {
    console.log("STOP BUFFER")
    const bufferInfo: HTMLButtonElement = this.element.querySelector(".cali-media-buffering") as HTMLButtonElement;
    bufferInfo.classList.remove("active");
    this.mediaElement.removeEventListener("canplay", this.onStopWaiting);
    this.mediaElement.removeEventListener("playing", this.onStopWaiting);
  }

  public SetControlButtons(element: HTMLElement) {
    const play: HTMLButtonElement = element.querySelector(".cali-media-play") as HTMLButtonElement;
    const pause: HTMLButtonElement = element.querySelector(
      ".cali-media-pause"
    ) as HTMLButtonElement;
    const restart: HTMLButtonElement = element.querySelector(
      ".cali-media-restart"
    ) as HTMLButtonElement;

    this.mediaElement.addEventListener("play", () => {
      console.log("PLAY 1")
      play.setAttribute("disabled", "disabled");
      pause.removeAttribute("disabled");
      restart.setAttribute("disabled", "disabled");
    });
    this.mediaElement.addEventListener("pause", () => {
      if (element.classList.contains("cali-media-hidden")) {
        console.log("PLAY 2")
        play.setAttribute("disabled", "disabled");
      } else {
        play.removeAttribute("disabled");
      }
      pause.setAttribute("disabled", "disabled");
      restart.setAttribute("disabled", "disabled");
    });
    this.mediaElement.addEventListener("ended", () => {
      console.log("PLAY 3")
      play.setAttribute("disabled", "disabled");
      pause.setAttribute("disabled", "disabled");
      restart.removeAttribute("disabled");
    });

    if (!this.mediaElement.autoplay && this.mediaElement.paused) {
      play.removeAttribute("disabled");
      pause.setAttribute("disabled", "disabled");
      restart.setAttribute("disabled", "disabled");
    }
  }

  public Render(url: string): Promise<object> {
    return new Promise(resolve => {
      let dataObj = {
        subtitles: "",
        keyframes: ""
      };

      let subsfile = `${CaliMediaControlUtil.FolderPath(
        url
      )}/subtitles/${CaliMediaControlUtil.FileNemWoExtension(url)}.txt`;
      let keysfile = `${CaliMediaControlUtil.FolderPath(
        url
      )}/subtitles/${CaliMediaControlUtil.FileNemWoExtension(url)}-keyframes.txt`;

      const localizeSubsdURL = Localization.LocalizeURL(subsfile);
      const localizedKeysURL = Localization.LocalizeURL(keysfile);
      this.fileReader
        .Exists(localizeSubsdURL)
        .then((exists: boolean) => {
          if (exists) {
            return this.fileReader.Text(localizeSubsdURL);
          } else {
            return this.fileReader.Text(subsfile);
          }
        })
        .then((subsdata: string) => {
          dataObj.subtitles = subsdata;
          return this.fileReader.Exists(localizedKeysURL);
        })
        .catch(() => {
          return this.fileReader.Exists(localizedKeysURL);
        })
        .then((exists: boolean) => {
          if (exists) {
            return this.fileReader.Text(localizedKeysURL);
          } else {
            return this.fileReader.Text(keysfile);
          }
        })
        .then((keysdata: string) => {
          dataObj.keyframes = keysdata;
        })
        .catch(() => {})
        .finally(() => {
          resolve(dataObj);
        });
    });
  }

  public Layout() {
    if (this.item) {
      CaliMediaControlLayout.Resize(this.item, this.element, this.mediaElement);
      CaliMediaControlLayout.Place(this.item, this.element, this.mediaElement);
    }
  }

  public Destroy() {
    this.mediaElement.removeEventListener("waiting", this.onWaiting);
    this.mediaElement.removeEventListener("canplaythrough", this.onCanPlayThrough);
    this.mediaElement.removeEventListener("ended", this.onEnded);
    window.removeEventListener("resize", this.onResize);

    this.mediaElement.pause();
    this.mediaElement.removeAttribute("src");
    const allSources: Array<HTMLElement> = Array.from(this.mediaElement.querySelectorAll("source"));
    allSources.forEach((el: any) => {
      el.removeAttribute("src");
    });
    this.mediaElement.load();
    if (this.mediaElement.parentNode) {
      this.mediaElement.parentNode.removeChild(this.mediaElement);
    }
  }
}
