<template>
  <section class="cali-image-container" ref="container" :data-california-id="selectedItem.ID">
    <img class="cali-image">
  </section>
</template>

<script>
import Vue from "vue";
import { CaliImageControls } from "./controls";
import { Data } from "../../California.Data";
import EventBus from "../../Vue.EventBus";

export default {
  name: "cali-image",
  props: {
    dataIndex: {
      default: 0,
      type: Number
    },
    selectedItem: {
      default: null,
      type: Object
    }
  },
  data() {
    return {};
  },
  computed: {},
  methods: {
    imageSrc() {
      let url = Data.ParseDataURL(
        this.selectedItem,
        this.selectedItem.Data[this.dataIndex]
      ).url;
      return this.$ContentFolder + url;
    },
    renderImage() {
      this.imgProtocol.Render(this.imageSrc(), this.$refs.container);
    }
  },
  created() {
    this.imgProtocol = new CaliImageControls(this.selectedItem);
  },
  mounted() {
    this.renderImage();
  },
  updated() {},
  destroyed() {},
  watch: {
    selectedItem: function(newVal, oldVal) {
      this.renderImage();
    }
  }
};
</script>

<style lang="less">
#cali-app {
  .cali-image-container {
    background-repeat: no-repeat;
  }
}
</style>
