import { StructureItem } from "./StructureItem";
import { Navigator } from "../Navigation";
declare var CaliforniaCoursedataLoader: any;

export class Structure {
  constructor() {
    this.Navigator = new Navigator(this);
  }

  private itemList: Array<StructureItem> = [];
  private resetLists() {
    this.itemList = [];
  }

  private calcItemProgress(item: StructureItem) {
    let progresstype = item.Settings.Progresstype;
    if (!progresstype || progresstype == "visited") {
      item.Progress = 1;
    } else if (progresstype == "score") {
      let itemPass = item.Settings.PassingScale || 1;
      let itemScore = item.Settings.Score || 0;
      let itemMax = item.Settings.MaxScore || 1;
      if (itemScore >= itemMax * itemPass) item.Progress = 1;
    }
  }

  private totalChildProgress(item: StructureItem, parent?:StructureItem): number {
    if (!item) return;

    let progressAcum: number = !parent && item.ChildCount == 0 && item.Settings.Progresstype != "none" ? item.Progress : 0;

    for (let child of item.Children) {
      if (child.Settings.Progresstype != "none") {
        progressAcum += this.totalChildProgress(child);
      }
    }

    return progressAcum;
  }

  private progressParent(item: StructureItem): number {
    if (!item) return;

    const subProgress = item.SubProgress > -1 ? item.SubProgress : item.Progress;
    let childProgress = subProgress + this.totalChildProgress(item, item);
    let divider = 1 + item.TotalChildCount;
    console.log(item.Name + "( " + subProgress + " ): " + childProgress + " / " + divider + " = " + childProgress / divider);
    const rounded = Math.round((childProgress / divider) * Math.pow(10, 2)) / Math.pow(10, 2);

    item.Progress = rounded;
    this.progressParent(item.Parent);
    this.evaluateLocking(item);
  }

  private evaluateLocking(item: StructureItem) {
    let nextItem = item.After();
    if (nextItem) {
      nextItem.EvaluateLock();
    }
  }

  private createItem(
    seedItem: StructureItem = null,
    parentItem: StructureItem = null
  ): StructureItem {
    let newItem: StructureItem = seedItem ? new StructureItem(seedItem) : new StructureItem({});

    if (parentItem) {
      newItem.Parent = parentItem;
      newItem.Index = parentItem.ChildCount;
      newItem.RefID = newItem.RefID || parentItem.RefID;
      newItem.ParentIDs = parentItem ? parentItem.ParentIDs.concat([parentItem.ID]) : [];
      if (newItem.Settings.Progresstype != "none") {
        parentItem.ChildCount++;
      }
      if (newItem.Children.length == 0 && newItem.Settings.Progresstype != "none") {
        parentItem.TotalChildCount++;
        let p = parentItem.Parent;
        while (p) {
          p.TotalChildCount++;
          p = p.Parent;
        }
      }
    }

    this.itemList.push(newItem);
    return newItem;
  }

  private isFiltered(item: StructureItem, structParent: StructureItem, filter: Array<string> = []) {
    let fItem = item;

    while (fItem !== null) {
      let itemFilter = fItem.Settings.Filter || [];

      for (let i = 0; i < filter.length; i++) {
        let subFilters = filter[i].split(",");
        for (let j = 0; j < subFilters.length; j++) {
          if (itemFilter.indexOf(subFilters[j]) != -1) {
            return true;
          }
          if (itemFilter.indexOf(`!${subFilters[j]}`) != -1) {
            return false;
          }
        }
      }

      fItem = structParent || fItem.Parent;
      structParent = null;
    }

    return false;
  }

  private addChildren(
    loadedItem: StructureItem,
    structureParent: StructureItem = null,
    filter: Array<string> = []
  ): Array<StructureItem> {
    let structureChildren: Array<StructureItem> = [];

    for (let i = 0; i < loadedItem.Children.length; i++) {
      let loadedItemChild = loadedItem.Children[i];

      if (filter.length > 0 && !this.isFiltered(loadedItemChild, structureParent, filter)) {
        loadedItemChild.Settings.hidden = true;
        continue;
      }

      let newStructureChild: StructureItem = this.createItem(loadedItemChild, structureParent);

      if (newStructureChild.Settings.NavigationBase) this.NavigationBase = newStructureChild;
      if (newStructureChild.Settings.MenuRoot) this.MenuRoot = newStructureChild;

      structureChildren.push(newStructureChild);

      if (loadedItemChild.Children && loadedItemChild.Children.length > 0) {
        newStructureChild.Children = this.addChildren(loadedItemChild, newStructureChild, filter);
      }
    }

    return structureChildren;
  }

  private createFromCaliforniaJSON(
    structureJson: object,
    filter: Array<string> = []
  ): Promise<Structure> {
    return new Promise<Structure>((resolve, reject) => {
      let newItem = this.createItem(structureJson as StructureItem);
      this.Root = newItem;
      this.NavigationBase = newItem;
      this.MenuRoot = newItem;
      this.CurrentItem = newItem;

      newItem.Children = this.addChildren(structureJson as StructureItem, newItem, filter);

      if (this.Root.ChildCount == 1 && this.Root.ID === "00000000-0000-0000-0000-000000000000") {
        this.Root = this.Root.Children[0];
        this.Root.Parent = null;
        this.itemList.shift();
      }

      this.itemList.forEach((item: StructureItem, i: number) => {
        item.FlatIndex = i;
      });

      resolve(this);
    });
  }

  private createFromExternalJSON(structureJson: object): Promise<Structure> {
    return new Promise<Structure>((resolve, reject) => {
      var s = document.createElement("script");
      s.type = "text/javascript";
      s.src = structureJson["Structure-Loader"];
      window.document.body.appendChild(s);
      s.addEventListener("load", e => {
        let externalStructure = CaliforniaCoursedataLoader(structureJson);
        resolve(externalStructure);
      });
    });
  }

  // Public Properties

  public Root: StructureItem;
  public NavigationBase: StructureItem;
  public MenuRoot: StructureItem;
  public Navigator: Navigator;
  public ContextItem: StructureItem;
  public CurrentItem: StructureItem;
  public FlatListPosition: number;

  // Public Functions

  public CreateFromJSON(structureJson: object, filter: Array<string> = []): Promise<Structure> {
    this.resetLists();

    if (structureJson["Structure-Ref"]) {
      if (structureJson["Structure-Loader"]) {
        return new Promise<Structure>((resolve, reject) => {
          this.createFromExternalJSON(structureJson)
            .then(convertedStructure => {
              return this.createFromCaliforniaJSON(convertedStructure, filter);
            })
            .then(loadedStructure => {
              resolve(loadedStructure);
            })
            .catch(reason => {
              reject(reason);
            });
        });
      }
    } else {
      return new Promise<Structure>((resolve, reject) => {
        this.createFromCaliforniaJSON(structureJson, filter)
          .then(loadedStructure => {
            resolve(loadedStructure);
          })
          .catch(reason => {
            reject(reason);
          });
      });
    }
  }

  public GetFlatList(): Array<StructureItem> {
    return this.itemList;
  }

  public GetItemByID(id: string): StructureItem {
    return this.itemList.filter(a => {
      return a.ID === id;
    })[0] as StructureItem;
  }

  public GetItemsByName(name: string): Array<StructureItem> {
    return this.itemList.filter(a => {
      return a.Name === name;
    }) as Array<StructureItem>;
  }

  public ProgressItem(item?: StructureItem) {
    item = item || this.CurrentItem;
    item.Visited = true;

    if (item.ChildCount < 1) {
      this.calcItemProgress(item);
      this.progressParent(item.Parent);
    } else {
      this.progressParent(item);
    }

    this.evaluateLocking(item);
  }

  public SetItem(item: StructureItem, subProgress?: number) {
    this.CurrentItem = item;
    this.ContextItem = item.Parent;
    if (this.ContextItem && this.ContextItem.ChildCount > 0) {
      this.ContextItem.ResumeIndex = item.Index;
    }
    if (subProgress && item.ChildCount > 0) {
      item.SubProgress = subProgress;
    }
    this.FlatListPosition = item.FlatIndex;
    this.ProgressItem();
  }
}
