<template>
    
  <div class="cali-progress-bar" ref="container">
    <div class="cali-progress-bar-background">
      <span class="cali-index-indicator">{{ position }}</span>
      <span class="cali-number-indicator">{{`${visitedNumber}/${maxNumber}`}}</span>
      <span class="cali-percent-indicator">{{`${Math.round(percent)}%`}}</span>
    </div>
    <div class="cali-progress-bar-foreground">
      <span class="cali-index-indicator">{{ position }}</span>
      <span class="cali-number-indicator">{{`${visitedNumber}/${maxNumber}`}}</span>
      <span class="cali-percent-indicator">{{ `${Math.round(percent)}%` }}</span>
    </div>
  </div>    

</template>

<script>
export default {
  name: "progress-bar",
  props: ["ProgressItem"],
  data() {
    return {
      CaliforniaCourse: this.$CaliforniaCourse
    };
  },
  computed: {
    position() {

      if (this.CaliforniaCourse.CurrentItem.ID != this.CaliforniaCourse.Root.ID) {
        return this.CaliforniaCourse.CurrentItem.Index +1;
      }

      let activeList = this.$CaliforniaCourse.itemList.filter(item => {
        if (item.Settings && !item.Settings.Hidden && item.ChildCount === 0) {
          return item;
        }
      });
      for (let i = 0; i < activeList.length; i++) {
        if (activeList[i].ID == this.CaliforniaCourse.CurrentItem.ID) {
          return i + 1;
        }
      }
      return 0;
    },
    percent() {
      return 100 * this.ProgressItem.Progress;
    },
    maxNumber() {
      return 1 + this.ProgressItem.TotalChildCount;
    },
    visitedNumber() {
      return this.getVisited(this.ProgressItem);
    }
  },
  methods: {
    makeClip(bar) {
      if (!bar) return;
      let h = bar.offsetHeight;
      let w = Math.ceil(bar.offsetWidth * (this.percent / 100));
      let foreBar = bar.querySelector(".cali-progress-bar-foreground");
      foreBar.style.clip = `rect(0px,${w}px,${h}px,0px)`;
      let redraw = foreBar.offsetHeight;
      foreBar.classList.add("animated");
    },
    getVisited(item) {
      item = item || this.ProgressItem;
      let total = item.Progress > 0 ? 1 : 0;
      for (let i = 0; i < item.ChildCount; i++) {
        total += this.getVisited(item.Children[i]);
      }
      return total;
    }
  },
  mounted() {
    this.$el
      .querySelector(".cali-progress-bar-foreground")
      .classList.remove("animated");
    this.makeClip(this.$refs.container);
  },
  updated() {
    this.makeClip(this.$refs.container);
  }
};
</script>

<style lang="less" scoped>
@height: 30px;

.cali-progress-bar {
  width: 100%;
  height: @height;
  position: relative;

  .cali-percent-indicator,
  .cali-number-indicator,
  .cali-index-indicator {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    line-height: @height;
    text-align: center;
  }

  .cali-number-indicator,
  .cali-index-indicator {
    display: none;
  }

  .cali-progress-bar-background {
    position: absolute;
    width: 100%;
    height: 100%;
  }

  .cali-progress-bar-foreground {
    position: absolute;
    height: 100%;
    width: 100%;
    background-color: #000;
    clip: rect(0px, 0px, 100px, 0px);
    &.animated {
      transition: clip 0.3s ease-in-out;
    }
    .cali-percent-indicator,
    .cali-number-indicator,
    .cali-index-indicator {
      color: #fff;
    }
  }
}
</style>
