import * as Core from "california_core";
import { ContentResize, ContentResizeModes } from "../../California.ContentSize";
import EventBus from "../../Vue.EventBus";

export class CaliIframeControls {
  private iframeElement: HTMLIFrameElement;
  private iframeWindow: Window;
  private iframeDocument: HTMLDocument;
  private resizeMode: ContentResizeModes = ContentResizeModes.Content;
  private position: { [k: string]: any } | null = null;
  private fixedSize: { [k: string]: any } | null = null;
  private smallPosition: { [k: string]: any } | null = null;
  private smallFixedSize: { [k: string]: any } | null = null;
  private maxWidth: number = 0;
  private maxHeight: number = 0;
  private animationLabels: Array<any> = [];

  constructor(
    element: HTMLIFrameElement,
    resizeMode?: ContentResizeModes,
    position?: { [k: string]: any },
    fixedSize?: { [k: string]: any },
    smallPosition?: { [k: string]: any },
    smallFixedSize?: { [k: string]: any }
  ) {
    this.iframeElement = element;

    if (!this.iframeElement.contentWindow) throw new TypeError();
    this.iframeWindow = this.iframeElement.contentWindow;

    if (!this.iframeWindow.document) throw new TypeError();
    this.iframeDocument = this.iframeWindow.document;

    if (!this.iframeDocument.documentElement) throw new TypeError();

    this.iframeDocument.documentElement.style.overflow = "hidden";
    this.iframeElement.style.width = "100%";
    this.iframeElement.style.height = "100%";
    this.maxWidth = this.iframeElement.clientWidth;
    this.maxHeight = this.iframeElement.clientHeight;
    this.iframeElement.style.width = "";
    this.iframeElement.style.height = "";
    if (fixedSize) {
      let index = parseInt(this.iframeElement.dataset.caliIndex || "", 10);
      if (!isNaN(index)) {
        this.fixedSize = ContentResize.GetPropForDataIndex(fixedSize, index);
        this.position = ContentResize.GetPropForDataIndex(position, index);
        if (smallFixedSize) {
          this.smallFixedSize = ContentResize.GetPropForDataIndex(smallFixedSize, index);
          this.smallPosition = ContentResize.GetPropForDataIndex(smallPosition, index);
        }
      }
    }
    if (resizeMode) this.resizeMode = resizeMode;

    this.loadIframe();

    this.iframeElement.addEventListener("load", () => {
      const animWin = <any>this.iframeWindow;
      if (animWin.AdobeAn) {
        animWin.AdobeAn.bootstrapCallback(() => {
          this.getAnimationLabels(animWin.exportRoot);
          animWin.exportRoot.play(0);
          setTimeout(() => {
            this.doRezise();
            this.iframeElement.dataset.loaded = "true";
          }, 0);
        });
      } else {
        this.doRezise();
        this.iframeElement.dataset.loaded = "true";
      }
    });

    EventBus.$on("windowResize", () => {
      this.doRezise();
    });
  }

  private getAnimationLabels(exportRoot: any) {
    this.animationLabels = [];
    Object.keys(exportRoot.timeline._labels).forEach(label => {
      this.animationLabels.push(exportRoot.timeline._labels[label]);
    });
    this.animationLabels.sort(function(a: number, b: number) {
      return a - b;
    });
  }

  private injectCSS(): Promise<any> {
    if (!this.iframeDocument) throw new TypeError();
    let link = this.iframeDocument.createElement("link");
    link.type = "text/css";
    link.rel = "stylesheet";
    link.href = Core.Net.Path.FromRoot("assets/css/california_player.css");

    if (!this.iframeDocument.head) throw new Error();

    let styleTags = this.iframeDocument.head.querySelectorAll("style")[0];
    this.iframeDocument.head.insertBefore(link, styleTags);

    return new Promise<any>((resolve, reject) => {
      if (!this.iframeDocument) throw new TypeError();
      var img = this.iframeDocument.createElement("img");
      img.onerror = function() {
        resolve();
      };
      img.src = link.href;
    });
  }

  private doRezise() {
    const mode = this.resizeMode ? this.resizeMode.toLowerCase() : ContentResizeModes.Parent;
    switch (mode) {
      case ContentResizeModes.Content:
        this.resizeToContentWidth();
        this.resizeToContentHeight();
        break;
      case ContentResizeModes.Parent:
        this.resizeToParentWidth();
        this.resizeToParentHeight();
        break;
      case ContentResizeModes.Fixed:
        this.resizeToFixed(this.fixedSize, this.position);
        break;
      default:
        this.resizeToContentWidth();
        this.resizeToContentHeight();
    }
  }

  private loadIframe() {
    if (!this.iframeDocument) throw new TypeError();
    this.iframeDocument.body.style.display = "inline-block";
    this.iframeDocument.body.classList.add("cali-iframe-content");

    let parentAppNode = <HTMLElement>(
      this.iframeWindow.parent.document.querySelector("[data-context-id]")
    );
    this.iframeDocument.body.dataset["contextId"] = parentAppNode
      ? parentAppNode.dataset["contextId"]
      : "";

    this.injectCSS().then(() => {
      this.doRezise();
      this.iframeWindow.addEventListener("hashchange", this.hashchanged);
      this.iframeDocument.body.style.display = "";
    });
  }
  private getTotalBodyYMargin(): number {
    if (!this.iframeDocument || !this.iframeDocument.defaultView) return 0;

    let conputedBody = this.iframeDocument.defaultView.getComputedStyle(
      this.iframeDocument.body,
      ""
    );
    let margin = parseInt(conputedBody.getPropertyValue("margin-top"), 10);
    margin += parseInt(conputedBody.getPropertyValue("margin-bottom"), 10);

    let firstChild = this.iframeDocument.body.firstElementChild;
    if (firstChild) {
      let computedFirstChild = this.iframeDocument.defaultView.getComputedStyle(firstChild, "");
      margin += parseInt(computedFirstChild.getPropertyValue("margin-top"), 10);
    }
    let lastChild = this.iframeDocument.body.lastElementChild;
    if (lastChild) {
      let computedLastChild = this.iframeDocument.defaultView.getComputedStyle(lastChild, "");
      margin += parseInt(computedLastChild.getPropertyValue("margin-bottom"), 10);
    }

    return margin;
  }

  private messureScrollbarX(): number {
    return this.iframeWindow.innerHeight - this.iframeDocument.body.clientHeight;
  }

  private messureScrollbarY(): number {
    return this.iframeWindow.innerWidth - this.iframeDocument.body.clientWidth;
  }

  private resizeToContentWidth() {
    if (!this.iframeDocument.defaultView) return;
    let computed = this.iframeDocument.defaultView.getComputedStyle(this.iframeDocument.body, "");
    let marginLeft = parseInt(computed.getPropertyValue("margin-left"), 10);
    let marginRight = parseInt(computed.getPropertyValue("margin-right"), 10);
    let contentWidth =
      this.iframeDocument.body.offsetWidth + marginLeft + marginRight + this.messureScrollbarY();
    this.iframeElement.style.width = "1px";
    this.iframeElement.style.minWidth = Math.min(contentWidth, this.maxWidth) + "px";
  }

  private resizeToContentHeight() {
    let contentHeight =
      this.iframeDocument.body.scrollHeight + this.getTotalBodyYMargin() + this.messureScrollbarX();
    this.iframeElement.style.height = "1px";
    this.iframeElement.style.minHeight = contentHeight + "px";
  }

  private resizeToParentHeight() {
    if (!this.iframeDocument.documentElement || !this.iframeElement.parentElement) return;
    let newHeight = this.iframeElement.parentElement.offsetHeight;
    this.iframeElement.style.height = "1px";
    this.iframeElement.style.minHeight = newHeight + "px";
  }

  private resizeToParentWidth() {
    if (!this.iframeDocument.documentElement || !this.iframeElement.parentElement) return;
    let newWidth = this.iframeElement.parentElement.offsetWidth;
    this.iframeElement.style.width = "1px";
    this.iframeElement.style.minWidth = newWidth + "px";
  }

  private resizeToFixed(size: { [k: string]: any } | null, position: { [k: string]: any } | null) {
    if (!size || !position || !this.iframeWindow) {
      setTimeout(() => {
        this.resizeToFixed(size, position);
      }, 200);
      return;
    }

    let finalSize = size;

    if (window.innerWidth < 769 && this.smallFixedSize) {
      finalSize = this.smallFixedSize;
    }

    this.iframeWindow.document.body.style.overflow = "auto";
    this.iframeWindow.requestAnimationFrame(() => {
      switch (finalSize.width.toLowerCase()) {
        case "parent":
          this.resizeToParentWidth();
          break;
        case "content":
          this.resizeToContentWidth();
          break;
        default:
          this.iframeElement.style.width = "1px";
          this.iframeElement.style.minWidth = finalSize.width;
      }
    });
    this.iframeWindow.requestAnimationFrame(() => {
      switch (finalSize.height.toLowerCase()) {
        case "parent":
          this.resizeToParentHeight();
          break;
        case "content":
          this.resizeToContentHeight();
          break;
        default:
          this.iframeElement.style.height = "1px";
          this.iframeElement.style.minHeight = finalSize.height;
      }
    });

    let finalPos = position;

    if (window.innerWidth < 769 && this.smallPosition) {
      finalPos = this.smallPosition;
    }

    if (finalPos.y !== "" || finalPos.x !== "") {
      this.iframeElement.style.position = "absolute";
      this.iframeElement.style.top = finalPos.y;
      this.iframeElement.style.left = finalPos.x;
    }

    //   this.iframeWindow.document.body.style.overflow = "";
  }

  private hashchanged(e: any) {
    EventBus.$emit("IframeHashChange", e);
  }

  public DisplaySubPage(index: number) {
    const frame = this.animationLabels[index];
    if (frame) {
      const animWin = <any>this.iframeWindow;
      if (animWin.exportRoot) {
        animWin.exportRoot.gotoAndPlay(frame);
      }
    }
  }
}
