import * as Core from "california_core";
import { Data } from "../California.Data";
import Progress from "../California.Progress";
import Vue from "vue";
import EventBus from "../Vue.EventBus";

export default class Navigation {
  public IsBacking(): boolean {
    const history = this.LastItem;
    const hasParent = history && history.Parent && Data.Course.CurrentItem.Parent;
    const hasSameParent =
      history && hasParent && history.Parent.ID === Data.Course.CurrentItem.Parent.ID;
    if (hasParent && hasSameParent) {
      if (history && history.Index === Data.Course.CurrentItem.Index + 1) {
        return true;
      }
    }
    return false;
  }

  private pushRoute(item: Core.StructureItem) {
    if (this.inPopUp) {
      EventBus.$emit("showmodal", item);
      return;
    }
    if (!item) {
      Vue.prototype.$CaliforniaApp.$router.push({ path: "/home" });
    } else {
      Vue.prototype.$CaliforniaApp.$router.push({
        name: item.ChildCount > 0 ? "collection" : "item",
        params: { id: item.ID }
      });
    }
  }

  public ToID(id: string) {
    let item: Core.StructureItem = Data.Course.GetItemByID(id);
    this.pushRoute(item);
  }

  private evaluateCompleted() {
    let progressType = (Data.Course.CurrentItem.Settings.Progresstype || "").toLowerCase();
    let progressLimit = null;

    const progress: number = this.visitedSubPages.reduce((a: number, b: number) => a + b, 0);
    const scaled = progress / (this.NumberOfSubPages + 1);

    if (progressType.indexOf(":") != -1) {
      const parts = progressType.split(":");
      progressType = parts[0];
      progressLimit = parseFloat(parts[1].toString());
    }
    progressLimit = progressLimit || 1;
    const writeProgress = Math.max(Data.Course.CurrentItem.SubProgress, scaled);

    if (
      this.NumberOfSubPages > 0 &&
      Data.Course.CurrentItem.ChildCount > 0 &&
      (!progressType ||
        progressType == "subpages" ||
        progressType == "limit" ||
        progressType == "completed")
    ) {
      Data.Course.SetItem(Data.Course.CurrentItem, writeProgress);
      Progress.SetProgress();
    }

    if (
      (progressType == "subpages" || progressType == "completed") &&
      this.NumberOfSubPages > 0 &&
      progress > this.NumberOfSubPages
    ) {
      Data.Course.CurrentItem.Progress = 1;
      Data.Course.SetItem(Data.Course.CurrentItem);
      Progress.SetProgress();
    } else if (progressType == "limit" && scaled >= progressLimit) {
      Data.Course.CurrentItem.Progress = 1;
      Data.Course.SetItem(Data.Course.CurrentItem);
      Progress.SetProgress();
    }
    else if (progressType == "subpage" && this.SubPageLocation >= progressLimit) {
      Data.Course.CurrentItem.Progress = 1;
      Data.Course.SetItem(Data.Course.CurrentItem);
      Progress.SetProgress();
    }
  }

  public SubPages: Element[][] = [];
  public SubPageLocation: number = -1;
  private NumberOfSubPages: number = 0;
  private FirstSubPage: number = 0;
  private visitedSubPages: Array<number> = [];
  public LastItem: Core.StructureItem | null = null;
  public inPopUp: boolean = false;
  private subPageTimer: number = -1;

  public ToSubPage(index: number) {
    //TODO: Fix subpage navigation in popup content
    // In the meantime just prevent subpage event when popups is loaded
    if (this.inPopUp) return;

    this.SubPageLocation = index;
    this.visitedSubPages[index] = 1;
    this.evaluateCompleted();

    const pageList: Array<HTMLElement> = (this.SubPages[index] as Array<HTMLElement>) || [];
    let subPageDuration: number = -1;

    pageList.forEach((p: HTMLElement) => {
      subPageDuration = Math.max(
        subPageDuration,
        parseFloat(p.dataset.californiaSubPageDuration || "-1")
      );
    });

    window.clearTimeout(this.subPageTimer);

    if (subPageDuration > 0) {
      this.subPageTimer = window.setTimeout(() => {
        this.ToNextSubPage();
      }, subPageDuration * 1000);
    }
    EventBus.$emit("ToSubPage", this.SubPageLocation);
    EventBus.$emit("ToMediaSubPage", this.SubPageLocation);
  }

  public ToNextSubPage() {
    this.ToSubPage(Math.min(this.NumberOfSubPages, this.SubPageLocation + 1));
  }

  public ToPreviousSubPage() {
    this.ToSubPage(Math.max(this.FirstSubPage, this.SubPageLocation - 1));
  }

  public ToName(name: string) {
    let item: Core.StructureItem | null = null;
    item = Data.Course.GetItemsByName(name)[0];
    if (!item) return;
    this.pushRoute(item);
  }

  public ToIndex(index: number) {
    let item = Data.Course.GetFlatList()[index];
    if (!item) return;

    this.pushRoute(item);
  }

  public ToChildIndex(index: number) {
    let item = Data.Course.CurrentItem.Children[index];
    if (!item) return;

    this.pushRoute(item);
  }

  public ToNext(forceNextSlide: boolean = false) {
    if (
      !forceNextSlide &&
      (this.NumberOfSubPages > 0 && this.SubPageLocation < this.NumberOfSubPages)
    ) {
      this.ToNextSubPage();
      return;
    }

    let item: Core.StructureItem;
    const nextItemSetting = Data.Course.CurrentItem.Settings.NextItem;
    if (nextItemSetting && nextItemSetting.toLowerCase() == "bookmark") {
      item = Data.Course.GetItemByID(this.bookmarkID() || "");
    } else {
      item = Data.Course.Navigator.GetNextItem();
    }

    this.pushRoute(item);
  }

  public ToPrevious(forceNextSlide: boolean = false) {
    if (!forceNextSlide && (this.NumberOfSubPages > 0 && this.SubPageLocation > 0)) {
      this.ToPreviousSubPage();
      return;
    }

    let item: Core.StructureItem;
    const prevItemSetting = Data.Course.CurrentItem.Settings.PreviousItem;
    if (prevItemSetting && prevItemSetting.toLowerCase() == "bookmark") {
      item = Data.Course.GetItemByID(this.bookmarkID() || "");
    } else {
      item = Data.Course.Navigator.GetPreviousItem();
    }
    if (!item) return;

    this.pushRoute(item);
  }

  public ToParent() {
    let item: Core.StructureItem = Data.Course.CurrentItem.Parent;
    if (!item) return;

    this.pushRoute(item);
  }

  public ToHome() {
    let item: Core.StructureItem = Data.Course.NavigationBase;
    if (!item) return;
    this.pushRoute(item);
  }

  public ToBookmark() {
    let item: Core.StructureItem = Data.Course.GetItemByID(this.bookmarkID() || "");
    if (!item) return;
    this.pushRoute(item);
  }

  public OpenPopup(itemID: string) {
    let item: Core.StructureItem = Data.Course.GetItemByID(itemID || "");
    if (!item) return;
    Data.Course.ProgressItem(item);
    EventBus.$emit("showmodal", item);
  }

  public SetSubPages(pages: Array<Element>, numberofPages: number, firstSubPage: number = 0) {
    if (pages.length < 1) return -1;

    pages.forEach((p: Element, index: number) => {
      const page = p as HTMLElement;
      if (!page) return;
      const pIndex = parseInt(page.dataset.californiaSubPage || "", 10);
      this.SubPages[pIndex] = this.SubPages[pIndex] || [];
      this.SubPages[pIndex].push(page);
    });
    this.NumberOfSubPages = Math.max(this.NumberOfSubPages, numberofPages);
    this.FirstSubPage = firstSubPage;
    this.SubPageLocation = this.IsBacking() ? numberofPages : firstSubPage;
  }

  private bookmarkID(): string | undefined {
    const bmarkID: string | undefined = Progress.GetBookmark()
      .split("/")
      .slice(-1)[0];
    return bmarkID;
  }

  private isBookmarkItem(itemID: string): boolean {
    return this.bookmarkID() === itemID;
  }

  public FromRoute(to: any, from: any, next: Function): Promise<Core.NavigationResult> {
    let itemID: string = to.params.id || Data.Course.Root.ID;
    return new Promise<Core.NavigationResult>(resolve => {
      this.LastItem = Data.Course.CurrentItem;
      Data.Course.Navigator.NavigateToID(itemID).then((e: any) => {
        if (e.Result == Core.Navigation.ResultCode.OK || this.isBookmarkItem(itemID)) {
          this.SubPages = [];
          this.SubPageLocation = -1;
          this.NumberOfSubPages = 0;
          this.visitedSubPages = [];
          resolve(Core.Navigation.ResultCode.OK);
          next();
        } else {
          Vue.prototype.$CaliforniaApp.$router.replace({ path: "/home" });
        }
      });
    });
  }

  public FromExternalCommand(command: string, value: any) {
    switch (command.toLowerCase()) {
      case "navtoid":
        EventBus.$emit("NavToID", value);
        break;
      case "navtoindex":
        EventBus.$emit("NavToIndex", parseInt(value, 10));
        break;
      case "navtochildindex":
        EventBus.$emit("NavToChildIndex", parseInt(value, 10));
        break;
      case "navtoname":
        EventBus.$emit("NavToName", value);
        break;
      case "navtonext":
        EventBus.$emit("NavToNext", value);
        break;
      case "navtonextslide":
        EventBus.$emit("NavToNext", true);
        break;
      case "navtonextsubpage":
        EventBus.$emit("NavToNextSubPage", value);
        break;
      case "navtoprevious":
        EventBus.$emit("NavToPrev", value);
        break;
      case "navtoprev":
        EventBus.$emit("NavToPrev", value);
        break;
      case "navtopreviousslide":
        EventBus.$emit("NavToPrev", true);
        break;
      case "navtoprevslide":
        EventBus.$emit("NavToPrev", true);
        break;
      case "navtoprevsubpage":
        EventBus.$emit("NavToPrevSubPage", value);
        break;
      case "navtoprevioussubpage":
        EventBus.$emit("NavToPrevSubPage", value);
        break;
      case "navtosubpage":
        EventBus.$emit("NavToSubPage", parseInt(value, 10));
        break;
      case "navtoparent":
        EventBus.$emit("NavToParent", value);
        break;
      case "navtohome":
        EventBus.$emit("NavToHome", value);
        break;
      case "navtobookmark":
        EventBus.$emit("NavToBookmark", value);
        break;
      case "openpopup":
        EventBus.$emit("OpenPopup", value);
        break;
      case "setscore":
        EventBus.$emit("SetScore", parseFloat(value));
        break;
    }
  }

  constructor() {
    EventBus.$on("ContentReady", () => {
      if (this.SubPageLocation > -1) {
        this.ToSubPage(this.SubPageLocation);
      }
    });

    EventBus.$on("NavigationStarted", () => {});

    EventBus.$on("NavigationDone", () => {});

    EventBus.$on("NavToID", (e: object) => {
      this.ToID(e.toString());
    });

    EventBus.$on("NavToIndex", (e: object) => {
      this.ToIndex(parseInt(e.toString()));
    });

    EventBus.$on("NavToChildIndex", (e: object) => {
      this.ToChildIndex(parseInt(e.toString()));
    });

    EventBus.$on("NavToName", (e: object) => {
      this.ToName(e.toString());
    });

    EventBus.$on("NavToNext", (e: boolean = false) => {
      this.ToNext(e);
    });

    EventBus.$on("NavToPrev", (e: boolean = false) => {
      this.ToPrevious(e);
    });

    EventBus.$on("NavToNextSubPage", (e: object) => {
      this.ToNextSubPage();
    });

    EventBus.$on("NavToPrevSubPage", (e: object) => {
      this.ToPreviousSubPage();
    });

    EventBus.$on("NavToSubPage", (e: object) => {
      this.ToSubPage(parseInt(e.toString(), 10));
    });

    EventBus.$on("NavToParent", (e: object) => {
      this.ToParent();
    });

    EventBus.$on("NavToHome", (e: object) => {
      this.ToHome();
    });

    EventBus.$on("NavToBookmark", (e: object) => {
      this.ToBookmark();
    });

    EventBus.$on("OpenPopup", (e: string) => {
      this.OpenPopup(e);
    });

    EventBus.$on("SetScore", (e: number) => {
      Data.Course.CurrentItem.Settings.Score = e;
      Data.Course.SetItem(Data.Course.CurrentItem);
      Progress.SetProgress();
    });

    EventBus.$on("IframeHashChange", (e: any) => {
      var hashParts = e.target.location.href
        .split("#")[1]
        .toLowerCase()
        .split("|");

      if (hashParts.length < 3) return;
      if (hashParts[0] !== "california") return;

      this.FromExternalCommand(hashParts[1], hashParts[2]);
    });
  }
}
