import * as Core from "california_core";
import { Localization } from "../../../California.Localization";

export class CaliTripControls {
  private fileReader: Core.IO.Reader;
  private urlTopicID: string = "";

  constructor() {
    this.fileReader = new Core.IO.Reader();
  }

  private getItemIDFromURL(url: string = ""): string {
    let fileName = url.substring(url.lastIndexOf("/") + 1);
    const TRIP_GUID_LENGTH = 32;
    const GUIDS = fileName.split("-").filter(part => part.length == TRIP_GUID_LENGTH);
    const GUID = GUIDS[GUIDS.length - 1];
    return decodeURI(GUID);
  }

  private setTypeClasses(doc: HTMLElement) {
    const pageTitle = doc.querySelector("h1");
    if (pageTitle) pageTitle.classList.add("page-title");
  }

  private removeEmptySegments(doc: HTMLElement) {
    const segments = Array.from(doc.querySelectorAll(".segment"));
    segments.forEach((seg: Element, index: number) => {
      if (!seg.textContent || seg.textContent.replace(/\s/g, "").length < 1) {
        if (seg.parentElement) {
          seg.parentElement.removeChild(seg);
        }
      }
    });
  }

  private removeImg(doc: HTMLElement) {
    const imgs = Array.from(doc.querySelectorAll("img"));
    imgs.forEach((img: Element, index: number) => {
      if (img.parentElement) {
        img.parentElement.removeChild(img);
      }
    });
  }

  private parseSubPages(doc: HTMLElement) {
    const segments = Array.from(doc.querySelectorAll(".segment"));
    segments.forEach((seg: Element, index: number) => {
      if (index == 0 || seg.querySelectorAll("h1, h2, h3, xref").length > 0) {
        seg.setAttribute("data-california-sub-page", index.toString());
        Array.from(seg.querySelectorAll("h1, h2, h3")).forEach(header => {
          header.classList.add("sub-page-title");
        });
      }
    });
  }

  private getButtonTitle(link: Element) {
    let parent: Element | null = link;
    while (parent && !parent.classList.contains("segment")) {
      parent = parent.parentElement;
    }

    if (!parent) return null;

    const titleNode = parent.querySelector("h2");
    if (titleNode) console.log(titleNode.innerText);
    return titleNode ? titleNode.innerText : null;
  }

  private createSubPageButton(link: Element, index: number) {
    const button = document.createElement("button");
    const btnTitle = this.getButtonTitle(link);
    button.classList.add("cali-button");
    button.setAttribute(`data-california-navigation`, `navToSubPage|${index}`);
    button.setAttribute(`data-california-navigation-toggle`, `navToSubPage|0`);
    button.innerHTML = `<span class="label">${btnTitle || index}</span>`;

    if (btnTitle && btnTitle.indexOf("(") > -1 && btnTitle.indexOf(")") > -1) {
      button.classList.add("contains-symbol");
    }

    return button;
  }

  private parseSubPageLinks(doc: HTMLElement) {
    let buttonPanel = doc.querySelector(".trip-subpage-button-panel");
    if (!buttonPanel) {
      buttonPanel = document.createElement("div");
      buttonPanel.classList.add("trip-subpage-button-panel");
      doc.insertBefore(buttonPanel,doc.firstChild);
    }

    const links = Array.from(doc.querySelectorAll(".xref"));
    const subPages = Array.from(doc.querySelectorAll("[data-california-sub-page]"));
    if (links.length >= subPages.length - 1) {
      links.forEach((link: Element, index: number) => {
        const anchor = link.querySelector("a");
        const href = anchor ? anchor.getAttribute("href") : "";
        const target = this.getItemIDFromURL(href || "");

        if (target != this.urlTopicID) return;

        if (!buttonPanel || !anchor) return;

        buttonPanel.appendChild(this.createSubPageButton(link, index + 1));

        if (anchor.parentElement && anchor.parentElement.childElementCount == 1) {
          if (anchor.parentElement.parentElement) {
            anchor.parentElement.parentElement.removeChild(anchor.parentElement);
          }
        } else {
          if (anchor.parentElement) {
            anchor.parentElement.removeChild(anchor);
          }
        }
      });
    } else {
      const segments = Array.from(doc.querySelectorAll(".segment"));
      segments.forEach((seg: Element, index: number) => {
        if (index < 1 || !buttonPanel) return;
        buttonPanel.appendChild(this.createSubPageButton(seg, index));
      });
    }
  }

  private parseLinks(doc: HTMLElement) {
    const links = Array.from(doc.querySelectorAll(".xref"));

    links.forEach((link: Element) => {
      const anchor = link.querySelector("a");
      const href = anchor ? anchor.getAttribute("href") : "";
      const target = this.getItemIDFromURL(href || "");

      if (target == this.urlTopicID) {
        if (link.parentElement) link.parentElement.removeChild(link);
        return;
      }

      const button = document.createElement("button");
      button.classList.add("cali-button");
      button.setAttribute(`data-california-navigation`, `navToID|${target}`);
      button.innerHTML = `<span class="label">${(anchor && anchor.innerText) || ""}</span>`;

      if (link.parentElement) {
        link.parentElement.insertBefore(button, link);
        link.parentElement.removeChild(link);
      }
    });
  }

  public Render(url: string): Promise<string> {
    this.urlTopicID = this.getItemIDFromURL(url);
    const localizedURL = Localization.LocalizeURL(url, "-");

    return new Promise(resolve => {
      this.fileReader
        .Exists(localizedURL)
        .then((exists: boolean) => {
          if (exists) {
            return this.fileReader.Text(localizedURL);
          } else {
            return this.fileReader.Text(url);
          }
        })
        .then((data: string) => {
          const tmpDoc = document.createElement("section");
          tmpDoc.innerHTML = data;
          this.removeEmptySegments(tmpDoc);
          this.removeImg(tmpDoc);
          this.setTypeClasses(tmpDoc);
          this.parseSubPages(tmpDoc);
          this.parseSubPageLinks(tmpDoc);
          this.parseLinks(tmpDoc);
          resolve(`${tmpDoc.innerHTML}`);
        });
    });
  }
}
