export default class IFNE {
  constructor() {}

  private subPageOveride: HTMLElement | null = null;
  private overridden: boolean = false;

  public OnPorgressContentDisplayed(e: HTMLElement) {
    if (this.subPageOveride === null) {
      this.subPageOveride = e;
    }
  }

  public OnSubPageChange(e: number) {
    this.onResize();

    let firstFoundSubPage: HTMLElement | null = document.querySelector(
      `[data-california-sub-page="${e}"]`
    );

    if (this.subPageOveride && !this.overridden) {
      firstFoundSubPage = this.subPageOveride;
      this.overridden = true;
    }
    if (firstFoundSubPage) this.swipeToContent(firstFoundSubPage);
  }

  public ResetSwipe() {
    const mainstage: HTMLElement | null = document.querySelector(`#cali-app .cali-component-area`);
    const appBody: HTMLElement | null = document.querySelector(`#cali-app .cali-body`);
    if (!mainstage || !appBody) return;
    mainstage.style.left = "";
  }

  private swipeToContent(e: HTMLElement) {
    const mainstage: HTMLElement | null = document.querySelector(`#cali-app .cali-component-area`);
    const appBody: HTMLElement | null = document.querySelector(`#cali-app .cali-body`);
    if (!mainstage || !appBody) return;

    if (mainstage.offsetWidth <= appBody.offsetWidth) {
      return;
    }

    const focusDirection: string | undefined = e.dataset.californiaSubPageFocus;

    switch (focusDirection) {
      case "left":
        mainstage.style.left = "";
        break;
      case "right":
        mainstage.style.left = "-100%";
        break;
    }
  }

  private onResize() {
    const containBoxes: Array<HTMLElement> = Array.from(
      document.querySelectorAll(`[data-resize-to-contain="true"]`)
    );
    if (containBoxes.length < 1) return;

    containBoxes.forEach(box => {
      if (!box.parentElement) return;

      box.style.height = "";
      box.style.width = "";

      const orgW = box.offsetWidth;
      const orgH = box.offsetHeight;
      const ratio = orgH / orgW;

      const parentW = box.parentElement.offsetWidth;
      const parentH = box.parentElement.offsetHeight;

      let maxW;
      let maxH;
      if (orgW >= orgH) {
        maxW = parentW;
        maxH = parentW * ratio;
      } else {
        maxW = parentH / ratio;
        maxH = parentH;
      }

      if (maxH >= parentH) {
        box.style.width = `${parentH / ratio}px`;
        box.style.height = `${parentH}px`;
      } else {
        box.style.width = `${parentW}px`;
        box.style.height = `${parentW * ratio}px`;
      }
    });
  }

  public ResizeContent() {
    window.removeEventListener("resize", this.onResize);
    window.addEventListener("resize", this.onResize);
  }

  public HideMenu() {
    const menuButton = document.querySelector(
      "#cali-app .cali-dropdown .cali-button.navigation-toggle-menu"
    );
    if (!menuButton) return;
    const menu = document.querySelector("#cali-app .cali-dropdown > ul");
    if (!menu) return;
    menuButton.classList.remove("active");
    menu.classList.remove("active");
  }

  public MobileHomeButton() {
    let buttonPanel = document.querySelector("#cali-app header .extra-buttons");
    if (!buttonPanel) {
      buttonPanel = document.createElement("div");
      buttonPanel.classList.add("extra-buttons");
    }

    const homeButton = document.querySelector(
      "#cali-app header .cali-button.cali-navigation-to-home"
    );
    if (!homeButton) return;

    const headerNav = document.querySelector(`#cali-app header nav`);
    if (!headerNav) return;

    const mobileMenuHomeButton: HTMLElement = homeButton.cloneNode(true) as HTMLElement;

    const menu = document.querySelector("#cali-app header .cali-dropdown ul");
    if (!menu) return;
    buttonPanel.appendChild(mobileMenuHomeButton);
    menu.insertBefore(buttonPanel, menu.firstChild);

    mobileMenuHomeButton.addEventListener("touchstart", () => {
      (<any>window).California.ExternalCommand("NavToHome");
    });
  }

  public AddGalleryButton() {
    let buttonPanel = document.querySelector("#cali-app header .extra-buttons");
    if (!buttonPanel) {
      buttonPanel = document.createElement("div");
      buttonPanel.classList.add("extra-buttons");
    }

    const galleryButtonItem = document.querySelector(
      `#cali-app header li[data-california-id="ddbb7665-16dd-4793-a431-6ef7d0c7c5bb"]`
    );
    if (!galleryButtonItem) return;

    const galleryButton = galleryButtonItem.querySelector(`button.cali-button`);
    if (!galleryButton) return;

    const headerNav = document.querySelector(`#cali-app header nav`);
    if (!headerNav) return;

    const mobileGalleryButton: HTMLElement = galleryButton.cloneNode(true) as HTMLElement;
    mobileGalleryButton.classList.remove("cali-selected");
    mobileGalleryButton.classList.add("gallery-navigation");

    const headerGalleryButton: HTMLElement = mobileGalleryButton.cloneNode(true) as HTMLElement;

    const menu = document.querySelector("#cali-app header .cali-dropdown ul");
    if (!menu) return;
    buttonPanel.appendChild(mobileGalleryButton);
    menu.insertBefore(buttonPanel, menu.firstChild);
    headerNav.appendChild(headerGalleryButton);
    if (galleryButtonItem.parentElement) {
      galleryButtonItem.parentElement.removeChild(galleryButtonItem);
    }

    mobileGalleryButton.addEventListener("touchstart", () => {
      (<any>window).California.ExternalCommand("OpenPopup", "ddbb7665-16dd-4793-a431-6ef7d0c7c5bb");
    });
    headerGalleryButton.addEventListener("click", () => {
      (<any>window).California.ExternalCommand("OpenPopup", "ddbb7665-16dd-4793-a431-6ef7d0c7c5bb");
    });
  }
}
