<template>
  <section class="cali-footer-content">
    <nav ref="nav-content">
      <navigation-children
        :ParentItem="CaliforniaCourse.MenuRoot || CaliforniaCourse.NavigationBase"
      />
      <navigation-to-home/>
      <navigation-to-parent/>
      <navigation-to-sibling
        v-if="CaliNavigation.NumberOfSubPages == 0 || CaliNavigation.SubPageLocation == 0 || !navigateSubPages"
        :NavigationDirection="'back'"
        :LinkItem="prevNavItem"
      />
      <navigation-to-subpage v-else :NavigationDirection="'back'"/>
      <navigation-to-sibling
        v-if="CaliNavigation.NumberOfSubPages < 1 ||  CaliNavigation.SubPageLocation >= CaliNavigation.NumberOfSubPages || !navigateSubPages"
        :NavigationDirection="'forward'"
        :LinkItem="nextNavItem"
      />
      <navigation-to-subpage v-else :NavigationDirection="'forward'"/>
      <navigation-exit/>
    </nav>
  </section>
</template>

<script>
import EventBus from "../../../../Vue.EventBus";
import NavigationToSibling from "./ToSibling";
import NavigationToSubpage from "./ToSubpage";
import NavigationExit from "./Exit";
import NavigationChildren from "./Children.vue";
import NavigationToParent from "../../../Common/Parts/Navigation/ToParent";
import NavigationToHome from "../../../Common/Parts/Navigation/ToHome";
import Progress from "../../../../California.Progress";

export default {
  name: "navigtion-footer-console",
  props: {},
  data() {
    return {
      CaliNavigation: this.$Navigation,
      CaliforniaCourse: this.$CaliforniaCourse
    };
  },
  computed: {
    navigateSubPages() {
      const progType =
        this.CaliforniaCourse.CurrentItem.Settings.Progresstype || "";
      return progType.toLowerCase() == "subpages";
    },
    nextNavItem() {
      const nxtItem = this.CaliforniaCourse.CurrentItem.Settings.NextItem;
      if (nxtItem && nxtItem.toLowerCase() == "bookmark") {
        return Progress.BookmarkItem;
      } else {
        return this.CaliforniaCourse.Navigator.GetNextItem();
      }
    },
    prevNavItem() {
      const prvItem = this.CaliforniaCourse.CurrentItem.Settings.PreviousItem;
      if (prvItem && prvItem.toLowerCase() == "bookmark") {
        return Progress.BookmarkItem;
      } else {
        return this.CaliforniaCourse.Navigator.GetPreviousItem();
      }
    }
  },
  methods: {},
  components: {
    NavigationToHome,
    NavigationToParent,
    NavigationToSibling,
    NavigationToSubpage,
    NavigationExit,
    NavigationChildren
  }
};
</script>