export default class mockAPI {

    private dataHolder: { [key: string]: string } = {};

    private getFromLocalStorage(): { [key: string]: string } {
        let savedData: string = window.localStorage.getItem('cmi') || '{}'
        return JSON.parse(savedData) || {};
    }

    private saveToLocalStorage() {
        let saveData: object = this.dataHolder
        window.localStorage.setItem('cmi', JSON.stringify(saveData));
    }

    public Initialize() {
        this.dataHolder = this.getFromLocalStorage();
    }

    public Terminate() {
        this.saveToLocalStorage()
    }
    public SetValue(i_key: string, i_val: string) {
        this.dataHolder[i_key] = i_val;
    }

    public GetValue(i_key: string) {
        return this.dataHolder[i_key];
    }

    public Commit() {
        return true;
    }

}