import Vue from "vue";
import SettingsApp from "../Vue.Components/SlideShow/Screens/Settings.vue";
import VueI18n from "vue-i18n";
import { Localization } from "../California.Localization";

export class Settings {
  private i18n: VueI18n;

  constructor() {
    this.i18n = this.initLocalization();
    this.StartSettingsApp();
  }

  private initLocalization(): VueI18n {
    Vue.use(VueI18n);
    return new VueI18n({
      locale: "en",
      fallbackLocale: "en",
      messages: Localization.UIStrings
    });
  }

  public StartSettingsApp() {

    const bodyNode:HTMLElement | null = document.querySelector(".cali-body");
    if(bodyNode){
      const settingsNode = document.createElement("div");
      settingsNode.setAttribute("id", "cali-settings");
      bodyNode.appendChild(settingsNode);
    }
    
    let i18n = this.i18n;
    Vue.prototype.$CaliforniaSettingsApp = new Vue({
      el: "#cali-settings",
      template: "<SettingsApp/>",
      i18n,
      components: { SettingsApp }
    });
  }
}
