export default class VHO {
  private win: any = window as any;
  private rowResizeTimer: number;
  private area: HTMLElement | null = null;
  private rows: Array<HTMLElement> | null = null;
  private retry: number = 0;

  constructor() {
    this.rowResizeTimer = -1;
  }

  private scaleChapterSelector() {
    this.area = document.querySelector(
      `[data-item-type="navigation-base"] .cali-collection.cali-body .cali-navigation-children`
    ) as HTMLElement;
    if (!this.area) {
      if (this.retry < 5) {
        window.setTimeout(this.scaleChapterSelector, 200);
      }
      this.retry += 1;
      return;
    }

    this.retry = 0;

    const ASPECT_RATIO: number = 160 / 1570;

    const areaWidth = this.area.offsetWidth;
    const areaHeigt = this.area.offsetHeight;
    const newHeight: number = areaWidth * ASPECT_RATIO;

    let offset: number = -1;
    this.rows = Array.from(this.area.querySelectorAll("li"));
    for (let i = 0; i < this.rows.length; i++) {
      const rowHeight = Math.min(areaHeigt / 5, newHeight);
      this.rows[i].style.height = rowHeight + "px";
      const newWidth: number = rowHeight / ASPECT_RATIO;
      const logoMargin = newWidth * 0.1;
      const label = this.rows[i].querySelector(`.label`) as HTMLElement;
      const middle = areaWidth / 2;
      const fontSize = Math.min(rowHeight * 0.25, 28);
      const marginLeft = middle + logoMargin * offset ;
      label.style.maxWidth = areaWidth - marginLeft - fontSize + "px";
      label.style.paddingRight = fontSize + "px";
      label.style.marginLeft = marginLeft + "px";
      label.style.fontSize = fontSize + "px";
      offset += 1;
    }
  }

  public ChapterSelector() {
    this.win.California.Events.$on("ContentReady", this.scaleChapterSelector);
    window.addEventListener("resize", () => {
      window.clearTimeout(this.rowResizeTimer);
      this.rowResizeTimer = window.setTimeout(this.scaleChapterSelector, 30);
    });
    this.scaleChapterSelector();
  }

  public OnSubtitleCueChange(e: any) {
    const subs: HTMLElement | null = e.element.querySelector(".cali-subtitles");
    if (!subs) return;
    const videoLeft = e.mediaElement.offsetLeft - e.mediaElement.clientWidth / 2;
    const videoTop = e.mediaElement.offsetTop - e.mediaElement.clientHeight / 2;
    const videoBottom = videoTop + e.mediaElement.clientHeight;
    subs.style.bottom = "auto";
    subs.style.top = `${videoBottom - subs.offsetHeight}px`;
    subs.style.width = `${e.mediaElement.clientWidth}px`;
    subs.style.left = `${videoLeft}px`;
  }
}
