<template>
    
    <router-link
      tag="button"
      class="cali-button cali-navigation-to-parent"
      :title="itemName"
      :disabled="!isActive"
      :to="routeURL"
    >
      <span class="label" v-t="{path:'message.UP_BUTTON_LABEL', locale: $i18n.locale}"></span>
    </router-link>

</template>

<script>
export default {
  name: "navigation-to-parent",
  data() {
    return {
      CaliforniaCourse: this.$CaliforniaCourse
    };
  },
  computed: {
    linkItem() {
      return this.CaliforniaCourse.CurrentItem.Parent;
    },
    isActive() {
      return this.linkItem ? true : false;
    },
    itemName() {
      if (!this.isActive) return "";
      return this.linkItem.Name;
    },
    routeURL() {
      if (!this.isActive) return "";

      let isParentRoot = this.linkItem.ID == this.CaliforniaCourse.Root.ID;
      if (isParentRoot) return "/home";

      return { name: "collection", params: { id: this.linkItem.ID } };
    }
  }
};
</script>
