<template>
  <router-link
    tag="button"
    class="cali-button navigation-to-sibling"
    :class="[directionClassname]"
    :title="itemName"
    :disabled="!isActive"
    :to="routeURL"
  >
    <span class="label" v-html="directionLabel"></span>
  </router-link>
</template>

<script>
export default {
  name: "navigation-to-sibling",
  props: ["LinkItem", "NavigationDirection"],
  data() {
    return {};
  },
  computed: {
    directionClassname() {
      return this.NavigationDirection == "back" ? "prev" : "next";
    },
    directionLabel() {
      return this.NavigationDirection == "back"
        ? this.$t("message.PREVIOUS_BUTTON_LABEL", this.$i18n.locale)
        : this.$t("message.NEXT_BUTTON_LABEL", this.$i18n.locale);
    },
    isActive() {
      return this.LinkItem &&
        this.completedCriteria &&
        !this.LinkItem.EvaluateLock()
        ? true
        : false;
    },
    getProgressLimit() {
      const current = this.$CaliforniaCourse.CurrentItem;
      let porgressLimit = 1;
      let progresstype = (current.Settings.Progresstype || "").toLowerCase();
      if (progresstype.indexOf(":") != -1) {
        const parts = progresstype.split(":");
        progresstype = parts[0];
        porgressLimit = parseFloat(parts[1].toString());
      }

      if (progresstype == "subpage") {
        porgressLimit = 1;
      }

      return porgressLimit;
    },
    getProgressMeter() {
      const current = this.$CaliforniaCourse.CurrentItem;
      const progresstype = (current.Settings.Progresstype || "").toLowerCase();
      let progress = current.Progress;
      if (
        progresstype == "completed" &&
        current.SubProgress > -1 &&
        current.ChildCount > 0
      ) {
        progress = current.SubProgress;
      }
      console.log(current.Name + " internal progress is: " + progress);
      return progress;
    },
    completedCriteria() {
      const current = this.$CaliforniaCourse.CurrentItem;
      const nxtItem = current.Settings.NextItem;
      if (this.NavigationDirection == "back") {
        return true;
      } else if (nxtItem && nxtItem.toLowerCase() == "bookmark") {
        return true;
      } else {
        console.log(
          "s: " + this.getProgressMeter + " >= " + this.getProgressLimit
        );
        return this.getProgressMeter >= this.getProgressLimit;
      }
    },
    routeURL() {
      if (!this.isActive) return "";
      let hasChildren = this.LinkItem && this.LinkItem.ChildCount > 0;
      let routeComponent = hasChildren ? "collection" : "item";
      return { name: routeComponent, params: { id: this.LinkItem.ID } };
    },
    itemName() {
      if (!this.isActive) return "";
      return this.LinkItem.Name;
    }
  }
};
</script>