import * as Core from "california_core";

export class PreSettings {
  private contentFolder: string;
  private settingsFile: string;

  constructor(contentFolder: string, settingsFile: string) {
    this.contentFolder = contentFolder;
    this.settingsFile = settingsFile;
  }

  private loadSettings(settingsFile: string): Promise<JSON> {
    return new Promise((resolve, reject) => {
      new Core.IO.Reader()
        .JSON(settingsFile)
        .then((result: JSON) => {
          resolve(result);
        })
        .catch((reason: Error) => {
          reject(reason);
        });
    });
  }

  public Load(): Promise<JSON> {
    return new Promise((resolve, reject) => {
      this.loadSettings(this.settingsFile)
        .then((data: JSON) => {
          resolve(data);
        })
        .catch(() => {
          resolve(JSON.parse("{}"));
        });
    });
  }
}
