<template>

  <section class="cali-home">
    <section class="cali-component-area">
    
      <h1 v-html="Root.Name" class="cali-course-title"></h1>
      <template v-for="(url, i) in Root.Data">
        <component
        :is="selectComponent(Root, url)"
        class="cali-component"
        v-bind="{selectedItem: Root, dataIndex: i}"
        :data-cali-data-type="selectComponent(Root, url)"
        :data-cali-index="i"
        :key="Root.ID + '-' + i "
        />
      </template>
      <navigation-children :ParentItem="Root" />
    </section>

  </section>

</template>

<script>
import Vue from "vue";
import { Data } from "../../../California.Data";
import NavigationChildren from "../Parts/Navigation/Children.vue";

let comps = Object.assign(
  {
    NavigationChildren
  },
  Data.LoadComponentFolder(require.context("../../../California.ContentTypes", true, /\.vue$/))
);

export default {
  name: "home",

  data() {
    return {
      CaliforniaCourse: this.$CaliforniaCourse,
      Root: this.$CaliforniaCourse.Root,
      SCORM: Vue.prototype.$SCORM
    };
  },
  beforeRouteUpdate: (to, from, next) => {
    Vue.prototype.$Navigation.FromRoute(to, from, next);
  },
  beforeRouteEnter: (to, from, next) => {
    Vue.prototype.$Navigation.FromRoute(to, from, next);
  },
  methods: {
    selectComponent(child, url) {
      return Data.ParseDataURL(child, url).contenttype;
    }
  },
  components: comps
};
</script>