<template>
  <div class="cali-preloader"></div>
</template>

<script>
import EventBus from "../../../../Vue.EventBus";
import { setTimeout } from "timers";

export default {
  name: "preloader",
  data() {
    return {};
  },
  created() {
    this.loadTimer = null;
  },
  methods: {
    check() {
      const allComponents = document.querySelectorAll(
        "#cali-app .cali-component"
      );
      const loadedComponents = document.querySelectorAll(
        "#cali-app .cali-component[data-loaded='true']"
      );
      if (loadedComponents.length < allComponents.length) {
        this.loadTimer = setTimeout(this.check, 50);
      } else {
        this.$nextTick().then(() => {
          EventBus.$emit("ContentReady");
          window.dispatchEvent(new CustomEvent("ContentReady"));
          this.$el.classList.add("removed");
        });
      }
    }
  },
  watch: {
    $route(from, to) {
      this.$nextTick().then(() => {
        this.check();
      });
    }
  }
};
</script>

<style lang="less">
.cali-preloader {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-color: #fff;
  opacity: 1;
  transition: opacity 0.3s ease-in-out 0s, height 0s linear 0s;
  &.removed {
    opacity: 0;
    height: 0%;
    transition: opacity 0.3s ease-in-out 0.2s, height 0s linear 0.3s;
  }
}
</style>
