import * as Net from './Net'
import * as IO from './IO'
import * as Data from './Data'
import * as Navigation from './Navigation'

export {
    Net,
    IO,
    Data,
    Navigation
}

export class Application {

    public Course: Data.Structure = null

    public LoadStructure(coursefile: string, filter: Array<string> = null): Promise<object> {
        let instance = this;
        return new Promise((resolve, reject) => {
            new IO.Reader()
                .JSON(coursefile)
                .then(result => {
                    return new Data.Structure().CreateFromJSON(result, filter)
                })
                .then(loadedStructure => {
                    instance.Course = loadedStructure
                    resolve(instance.Course)
                })
                .catch(reason => {
                    reject(reason)
                })
        })
    }
}