import { CaliMediaControlLayout } from "./controls-layout";
import EventBus from "../../Vue.EventBus";

export class CaliMediaControlSubtitles {
  private static onSubtitleCueChange(
    element: HTMLElement,
    mediaElement: HTMLVideoElement,
    trackTag: HTMLTrackElement,
    susbsTag: HTMLElement
  ) {
    if (trackTag.track.activeCues.length > 0) {
      const activeCue = trackTag.track.activeCues[0];
      susbsTag.innerHTML = activeCue.text;

      if (activeCue.text.length > 0) {
        susbsTag.classList.remove("empty");
      } else {
        susbsTag.classList.add("empty");
      }
    } else {
      susbsTag.innerHTML = "";
      susbsTag.classList.add("empty");
    }
    EventBus.$emit("subtitlecuechange", { element: element, mediaElement: mediaElement });
  }

  public static Render(data: string, element: HTMLElement, mediaElement: HTMLVideoElement) {
    if (!mediaElement) return;

    let susbsTag: HTMLElement;
    let subtitleTrackTag: HTMLTrackElement;

    const blob = new Blob([data as BlobPart], { type: "text/vtt" });
    const blobUrl = URL.createObjectURL(blob);

    subtitleTrackTag = document.createElement("track");
    subtitleTrackTag.setAttribute("kind", "subtitles");

    subtitleTrackTag.addEventListener("load", () => {
      subtitleTrackTag.addEventListener("cuechange", () => {
        this.onSubtitleCueChange(element, mediaElement, subtitleTrackTag, susbsTag);
      });
    });


    subtitleTrackTag.setAttribute("src", blobUrl);
    mediaElement.appendChild(subtitleTrackTag);
    mediaElement.textTracks[0].mode = "hidden";

    susbsTag = document.createElement("div");
    susbsTag.setAttribute("class", "cali-subtitles");
    susbsTag.classList.add("empty");
    const wrapper = element.querySelector(".media-wrapper");
    if (mediaElement && wrapper) {
      wrapper.insertBefore(susbsTag, mediaElement.nextElementSibling);
    }

  }
}
