import mockAPI from './mockAPI'
import * as Core from 'california_core'
import * as lzutf8 from "lzutf8"

enum _APIversion {
    SCORM_1_2 = 'SCORM Version 1.2',
    SCORM_2004 = 'SCORM Version 2004',
    NOT_SET = ''
}


export default class SCORM {

    private _contactEstablished = false
    private _startTime: Date = new Date()
    private _localAPIVersion: _APIversion = _APIversion.NOT_SET
    private _localAPI: any = null
    private _ModuleScores: { [key: string]: number } = {}
    private _isSCORM: boolean = false
    private _canCommit: boolean = true
    private _commitTimer: number = -1

    private _centisecsToISODuration(n: number): string {
        // Note: SCORM and IEEE 1484.11.1 require centisec precision
        // Months calculated by approximation based on average number
        // of days over 4 years (365*4+1), not counting the extra day
        // every 1000 years. If a reference date was available,
        // the calculation could be more precise, but becomes complex,
        // since the exact result depends on where the reference date
        // falls within the period (e.g. beginning, end or ???)
        // 1 year ~ (365*4+1)/4*60*60*24*100 = 3155760000 centiseconds
        // 1 month ~ (365*4+1)/48*60*60*24*100 = 262980000 centiseconds
        // 1 day = 8640000 centiseconds
        // 1 hour = 360000 centiseconds
        // 1 minute = 6000 centiseconds
        n = Math.max(n, 0) // there is no such thing as a negative duration
        let str: string = 'P'
        let nCs: number = n
        // Next set of operations uses whole seconds
        let nY: number = Math.floor(nCs / 3155760000)
        nCs -= nY * 3155760000
        let nM: number = Math.floor(nCs / 262980000)
        nCs -= nM * 262980000
        let nD: number = Math.floor(nCs / 8640000)
        nCs -= nD * 8640000
        let nH: number = Math.floor(nCs / 360000)
        nCs -= nH * 360000
        let nMin: number = Math.floor(nCs / 6000)
        nCs -= nMin * 6000
        // Now we can construct string
        if (nY > 0) str += nY + 'Y'
        if (nM > 0) str += nM + 'M'
        if (nD > 0) str += nD + 'D'
        if ((nH > 0) || (nMin > 0) || (nCs > 0)) {
            str += 'T'
            if (nH > 0) str += nH + 'H'
            if (nMin > 0) str += nMin + 'M'
            if (nCs > 0) str += nCs / 100 + 'S'
        }
        if (str == 'P') str = 'PT0H0M0S'
        // technically PT0S should do but SCORM test suite assumes longer form.
        return str
    }

    private _millisecondsToCMIDuration(n: number): string {
        //Convert duration from milliseconds to 0000:00:00.00 format
        let hms: string = ''
        let dtm: Date = new Date()
        dtm.setTime(n)
        let h: string = '000' + Math.floor(n / 3600000)
        let m: string = '0' + dtm.getMinutes()
        let s: string = '0' + dtm.getSeconds()
        let cs: string = '0' + Math.round(dtm.getMilliseconds() / 10)
        hms = h.substr(h.length - 4) + ':' + m.substr(m.length - 2) + ':'
        hms += s.substr(s.length - 2) + '.' + cs.substr(cs.length - 2)
        return hms
    }

    private _is2004(): boolean {
        return this._localAPIVersion == _APIversion.SCORM_2004
    }


    private _findAPI_1_2(win: any): void {
        try {
            let loc = win.parent.document.location
            // accessible
        } catch (e) {
            console.warn('Caolifornia.SCORM: Denied access to parent')
            return
        }
        while ((win.API === null) && (win.parent !== null) && (win.parent != win)) {
            win = win.parent
        }
        if (!win.API) return
        this._localAPI = win.API
        this._localAPIVersion = _APIversion.SCORM_1_2
    }


    private _findAPI_2004(win: any): void {
        try {
            let loc = win.parent.document.location
            // accessible
        } catch (e) {
            console.warn('Caolifornia.SCORM: Denied access to parent')
            return
        }
        while (!win.API_1484_11 && win.parent && win.parent != win) {
            win = win.parent
        }
        if (!win.API_1484_11) return
        this._localAPI = win.API_1484_11
        this._localAPIVersion = _APIversion.SCORM_2004
    }


    private _initAPI(win: any, i_callback: Function) {

        this.MAX_SCORE = 0
        this.PASSING_SCORE = 0

        this._findAPI_2004(win)
        if (!this._localAPIVersion && win.opener) {
            this._findAPI_2004(win.opener)
        }
        if (!this._localAPI) {
            this._findAPI_1_2(win)
            if (!this._localAPI && win.opener) {
                this._findAPI_1_2(win.opener)
            }
        }
        if (this._localAPI === null) {
            this._localAPIVersion = _APIversion.SCORM_2004
            this._localAPI = new mockAPI()
        }
        if (this._localAPIVersion == _APIversion.SCORM_1_2) {
            this._localAPI.LMSInitialize('')
            i_callback()
        } else if (this._localAPIVersion == _APIversion.SCORM_2004) {
            this._localAPI.Initialize('')
            i_callback()
        }

    }


    private _endAPI() {
        if (!this._localAPI) return
        if (this._localAPIVersion == _APIversion.SCORM_1_2) {
            this._localAPI.LMSFinish('')
        } else if (this._localAPIVersion == _APIversion.SCORM_2004) {
            this._localAPI.Terminate('')
        }
        this._localAPI = null
    }

    private _totalScore(): number {
        let accumScore = 0
        for (let key in this._ModuleScores) {
            accumScore += this._ModuleScores[key]
        }
        return accumScore
    }

    private _setLMSvalue(param: string, val: string) {
        if (!this._localAPI) return
        if (this._localAPIVersion == _APIversion.SCORM_1_2) {
            this._localAPI.LMSSetValue(param, val.toString())
        } else if (this._localAPIVersion == _APIversion.SCORM_2004) {
            this._localAPI.SetValue(param, val.toString())
        }
    }

    private _getLMSvalue(param: string) {

        if (!this._localAPI) return ''
        if (this._localAPIVersion == _APIversion.SCORM_1_2) {
            return this._localAPI.LMSGetValue(param)
        } else if (this._localAPIVersion == _APIversion.SCORM_2004) {
            return this._localAPI.GetValue(param)
        }

    }


    private _LMSCommit() {
        if (this._commitTimer === -1) return ''
        this._commitTimer = window.setTimeout(() => {
            this._commitTimer = -1
        }, 1000)

        let commited: boolean = false
        if (this._localAPIVersion == _APIversion.SCORM_1_2) {
            commited = this._localAPI.LMSCommit('')
        } else if (this._localAPIVersion == _APIversion.SCORM_2004) {
            commited = this._localAPI.Commit('')
        }
        if (!commited) {
            alert('Error: SCO lost contact with the LMS!\nNo progress will be saved. Please close the course and restart the LMS')
        }
    }


    public MAX_SCORE: number = 0
    public PASSING_SCORE: number = 0
    public BEST_SCORE: number = 0
    public SuspendData: string = ''
    public Running: boolean = false


    public StartLMS(): void {

        this._initAPI(window, () => {

            this._contactEstablished = true

            let LMSExitParam = this._is2004() ? 'cmi.exit' : 'cmi.core.exit'
            this._setLMSvalue(LMSExitParam, 'suspend')

            let suspData = this._getLMSvalue('cmi.suspend_data') || ''

            try {
                suspData = lzutf8.decompress(lzutf8.decodeBase64(suspData))
                this.SuspendData = JSON.parse(suspData)
            }
            catch (er) {
                this.SuspendData = ''
            }

            let LMSStatusParam = this._is2004() ? 'cmi.completion_status' : 'cmi.core.lesson_status'
            if (this._getLMSvalue(LMSStatusParam) != 'completed') {
                this._setLMSvalue(LMSStatusParam, 'incomplete')
            }

            let LMSMaxScoreParam = this._is2004() ? 'cmi.score.max' : 'cmi.core.score.max'
            this._setLMSvalue(LMSMaxScoreParam, this.MAX_SCORE.toString())

            let LMSScoreParam = this._is2004() ? "cmi.score.raw" : "cmi.core.score.raw"
            this.BEST_SCORE = parseInt(this._getLMSvalue(LMSScoreParam), 10)
            if (isNaN(this.BEST_SCORE)) this.BEST_SCORE = 0

            this._startTime = new Date()
            this._LMSCommit()

            this.Running = true

        })

    }

    public ExitLMS() {
        console.log("Exit SCORM Session")
        let dtm = new Date()
        let strtTime = this._startTime || new Date()
        let n = dtm.getTime() - strtTime.getTime()
        let LMSTimeParam = this._is2004() ? 'cmi.session_time' : 'cmi.core.session_time'
        let LMStime = this._is2004() ? this._centisecsToISODuration(n / 10) : this._millisecondsToCMIDuration(n)
        this._setLMSvalue(LMSTimeParam, LMStime)
        this._LMSCommit()
        this._endAPI()
    }


    public UpdatePosition(JSON_Data: string) {
        this._setLMSvalue('cmi.suspend_data', lzutf8.Encoding.Base64.encode(lzutf8.compress(JSON_Data)))
        this._LMSCommit()
    }

    public Clear() {
        let LMSStatusParam = this._localAPIVersion == _APIversion.SCORM_2004 ? 'cmi.completion_status' : 'cmi.core.lesson_status'
        let LMSsuccess = this._localAPIVersion == _APIversion.SCORM_2004 ? 'cmi.success_status' : 'cmi.core.success_status'
        let LMSScoreParam = this._is2004() ? "cmi.score.raw" : "cmi.core.score.raw"
        this.SuspendData = ''
        this._setLMSvalue(LMSStatusParam, 'incomplete')
        this._setLMSvalue(LMSsuccess, 'unknown')
        this._setLMSvalue(LMSScoreParam, '0')
        this._setLMSvalue("cmi.suspend_data", 'null')
        this._LMSCommit()
    }


    public GetLearnerName(): string {
        let LMSNameParam = this._is2004() ? "cmi.learner_name" : "cmi.core.student_name"
        return this._getLMSvalue(LMSNameParam)
    }

    public GetLearnerRol() {
        return []
    }

    public AddScore(val: string, module: Core.StructureItem) {
        this._ModuleScores[module.ID] = parseFloat(val)
    }

    public ReduceScore(val: string, module: Core.StructureItem) {
        if (this._ModuleScores[module.ID]) {
            this._ModuleScores[module.ID] = Math.max(this._ModuleScores[module.ID] - parseFloat(val), 0)
        }
    }

    public SetScore() {
        let totalScore = this._totalScore()
        let LMSScoreParam = this._is2004() ? "cmi.score.raw" : "cmi.core.score.raw"
        this._setLMSvalue(LMSScoreParam, totalScore.toString())
        this._LMSCommit()
    }

    public SetCourseScore(val: string) {
        let score = parseFloat(val)
        if (isNaN(score) && val != 'auto') return

        let LMSScoreParam = this._is2004() ? "cmi.score.raw" : "cmi.core.score.raw"

        if (val == 'auto') {
            score = this._totalScore()
        }

        this._setLMSvalue(LMSScoreParam, score.toString())

        let scaledScore = score / this.MAX_SCORE
        if (isNaN(scaledScore)) scaledScore = 0

        if (this._is2004()) this._setLMSvalue("cmi.score.scaled", scaledScore.toString())

        this._LMSCommit()
    }

    public GetTotalScore(): number {
        return this._totalScore()
    }

    public GetCourseScore() {
        let LMSScoreParam = this._is2004() ? "cmi.score.raw" : "cmi.core.score.raw"
        let score = parseInt(this._getLMSvalue(LMSScoreParam), 10)
        if (isNaN(score)) score = 0
        return score
    }

    public SetMaxScore(val: number) {
        let score = val
        if (isNaN(score)) return
        this.MAX_SCORE = score
        let LMSMaxScoreParam = this._is2004() ? 'cmi.score.max' : 'cmi.core.score.max'
        this._setLMSvalue(LMSMaxScoreParam, this.MAX_SCORE.toString())
    }

    public AddMaxScore(val: number) {
        let score = val
        if (isNaN(score)) return
        this.MAX_SCORE += score
        let LMSMaxScoreParam = this._is2004() ? 'cmi.score.max' : 'cmi.core.score.max'
        this._setLMSvalue(LMSMaxScoreParam, this.MAX_SCORE.toString())
    }

    public GetMaxScore(): number {
        return this.MAX_SCORE
    }

    public SetCourseSuccessStatus(val: string) { // val = 'passed' or 'failed'
        let LMSsuccess = this._is2004() ? "cmi.success_status" : "cmi.core.success_status"
        this._setLMSvalue(LMSsuccess, val.toLowerCase())
        this._LMSCommit()
    }

    public GetCourseSuccessStatus(): string {
        let LMSsuccess = this._is2004() ? "cmi.success_status" : "cmi.core.success_status"
        return this._getLMSvalue(LMSsuccess)
    }

    public SetCourseCompletionStatus(val: string) { // val = 'completed' or 'incomplete'
        let LMSStatusParam = this._is2004() ? 'cmi.completion_status' : 'cmi.core.lesson_status'
        if (this._getLMSvalue(LMSStatusParam).toLowerCase() == 'completed') return
        this._setLMSvalue(LMSStatusParam, val.toLowerCase())
        this._LMSCommit()
    }

    public GetCourseCompletionStatus(): string {
        let LMSStatusParam = this._is2004() ? 'cmi.completion_status' : 'cmi.core.lesson_status'
        return this._getLMSvalue(LMSStatusParam)
    }

}