<template>
  <cali-html
    v-if="rendered && dataIndex == dataLength-2"
    v-bind="{selectedItem: selectedItem, dataIndex: 0, htmlContent: renderedHTML}"
    :data-cali-ViewItem="selectedItem.ID"
    data-cali-data-type="html"
    class="cali-component"
    :data-cali-index="0"
  ></cali-html>
  <cali-quiz
    v-else-if="rendered && dataIndex == dataLength-1"
    v-bind="{selectedItem: selectedItem, dataIndex: 1, htmlContent: renderedHTML}"
    :data-cali-ViewItem="selectedItem.ID"
    data-cali-data-type="quiz"
    class="cali-component"
    :data-cali-index="1"
  ></cali-quiz>
</template>
<script>
import Vue from "vue";
import { CaliTripQuizControls } from "./controls";
import { Data } from "../../../California.Data";
import caliQuiz from "../../CaliQuiz/CaliQuiz.vue";
import caliHtml from "../../CaliHtml/CaliHtml.vue";

export default {
  components: {
    caliQuiz,
    caliHtml
  },
  name: "cali-tripquiz",
  props: {
    dataIndex: {
      default: 0,
      type: Number
    },
    dataLength: {
      default: 0,
      type: Number
    },
    selectedItem: {
      default: null,
      type: Object
    }
  },
  data() {
    return {
      rendered: false
    };
  },
  computed: {},
  methods: {
    htmlSrc() {
      return (
        this.$ContentFolder +
        Data.ParseDataURL(
          this.selectedItem,
          this.selectedItem.Data[this.dataIndex]
        ).url
      );
    },
    addQuestions(htmlData) {
      const comp = this;
      const tmpDoc = document.createElement("section");
      tmpDoc.innerHTML = htmlData;
      let segments = tmpDoc.querySelectorAll("[data-california-question]");
      segments = comp.getNrandom(segments, 5);
      for (let i = 0; i < segments.length; i++) {
        const seg = segments[i];
        seg.setAttribute("data-california-question", i.toString());
        const innerContainer = seg.querySelector(".segment");
        if (innerContainer) {
          innerContainer.innerHTML += comp.addButtons();
        }
        comp.renderedHTML += seg.outerHTML;
      }
    },
    addErrorMsg(htmlData) {
      const comp = this;
      const tmpDoc = document.createElement("section");
      tmpDoc.innerHTML = htmlData;
      let segments = tmpDoc.querySelectorAll("[data-california-quiz-feedback]");
      for (let i = 0; i < segments.length; i++) {
        const seg = segments[i];
        comp.renderedHTML += seg.outerHTML;
      }
    },
    addTitle(htmlData) {
      const tmpDoc = document.createElement("section");
      tmpDoc.innerHTML = htmlData;
      const title = tmpDoc.querySelector("h1");
      if (title) this.renderedHTML += title.outerHTML;
    },
    addButtons() {
      return `
            <div class="cali-tripquiz-buttons">
            <button class="cali-button cali-button-submit" data-california-quiz-submit="true">
            <span class"label">${this.$CaliforniaApp.$t(
              "message.SUBMIT_BUTTON_LABEL",
              Vue.prototype.$CaliforniaApp.$i18n.locale
            )}</span>
            </button>
            </div>
            `;
    },
    getNrandom(collection, len) {
      const shuffled = Array.from(collection).sort(() => 0.5 - Math.random());
      let selected = shuffled.slice(0, len);
      return selected;
    },
    GetHTML() {
      const comp = this;
      comp.rendered = false;
      this.TripQuizProtocol.Render(
        this.htmlSrc(),
        this.dataIndex,
        this.dataLength
      ).then(function(data) {
        if (comp.dataIndex == comp.dataLength - 1) {
          comp.addTitle(data);
          comp.addQuestions(data);
          comp.addErrorMsg(data);
          //  comp.addButtons();
        } else {
          comp.renderedHTML += data;
        }
        comp.rendered = true;
      });
    }
  },
  created() {
    this.TripQuizProtocol = new CaliTripQuizControls(this.selectedItem);
    this.renderedHTML = "";
  },
  mounted() {
    this.rendered = false;
    this.GetHTML();
  },
  updated() {},
  beforeDestroy() {}
};
</script>

