<template>
  <div
    id="cali-app"
    :data-course-id="CaliforniaCourse.Root.ID"
    :data-context-id="contextCSSID"
    :data-item-id="CaliforniaCourse.CurrentItem.ID"
    :data-item-type="itemType"
    :data-cali-language="this.$VARIABLES.Language"
  >
    <link v-if="contentCSS" :href="contentCSS" rel="stylesheet">
    <header v-if="$route.name != '404' && $route.name != 'settings'">
      <navigtion-header-console/>
      <progress-bar :ProgressItem="CaliforniaCourse.Root" class="cali-total-progress"></progress-bar>
      <progress-bar
        :ProgressItem="CaliforniaCourse.ContextItem || CaliforniaCourse.CurrentItem"
        class="cali-sub-item-progress"
      ></progress-bar>
    </header>
    <router-view class="cali-body"/>
    <preloader></preloader>
    <footer v-if="$route.name != '404' && $route.name != 'settings'">
      <progress-bar :ProgressItem="CaliforniaCourse.Root" class="cali-total-progress"></progress-bar>
      <progress-bar
        :ProgressItem="CaliforniaCourse.ContextItem || CaliforniaCourse.CurrentItem"
        class="cali-sub-item-progress"
      ></progress-bar>
      <navigtion-footer-console/>
    </footer>
    <section id="cali-plugins" v-if="$route.name != '404' && $route.name != 'settings'">
      <template v-for="(name, i) in plugins">
        <component :is="name" class="cali-plugin" :key="'cali-plugin-' + i"/>
      </template>
    </section>
    <modal-iframe></modal-iframe>
  </div>
</template>

<script>
import EventBus from "../Vue.EventBus";
import { Data } from "../California.Data";
import Progress from "../California.Progress";
import NavigtionHeaderConsole from "./SlideShow/Parts/Navigation/HeaderConsole";
import NavigtionFooterConsole from "./SlideShow/Parts/Navigation/FooterConsole";
import ProgressBar from "./SlideShow/Parts/UI/ProgressBar.vue";
import Preloader from "./SlideShow/Parts/UI/Preloader.vue";
import Vue from "vue";
import ModalIframe from "./SlideShow/Parts/UI/ModalIframe.vue";

let replaceRoute = false;

let loadedPlugins = {};
try {
  loadedPlugins = Data.LoadComponentFolder(
    require.context(
      __California_Options__.SkinFolder + "\\plugins",
      true,
      /\.vue$/
    )
  );
} catch (er) {
  loadedPlugins = {};
}

let comps = Object.assign(
  {
    NavigtionHeaderConsole,
    NavigtionFooterConsole,
    ProgressBar,
    Preloader,
    ModalIframe
  },
  loadedPlugins
);

export default {
  name: "cali-app",
  data() {
    return {
      CaliforniaCourse: this.$CaliforniaCourse,
      SCORM: Vue.prototype.$SCORM
    };
  },
  components: comps,
  computed: {
    contextCSSID() {
      let course = this.CaliforniaCourse;
      let parent = course.CurrentItem.Parent;
      let CSSID = course.CurrentItem.ID;

      while (parent && parent.ID !== course.Root.ID) {
        CSSID = parent.ID;
        parent = parent.Parent;
      }

      return CSSID;
    },
    contentCSS() {
      return __California_Options__.DataCSS;
    },
    contentJS() {
      return __California_Options__.DataJS;
    },
    itemType() {
      if (
        this.CaliforniaCourse.CurrentItem.ID == this.CaliforniaCourse.Root.ID
      ) {
        return "home";
      }
      if (
        this.CaliforniaCourse.CurrentItem.ID ==
        this.CaliforniaCourse.NavigationBase.ID
      ) {
        return "navigation-base";
      }
      return this.$route.name;
    },
    plugins() {
      return Object.keys(loadedPlugins);
    }
  },
  created() {
    let bookmark = Progress.GetBookmark();
    replaceRoute = true;
    this.$router.replace({ path: Progress.GetBookmark() });
    this.doingScrollEvent = false;
    this._scrollEmitter = e => {
      if (!this.doingScrollEvent) {
        window.requestAnimationFrame(() => {
          EventBus.$emit("windowScroll", e);
          this.doingScrollEvent = false;
        });
        this.doingScrollEvent = true;
      }
    };
    this._resizeTimer = null;
    this._resizeEmitter = () => {
      clearTimeout(this._resizeTimer);
      this._resizeTimer = setTimeout(e => {
        EventBus.$emit("windowResize", e);
      }, 10);
    };

    this.showPreloader = () => {
      let preloader = document.querySelector(".cali-preloader");
      if (preloader) {
        preloader.classList.remove("removed");
      }
    };

    if (
      "-ms-scroll-limit" in document.documentElement.style &&
      "-ms-ime-align" in document.documentElement.style
    ) {
      // detect it's IE11
      window.addEventListener(
        "hashchange",
        event => {
          var currentPath = window.location.hash.slice(1);
          if (this.$route.path !== currentPath) {
            this.$router.push(currentPath);
          }
        },
        false
      );
    }

    if (this.contentJS) {
      const scriptTag = document.createElement("script");
      scriptTag.src = this.contentJS;
      document.body.appendChild(scriptTag);
    }
  },
  mounted() {
    window.addEventListener("beforeunload", () => {
      this.SCORM.ExitLMS();
    });
    window.addEventListener("scroll", this._scrollEmitter);
    window.addEventListener("resize", this._resizeEmitter);
    window.addEventListener("orientationchange", this._resizeEmitter);
  },
  destroyed() {
    window.removeEventListener("scroll", this._scrollEmitter);
    window.removeEventListener("resize", this._resizeEmitter);
    window.removeEventListener("orientationchange", this._resizeEmitter);
  },
  beforeUpdate() {
    this.showPreloader();
  },
  watch: {
    $route(to, from) {
      if (to.params.command && to.params.command.toLowerCase() == "reset") {
        this.SCORM.Clear();
        window.location.href = window.location.href
          .replace("/#/home/reset", "")
          .replace("#/home/reset", "");
        return;
      }
      Progress.SetProgress();
    }
  }
};
</script>
