export class Path {
  public static FromRoot(url: string): string {

    const regexp = new RegExp(/^https?:\/\//);
    if (regexp.test(url)) return url;

    if (window.location.protocol == "file:") {

      let ref = window.location.href;
      ref = ref.substring(0, ref.lastIndexOf("/"));
      return `${ref}/${url}`;

    } else {

      url = url === "/" ? "" : url;
      let dir = window.location.pathname;
      dir = dir.indexOf("/") != -1 ? dir.substr(0, dir.lastIndexOf("/") + 1) : "/";
      if (dir.lastIndexOf("/") == dir.length - 1 && url.indexOf("/") == 0) {
        dir.substring(1);
      }

      return `${window.location.protocol}//${window.location.host}${dir}${url}`;
    }

  }
  
}
