<template>
  <cali-html
    v-bind="{selectedItem: selectedItem, dataIndex: dataIndex}"
    :data-cali-ViewItem="selectedItem.ID"
    data-cali-data-type="html"
    class="cali-component"
    :data-cali-index="dataIndex"
  ></cali-html>
</template>

<script>
import { CaliDragDropQuizControls } from "./controls";
import caliHtml from "../CaliHtml/CaliHtml.vue";
import EventBus from "../../Vue.EventBus";

export default {
  components: {
    caliHtml
  },
  name: "cali-drag-drop-quiz",
  props: {
    dataIndex: {
      default: 0,
      type: Number
    },
    selectedItem: {
      default: null,
      type: Object
    }
  },
  data() {
    return {};
  },
  computed: {},
  methods: {},
  created() {
    this.onReady = e => {
      if (
        e.index === this.dataIndex &&
        this.$el.querySelector("[data-california-drag-area]")
      ) {
        this.htmlDragDropQuizProtocol = new CaliDragDropQuizControls(
          this.$el,
          this.selectedItem
        );
      }
    };
  },
  mounted() {
    EventBus.$off("HTMLLoaded", this.onReady);
    EventBus.$on("HTMLLoaded", this.onReady);
  },
  updated() {},
  beforeDestroy() {
    if (this.htmlDragDropQuizProtocol) {
      this.htmlDragDropQuizProtocol.RemoveEvents();
      this.htmlDragDropQuizProtocol.Destroy();
    }
    EventBus.$off("HTMLLoaded", this.onReady);
  }
};
</script>

<style lang="less">
[data-california-quiz-feedback]:not(.active) {
  display: none;
}
</style>
