<template>
  <section class="cali-media cali-video cali-chroma-key-video" ref="content">
    <div class="media-wrapper" ref="wrapper"></div>
    <div class="cali-media-controls">
      <button
        class="cali-media-play"
        v-html="$t('message.MEDIA_PLAY_LABEL', $i18n.locale)"
        @click="play"
      ></button>
      <button
        class="cali-media-pause"
        v-bind:disabled="!autoplay"
        v-html="$t('message.MEDIA_PAUSE_LABEL', $i18n.locale)"
        @click="pause"
      ></button>
      <button
        class="cali-media-restart"
        v-bind:disabled="true"
        v-html="$t('message.MEDIA_RESTART_LABEL', $i18n.locale)"
        @click="restart"
      ></button>
      <div class="cali-media-buffering" v-html="$t('message.MEDIA_BUFFERING_LABEL', $i18n.locale)"></div>
    </div>
  </section>
</template>

<script>
import * as Core from "california_core";
import Vue from "vue";
import { CaliMediaControls } from "../CaliMedia/controls";
import { CaliMediaControlSubtitles } from "../CaliMedia/controls-subtitles";
import { CaliMediaControlKeyframes } from "../CaliMedia/controls-keyframes";
import { Data } from "../../California.Data";
import EventBus from "../../Vue.EventBus";
import { Localization } from "../../California.Localization";
import * as PIXI from "pixi.js";
import ChromaFilter from "./chroma-filter";

export default {
  name: "cali-chroma-key-video",
  props: {
    dataIndex: {
      default: 0,
      type: Number
    },
    dataLength: {
      default: 0,
      type: Number
    },
    selectedItem: {
      default: null,
      type: Object
    }
  },
  data() {
    return {
      subtitles: ""
    };
  },
  methods: {
    startVideoApp(url) {
      this.videoApp = new PIXI.Application(100, 100, {
        transparent: true
      });

      this.canvasBuffer = new PIXI.Application(100, 100, {
        transparent: true,
        forceCanvas: true
      });

      this.$refs.wrapper.appendChild(this.videoApp.view);

      var videoTexture = PIXI.Texture.fromVideo(url + "?" + Math.random());
      this.videoSprite = new PIXI.Sprite(videoTexture);

      this.canvasTexture = PIXI.Texture.fromCanvas(this.canvasBuffer.view);
      this.canvasSprite = new PIXI.Sprite(this.canvasTexture);

      let colorMatrix = new PIXI.filters.ColorMatrixFilter();
      colorMatrix.contrast(0.15);

      this.canvasSprite.filters = [new ChromaFilter(), colorMatrix];
      this.basevideo = videoTexture.baseTexture.source;
      this.basevideo.playbackRate = 1.0;
      if (this.autoplay) {
        this.basevideo.setAttribute("autoplay", "true");
      }
    },

    attachVideo() {
      this.canvasBuffer.stage.addChild(this.videoSprite);
      this.videoApp.stage.addChild(this.canvasSprite);
    },

    initVideo() {
      this.mediaProtocol = new CaliMediaControls();
      this.keyframeController = new CaliMediaControlKeyframes();
      this.RenderMediaFile();
    },

    addQualitySetting(url) {
      const qualitySetting =
        this.$VARIABLES.mediaquality &&
        this.$VARIABLES.mediaquality.value &&
        this.$VARIABLES.mediaquality.value[0];
      const qualityEnd =
        qualitySetting && qualitySetting.length > 0
          ? "_" + this.$VARIABLES.mediaquality.value
          : "";
      const lastPeriod = url.lastIndexOf(".");
      return [url.slice(0, lastPeriod), qualityEnd, url.slice(lastPeriod)].join(
        ""
      );
    },
    mediaSrc(addQuality) {
      let url = Data.ParseDataURL(
        this.selectedItem,
        this.selectedItem.Data[this.dataIndex]
      ).url;
      if (addQuality) url = this.addQualitySetting(url);
      return this.$ContentFolder + url;
    },
    play() {
      if (this.basevideo) this.basevideo.play();
    },
    pause() {
      if (this.basevideo) this.basevideo.pause();
    },
    restart() {
      if (this.basevideo) {
        this.basevideo.pause();
        this.basevideo.currentTime = 0;
        setTimeout(() => {
          this.basevideo.play();
        }, 100);
      }
    },
    updateTexture() {
      requestAnimationFrame(this.updateTexture);
      this.canvasTexture.update();
    },
    firstCanPlay() {
      this.updateTexture();
      if (this.autoplay) {
        this.$el.classList.remove("cali-media-hidden");
        this.$el.classList.remove("cali-media-invisible");
        if (!this.$el.dataset.loaded) this.basevideo.pause();
      } else {
        this.basevideo.pause();
      }
      this.videoAspectRatio =
        this.basevideo.videoHeight / this.basevideo.videoWidth;
      this.keyframeController.Attach(this.$el, this.basevideo);
      this.mediaProtocol.Layout();
      this.$el.dataset.loaded = "true";
      this.basevideo.removeEventListener("canplay", this.firstPlay);
    },
    RenderMediaFile() {
      const url = this.mediaSrc(true);
      const localizedMediaURL = Localization.LocalizeURL(url);
      this.fileReader
        .Exists(localizedMediaURL)
        .then(result => {
          this.$el.classList.add("cali-media-hidden");
          this.startVideoApp(result ? localizedMediaURL : url);
          this.mediaProtocol.Attach(
            this.selectedItem,
            this.$el,
            this.basevideo
          );
          return new Promise(resolve => {
            this.basevideo.addEventListener("canplay", this.firstPlay);
            resolve(this.mediaProtocol.Render(this.mediaSrc()));
          });
        })
        .then(data => {
          if (data.subtitles) {
            CaliMediaControlSubtitles.Render(
              data.subtitles,
              this.$el,
              this.basevideo
            );
          }
          if (data.keyframes) {
            this.keyframeController.Render(
              data.keyframes,
              this.$el,
              this.basevideo
            );
          } else {
            this.$el.classList.remove("cali-media-hidden");
            this.$el.classList.remove("cali-media-invisible");
          }
          this.mediaProtocol.SetControlButtons(this.$el);
        })
        .catch(err => {
          console.log(err);
        });
    },
    resize() {
      if (!this.videoApp) return;
      let w = 0;
      let h = 0;
      if (this.videoAspectRatio <= 1) {
        w = this.$refs.content.clientWidth;
        h = this.$refs.content.clientWidth * this.videoAspectRatio;
        this.videoSprite.width = w;
        this.videoSprite.height = h;
      } else {
        w = this.$refs.content.clientHeight / this.videoAspectRatio;
        h = this.$refs.content.clientHeight;
        this.videoSprite.width = w;
        this.videoSprite.height = h;
      }
      this.videoApp.renderer.resize(w, h);
      this.canvasBuffer.renderer.resize(w, h);
    },
    contentReady() {
      this.attachVideo();
      this.resize();
      this.mediaProtocol.SetControlButtons(this.$el);
    }
  },
  created() {
    this.fileReader = new Core.IO.Reader();
    this.basevideo = null;
    this.videoApp = null;
    this.canvasBuffer = null;
    this.videoSprite = null;
    this.canvasTexture = null;
    this.canvasSprite = null;
    this.videoAspectRatio = 1;
    this.firstPlay = this.firstCanPlay.bind(this);
  },
  mounted() {
    this.initVideo();
    window.addEventListener("resize", this.resize);
    EventBus.$on("ContentReady", this.contentReady);
    EventBus.$on("ToMediaSubPage", this.resize);
    EventBus.$on("showmodal", this.pause);
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.resize);
    EventBus.$off("ContentReady", this.contentReady);
    EventBus.$off("ToMediaSubPage", this.resize);
    EventBus.$off("showmodal", this.pause);
    if (this.videoApp) this.videoApp.destroy(true);
    if (this.canvasBuffer) this.canvasBuffer.destroy(true);
    this.mediaProtocol.Destroy();
    this.keyframeController.Destroy();
    this.mediaProtocol = null;
    this.keyframeController = null;
  },
  destroyed() {},
  computed: {
    autoplay() {
      const auto = this.selectedItem.Settings.MediaAutoPlay || false;
      switch (auto.toString().toLowerCase()) {
        case "false":
          return false;
        case "true":
          return true;
        case "firsttime":
          if (this.selectedItem.TotalChildCount > 0) {
            return (
              this.selectedItem.Progress <
              1 / (this.selectedItem.TotalChildCount + 1)
            );
          }
          return this.selectedItem.Visited === 0;
      }
    }
  }
};
</script>

<style lang="less">
.cali-component.cali-media {
  video {
    width: 0%;
    height: 0%;
  }
  .cali-subtitles {
    width: 100%;
    &.empty {
      display: none;
    }
  }
}
.cali-component.cali-media.cali-media-hidden {
  visibility: hidden;
}
.cali-component.cali-media.cali-media-showing {
  visibility: visible;
}
.cali-component.cali-media.cali-video {
  width: 100%;
  height: 100%;
  video {
    width: 100%;
    height: 100%;
  }
}
</style>
