import { Data } from "../California.Data";
import Vue from "vue";
import { StructureItem, Structure } from "california_core";

export default class Progress {
  private static _flattenTree(n: StructureItem): Array<object> {
    var children = new Array<object>();

    for (let child of n.Children) {
      children.push(child);

      if (child.ChildCount > 0) {
        children = children.concat(this._flattenTree(child));
      }
    }

    return children;
  }

  private static _flattenSuspendTree(n: StructureItem): Array<object> {
    var children = new Array<object>();

    for (let child of n.Children) {
      if (child.Progress > 0 || child.SubProgress > 0) {
        let suspendItem: { [key: string]: any } = {
          ID: child.ID,
          Progress: child.Progress,
          SubProgress: child.SubProgress,
          Visited: child.Visited,
          Score: child.Settings.Score || 0
        };
        if (child.ResumeIndex > -1) {
          suspendItem.ResumeIndex = child.ResumeIndex;
        }
        children.push(suspendItem);
      }
      if (child.ChildCount > 0) {
        children = children.concat(this._flattenSuspendTree(child));
      }
    }

    return children;
  }

  private static _setMaxScore(course: Structure) {
    let maxScore = 0;
    for (let item of this._flattenTree(course.Root)) {
      let structItem = <StructureItem>item;
      maxScore += structItem.Settings.MaxScore || 0;
    }
    Vue.prototype.$SCORM.SetMaxScore(maxScore);
  }

  private static _setScore(course: Structure) {
    let score = 0;
    for (let item of this._flattenTree(course.Root)) {
      let structItem = <StructureItem>item;
      score = structItem.Settings.Score || 0;
      Vue.prototype.$SCORM.AddScore(score.toString(), structItem);
    }
  }

  public static ClearProgress() {
    Vue.prototype.$SCORM.Clear();
  }

  public static SetProgress() {
    let bookmarkToSave;

    if (Data.Course.CurrentItem.Settings.PreventBookmark == true) {
      bookmarkToSave = this.GetBookmark();
    } else {
      bookmarkToSave = Vue.prototype.$CaliforniaApp.$route.path;
      this.BookmarkItem = Data.Course.CurrentItem;
    }

    let progressData = {
      bookmark: bookmarkToSave,
      items: this._flattenSuspendTree(Data.Course.Root)
    };
    Vue.prototype.$SCORM.UpdatePosition(JSON.stringify(progressData));

    //    console.log("---- Set Score -----");

    const currentScore = this.GetTotalScore();

     //    console.log("currentScore: " + currentScore);
     //    console.log("BEST_SCORE: " + Vue.prototype.$SCORM.BEST_SCORE);
     //    console.log("Sparar: " + Math.max(currentScore, Vue.prototype.$SCORM.BEST_SCORE));

    Vue.prototype.$SCORM.SetCourseScore(Math.max(currentScore, Vue.prototype.$SCORM.BEST_SCORE));

     //    console.log("---- Success? -----");
     //    console.log("Ifall " + Vue.prototype.$SCORM.GetCourseScore() + " >= " + this.GetPassingScore());

    if (Vue.prototype.$SCORM.GetCourseScore() >= this.GetPassingScore()) {
       //    console.log("Stt SuccessStatus till 'passed'");
      Vue.prototype.$SCORM.SetCourseSuccessStatus("passed");
       //    console.log("OCH stt CompletionStatus till 'completed'");
      Vue.prototype.$SCORM.SetCourseCompletionStatus("completed");
    }

     //    console.log("---- Completed? -----");
     //    console.log("Ifall " + Data.Course.NavigationBase.Progress + " >= 1");

    if (Data.Course.NavigationBase.Progress >= 1) {
      //    console.log("Stt CompletionStatus till 'completed'");
      Vue.prototype.$SCORM.SetCourseCompletionStatus("completed");
      if (Vue.prototype.$SCORM.GetMaxScore() == 0) {
         //    console.log("Ifall det inte finns pong, stt SuccessStatus till 'passed'");
        Vue.prototype.$SCORM.SetCourseSuccessStatus("passed");
      }
    }

     //    console.log("--------------------");
  }

  public static BookmarkItem: StructureItem;

  public static GetBookmark(): string {
    let suspData = Vue.prototype.$SCORM.SuspendData;
    return (suspData && suspData.bookmark) || "";
  }

  public static ApplyProgress(items: Structure): Structure {
    let suspData = Vue.prototype.$SCORM.SuspendData;
    if (!suspData || !suspData.items) return items;

    for (let suspItem of suspData.items) {
      let courseItem: StructureItem = items.GetItemByID(suspItem.ID);
      courseItem.Progress = suspItem.Progress;
      courseItem.SubProgress = suspItem.SubProgress;
      courseItem.Visited = suspItem.Visited;
      courseItem.Settings.Score = suspItem.Score;
      if (suspItem.ResumeIndex) courseItem.ResumeIndex = suspItem.ResumeIndex;
      courseItem.EvaluateLock();
    }

    return items;
  }

  public static Setup(course: Structure): Structure {
    this.ApplyProgress(course);
    this._setMaxScore(course);
    this._setScore(course);
    return course;
  }

  public static SetScore(item: StructureItem, score: number) {
    item.Settings.Score = Math.max(score, item.Settings.Score);
    Data.Course.SetItem(item);
  }

  public static GetPassingScore(): number {
    const passingScore = Data.Course.Root.Settings.PassingScore || this.GetMaxScore();
    return passingScore;
  }

  public static GetMaxScore(): number {
    return Vue.prototype.$SCORM.GetMaxScore();
  }

  public static GetTotalScore(): number {
    let score: number = 0;
    for (let item of this._flattenTree(Data.Course.Root)) {
      let structItem = <StructureItem>item;
      score += structItem.Settings.Score || 0;
    }
    return score;
  }
}
