<template>
  <section class="cali-xyleme" v-html="htmlContent"></section>
</template>

<script>
import Vue from "vue";
import { CaliXylemeControls } from "./controls";
import { Data } from "../../../California.Data";

export default {
  name: "cali-xyleme",
  props: {
    dataIndex: {
      default: 0,
      type: Number
    },
    selectedItem: {
      default: null,
      type: Object
    }
  },
  data() {
    return {
      htmlContent: ""
    };
  },
  created() {
    this.xmlSrc = () => {
      return (
        this.$ContentFolder +
        Data.ParseDataURL(
          this.selectedItem,
          this.selectedItem.Data[this.dataIndex]
        ).url
      );
    };
    this.xylemeProtocol = new CaliXylemeControls();
  },
  mounted() {
    this.RenderXMLTopic();
  },
  computed: {},
  methods: {
    RenderXMLTopic: function() {
      this.xylemeProtocol.Render(this.xmlSrc()).then(data => {
        this.htmlContent = data;
        this.$el.dataset.loaded = "true";
      });
    }
  },
  watch: {
    selectedItem: function(newVal, oldVal) {
      this.RenderXMLTopic();
    }
  }
};
</script>