<template>
  <ul class="cali-navigation-children">
    <template v-for="child in ParentItem.Children">
      <li
        v-if="!child.Settings.Hidden && !child.Settings.ExcludeFromMenu"
        :key="child.ID"
        :data-progress="child.Progress"
        :data-locked="child.Settings.Locked ? 'true' : 'false' "
      >
        <p v-if="child.Name" v-html="child.Name" class="child-name"></p>
        <p v-if="child.Description" v-html="child.Description" class="child-description"></p>
        <navigation-to-child
          class="cali-navigation-to-child"
          :LinkItem="child"
          :Action="$t('message.START_BUTTON_LABEL', $i18n.locale)"
        />
        <navigation-to-child
          v-if="getResumeList(child).length > 0"
          class="cali-navigation-resume"
          :LinkItem="resumeChild()"
          :Action="$t('message.CONTINUE_BUTTON_LABEL', $i18n.locale)"
        />
      </li>
    </template>
  </ul>
</template>

<script>
import NavigationToChild from "./ToChild";

export default {
  name: "navigation-children",
  props: ["ParentItem"],
  created() {
    this.resumeList = [];
  },
  methods: {
    getResumeList(item) {
      this.deepResume(item);
      return this.resumeList;
    },
    deepResume(item, inloop) {
      if (!inloop) {
        this.resumeList = [];
      }
      for (var i = 0; i < item.Children.length; i++) {
        var child = item.Children[i];
        if (child.Children.length > 0) {
          this.deepResume(child, true);
        }
      }
      if (item.ResumeIndex > -1) {
        this.resumeList.push(item.Children[item.ResumeIndex]);
      }
    },
    resumeChild() {
      let position = -1;
      let resumeItem = null;
      for (let i = 0; i < this.resumeList.length; i++) {
        let item = this.resumeList[i];
        if (item.FlatIndex > position) {
          resumeItem = item;
        }
      }
      return resumeItem;
    }
  },
  components: {
    NavigationToChild
  }
};
</script>

<style lang="less">
.cali-navigation-children {
  p.child-name,
  p.child-description {
    display: none;
  }
}
</style>
