<template>
  <section class="cali-media" ref="content" :class="['cali-' + mediaName]">
    <div class="media-wrapper">
      <video
        preload="auto"
        playsinline
        ref="mediaelement"
        :controls="selectedItem.Settings.ShowMediaControls"
      >
        <source src ref="mediasource" :type="mediatype">
      </video>
    </div>
    <div class="cali-media-controls">
      <button
        class="cali-media-play"
        v-bind:disabled="autoplay"
        v-html="$t('message.MEDIA_PLAY_LABEL', $i18n.locale)"
        @click="play"
      ></button>
      <button
        class="cali-media-pause"
        v-bind:disabled="!autoplay"
        v-html="$t('message.MEDIA_PAUSE_LABEL', $i18n.locale)"
        @click="pause"
      ></button>
      <button
        class="cali-media-restart"
        v-bind:disabled="true"
        v-html="$t('message.MEDIA_RESTART_LABEL', $i18n.locale)"
        @click="restart"
      ></button>
      <div class="cali-media-buffering" v-html="$t('message.MEDIA_BUFFERING_LABEL', $i18n.locale)"></div>
    </div>
  </section>
</template>

<script>
import * as Core from "california_core";
import Vue from "vue";
import { CaliMediaControls } from "./controls";
import { CaliMediaControlSubtitles } from "./controls-subtitles";
import { CaliMediaControlKeyframes } from "./controls-keyframes";
import { Data } from "../../California.Data";
import EventBus from "../../Vue.EventBus";
import { Localization } from "../../California.Localization";

export default {
  name: "cali-media",
  props: {
    dataIndex: {
      default: 0,
      type: Number
    },
    selectedItem: {
      default: null,
      type: Object
    }
  },
  data() {
    return {
      subtitles: ""
    };
  },
  methods: {
    initVideo() {
      this.mediaProtocol = new CaliMediaControls();
      this.keyframeController = new CaliMediaControlKeyframes();
      this.RenderMediaFile();
    },
    addQualitySetting(url) {
      const qualitySetting =
        this.$VARIABLES.mediaquality &&
        this.$VARIABLES.mediaquality.value &&
        this.$VARIABLES.mediaquality.value[0];
      const qualityEnd =
        qualitySetting && qualitySetting.length > 0
          ? "_" + this.$VARIABLES.mediaquality.value
          : "";
      const lastPeriod = url.lastIndexOf(".");
      return [url.slice(0, lastPeriod), qualityEnd, url.slice(lastPeriod)].join(
        ""
      );
    },
    mediaSrc() {
      let url = Data.ParseDataURL(
        this.selectedItem,
        this.selectedItem.Data[this.dataIndex]
      ).url;
      return this.$ContentFolder + url;
    },
    play() {
      if (this.$refs.mediaelement) this.$refs.mediaelement.play();
    },
    pause() {
      if (this.$refs.mediaelement) this.$refs.mediaelement.pause();
    },
    restart() {
      if (this.$refs.mediaelement) {
        this.$refs.mediaelement.pause();
        this.$refs.mediaelement.currentTime = 0;
        this.$refs.mediaelement.play();
      }
    },
    RenderMediaFile() {
      this.mediaProtocol.Attach(
        this.selectedItem,
        this.$el,
        this.$refs.mediaelement
      );
      this.keyframeController.Attach(this.$el, this.$refs.mediaelement);
      let finalURL = this.mediaSrc();
      let qualityMediaURL = this.addQualitySetting(finalURL);
      let localizedMediaURL = null;

      this.fileReader
        .Exists(qualityMediaURL)
        .then(result => {
          finalURL = result ? qualityMediaURL : finalURL;
          localizedMediaURL = Localization.LocalizeURL(finalURL);
          return this.fileReader.Exists(localizedMediaURL);
        })
        .then(result => {
          finalURL = result ? localizedMediaURL : finalURL;
          this.$el.classList.add("cali-media-hidden");
          this.$refs.mediasource.src = finalURL + "?" + Math.random();
          this.$refs.mediaelement.load();
          return this.mediaProtocol.Render(this.mediaSrc());
        })
        .then(data => {
          if (data.subtitles) {
            CaliMediaControlSubtitles.Render(
              data.subtitles,
              this.$el,
              this.$refs.mediaelement
            );
          }
          if (data.keyframes) {
            this.keyframeController.Render(
              data.keyframes,
              this.$el,
              this.$refs.mediaelement
            );
          }
          this.$el.classList.remove("cali-media-hidden");
          this.$el.classList.remove("cali-media-invisible");
          if (this.autoplay) {
            this.$refs.mediaelement.setAttribute("autoplay", "autoplay");
            this.$refs.mediaelement.play();
          } else if (data.keyframes) {
            this.keyframeController.GotoKeyframe(0);
          }
          this.mediaProtocol.SetControlButtons(this.$el);
        });
    }
  },
  created() {
    this.fileReader = new Core.IO.Reader();
  },
  mounted() {
    this.initVideo();
  },
  beforeDestroy() {
    this.mediaProtocol.Destroy();
    this.keyframeController.Destroy();
    this.mediaProtocol = null;
    this.keyframeController = null;
  },
  computed: {
    mediatype() {
      return this.mediaSrc().endsWith(".mp3") ? "audio/mp3" : "video/mp4";
    },
    mediaName() {
      return this.mediaSrc().endsWith(".mp3") ? "audio" : "video";
    },
    autoplay() {
      const auto = this.selectedItem.Settings.MediaAutoPlay || false;
      switch (auto.toString().toLowerCase()) {
        case "false":
          return false;
        case "true":
          return "true";
        case "firsttime":
          if (this.selectedItem.TotalChildCount > 0) {
            return (
              this.selectedItem.Progress <
              1 / (this.selectedItem.TotalChildCount + 1)
            );
          }
          return this.selectedItem.Progress === 0;
      }
    }
  }
};
</script>

<style lang="less">
.cali-component.cali-media {
  video {
    width: 0%;
    height: 0%;
  }
  .cali-subtitles {
    width: 100%;
    &.empty {
      display: none;
    }
  }
}
.cali-component.cali-media.cali-media-hidden {
  display: none;
}
.cali-component.cali-media.cali-media-showing {
  display: block;
}
.cali-component.cali-media.cali-video {
  width: 100%;
  height: 100%;
  video {
    width: 100%;
    height: 100%;
  }
}

.cali-component.cali-media.cali-audio {
  width: 0 !important;
  height: 0 !important;
  video {
    width: 0 !important;
    height: 0 !important;
  }
}
</style>
