import * as Core from "california_core";
import { ContentResize, ContentResizeModes } from "../../California.ContentSize";

export class CaliMediaControlLayout {
  private static resizeToFixed(item: Core.StructureItem, element: HTMLElement) {
    let size = item.Settings.Size;

    if (window.innerWidth < 769 && item.Settings.SmallSize) {
      size = item.Settings.SmallSize;
    }

    let index = parseInt(element.dataset.caliIndex || "", 10);
    if (!isNaN(index)) size = ContentResize.GetPropForDataIndex(size, index);
    element.style.width = size.width;
    element.style.height = size.height;
  }

  private static resizeToContent(element: HTMLElement, mediaElement: HTMLVideoElement) {
    element.style.width = mediaElement.videoWidth + "px";
    element.style.height = mediaElement.videoHeight + "px";
  }

  private static resizeToParent(element: HTMLElement, parent: HTMLElement) {
    element.style.width = parent.offsetWidth + "px";
    element.style.height = parent.offsetHeight + "px";
  }

  public static Resize(
    item: Core.StructureItem,
    element: HTMLElement,
    mediaElement: HTMLVideoElement
  ) {
    const mode = item.Settings.ResizeMode ? item.Settings.ResizeMode.toLowerCase() : ContentResizeModes.Parent;
    switch (mode) {
      case ContentResizeModes.Content:
        this.resizeToContent(element, mediaElement);
        break;
      case ContentResizeModes.Parent:
        if (element.parentElement) this.resizeToParent(element, element.parentElement);
        break;
      case ContentResizeModes.Fixed:
        this.resizeToFixed(item, element);
        break;
      default:
        this.resizeToContent(element, mediaElement);
    }
  }

  public static Place(
    item: Core.StructureItem,
    element: HTMLElement,
    mediaElement: HTMLVideoElement
  ) {
    const index = parseInt(element.dataset.caliIndex || "", 10);
    let positionSetting = item.Settings.Position;
    if (window.innerWidth < 769 && item.Settings.SmallPosition) {
      positionSetting = item.Settings.SmallPosition;
    }
    const position = !isNaN(index)
      ? ContentResize.GetPropForDataIndex(positionSetting, index)
      : null;
      
    if (position && !element.classList.contains("cali-audio")) {
      element.style.position = "absolute";
      element.style.top = position.y;
      element.style.left = position.x;
    }
  }
}
