import * as Core from "california_core";
import EventBus from "../Vue.EventBus";
import * as Startup from "../App.startup";
import Vue from "vue";

declare var __California_Options__: any;
(<any>window).California = {
  Core: Core,
  Events: EventBus,
  Course: {},
  ExternalCommand: function() {}
};

let contentFolder: string = Core.Net.Path.FromRoot("");


function parseQueryObject(): Object {
  const keyValues = window.location.search.substring(1).split("&");
  let returnObj: any = {};

  try {
    for (let i = 0; i < keyValues.length; i++) {
      let keyValue = keyValues[i];
      if (keyValue === "") continue;
      let keyValueParts = keyValue.split("=");
      let key = decodeURIComponent(keyValueParts[0]);
      if (key.toLowerCase() == "settings") {
        returnObj = JSON.parse(decodeURIComponent(keyValueParts[1]));
      }
    }
  } catch (er) {
    console.log(er);
    returnObj = {};
  }
  return returnObj;
}

Vue.prototype.$VARIABLES = {};
let querySettings = parseQueryObject();

if (Object.keys(querySettings).length > 0) {
  Vue.prototype.$VARIABLES = Object.assign({}, querySettings);
  new Startup.Content(contentFolder, `${contentFolder}courseJSON.json`);
} else {
  new Startup.PreSettings(contentFolder, `${contentFolder}languages.json`)
    .Load()
    .then((data: JSON) => {
      if (Object.keys(data).length > 0) {
        Vue.prototype.$VARIABLES = Object.assign(Vue.prototype.$VARIABLES, data);
        new Startup.Settings();
        return Promise.reject("Choose language before settings");
      } else {
        return new Startup.PreSettings(contentFolder, `${contentFolder}variables.json`).Load();
      }
    })
    .then((data: JSON) => {
      if (Object.keys(data).length > 0) {
        Vue.prototype.$VARIABLES = Object.assign(Vue.prototype.$VARIABLES, data);
        new Startup.Settings();
      } else {
        const courseLang = Vue.prototype.$VARIABLES.Language
          ? "_" + Vue.prototype.$VARIABLES.Language
          : "";
        const courseFile = `${contentFolder}courseJSON.json`;
        
        const localizedCourseFile = `${contentFolder}courseJSON${courseLang}.json`;
        new Core.IO.Reader().Exists(localizedCourseFile).then((result: boolean) => {
          new Startup.Content(contentFolder, result ? localizedCourseFile : courseFile);
        });
      }
    })
    .catch((reason: any) => {
      console.log(reason);
    });
}
