import * as Core from "california_core";
import { CaliDragDropQuizControlsDragDrop } from "./controls-drag-drop";
import EventBus from "../../Vue.EventBus";
import Vue from "vue";
import Progress from "../../California.Progress";

export class CaliDragDropQuizControls {
  private item: Core.StructureItem;
  private element: HTMLElement;
  private score: number = 1;
  private DragDropCore: CaliDragDropQuizControlsDragDrop;
  private sounds: Array<HTMLAudioElement>;

  private showAnswerButtons: Array<HTMLElement>;
  private tryAgainButtons: Array<HTMLElement>;
  private submitButtons: Array<HTMLElement>;
  private correctFeedback: Array<HTMLElement>;
  private incorrectFeedback: Array<HTMLElement>;
  private standardFeedback: Array<HTMLElement>;
  private allFeedback: Array<HTMLElement>;
  private defaultFeedback: Array<HTMLElement>;
  private numberOfTries: number = 0;

  private ondragDropQuizDrop: Function;
  private onShowAnswerClick: any;
  private onSubmitClick: any;
  private onTryAgainClick: any;

  private showMeMode: boolean = false;

  constructor(element: HTMLElement, item: Core.StructureItem) {
    this.element = element;
    this.item = item;
    this.DragDropCore = new CaliDragDropQuizControlsDragDrop(element);
    this.score = this.collectScore();
    this.sounds = Array.from(document.querySelectorAll("audio"));
    this.sounds = this.sounds.concat(Array.from(document.querySelectorAll("video")));
    this.numberOfTries = 0;

    this.correctFeedback = Array.from(
      this.element.querySelectorAll(`[data-california-quiz-feedback="correct"]`)
    );
    this.incorrectFeedback = Array.from(
      this.element.querySelectorAll(`[data-california-quiz-feedback="incorrect"]`)
    );
    this.allFeedback = this.correctFeedback.concat(this.incorrectFeedback);
    this.defaultFeedback = Array.from(
      this.element.querySelectorAll(`[data-california-quiz-feedback="default"]`)
    );
    this.defaultFeedback.forEach(el => {
      el.classList.add("active");
    });
    this.standardFeedback = Array.from(
      this.element.querySelectorAll(`[data-california-quiz-feedback="standard"]`)
    );

    this.showAnswerButtons = Array.from(
      this.element.querySelectorAll("[data-california-quiz-show-correct]")
    );
    this.tryAgainButtons = Array.from(
      this.element.querySelectorAll("[data-california-quiz-retry]")
    );
    this.submitButtons = Array.from(this.element.querySelectorAll("[data-california-quiz-submit]"));

    this.onSubmitClick = this.submitClickHandler.bind(this);
    this.ondragDropQuizDrop = this.dropHandler.bind(this);
    this.onShowAnswerClick = this.showAnswerClickHandler.bind(this);
    this.onTryAgainClick = this.tryAgainClickHandler.bind(this);

    this.toggleSubmit(this.DragDropCore.CanSubmit());
    this.attachEvents();
  }

  private stopAllSounds() {
    this.sounds.forEach((sound: HTMLAudioElement) => {
      sound.pause();
      sound.currentTime = 0;
    });
  }

  private collectScore(): number {
    const scoreElements = Array.from(this.element.querySelectorAll(`[data-california-quiz-score]`));
    let score = 0;
    if (scoreElements.length < 1) return 1;
    scoreElements.forEach(el => {
      score += parseFloat(el.getAttribute("data-california-quiz-score") || "0");
    });
    return score;
  }

  private setAsCorrect() {
    this.item.Settings.Score = this.score;
    Vue.prototype.$CaliforniaCourse.SetItem(this.item);
    Progress.SetProgress();
  }

  private displayCorrect() {
    this.stopAllSounds();

    this.defaultFeedback.forEach(el => {
      el.classList.remove("active");
    });

    this.incorrectFeedback.forEach(el => {
      el.classList.remove("active");
    });

    let evt: any = null;

    this.correctFeedback.forEach(el => {
      el.classList.add("active");
      if (el.nodeName == "AUDIO") {
        (el as HTMLAudioElement).currentTime = 0;
        (el as HTMLAudioElement).play();
      }
      evt = evt || el.dataset.californiaNavigation;
    });

    this.standardFeedback.forEach(el => {
      el.classList.add("active");
      if (el.nodeName == "AUDIO") {
        (el as HTMLAudioElement).currentTime = 0;
        (el as HTMLAudioElement).play();
      }
      evt = evt || el.dataset.californiaNavigation;
    });

    if (evt) {
      const commParts: any = evt.split("|");
      Vue.prototype.$Navigation.FromExternalCommand(commParts[0], commParts[1]);
    }

    this.setAsCorrect();

    const nextButtons: Array<HTMLElement> = Array.from(
      document.querySelectorAll(".cali-button.next")
    );

    nextButtons.forEach(el => {
      el.classList.add("hint");
      let durationVal = window.getComputedStyle(el).animationDuration || "0";
      let duration = parseFloat(durationVal) * 1000;
      setTimeout(() => {
        el.classList.remove("hint");
      }, duration);
    });
  }

  private selectedFeedbackIndex: number = -1;

  private displayInCorrect() {
    this.stopAllSounds();

    this.numberOfTries += 1;

    this.defaultFeedback.forEach(el => {
      el.classList.remove("active");
    });

    this.correctFeedback.forEach(el => {
      el.classList.remove("active");
    });

    let evt: any = null;

    this.incorrectFeedback.forEach(el => {
      let tryIndex = parseInt(el.dataset.californiaQuizFeedbackAfterTry || "", 10);
      tryIndex = isNaN(tryIndex) ? 0 : tryIndex;
      if (tryIndex >= this.numberOfTries) return;

      const elIndex: number = parseInt(el.dataset.californiaQuizFeedbackIndex || "", 10);
      if (isNaN(elIndex) || (!isNaN(elIndex) && elIndex == this.selectedFeedbackIndex)) {
        el.classList.add("active");
        if (el.nodeName == "AUDIO") {
          (el as HTMLAudioElement).currentTime = 0;
          (el as HTMLAudioElement).play();
        }
      }
      if (el.dataset.autoCorrect == "true") {
        this.autoCorrect(el.dataset.autoCorrectDelay || "0");
      }
      evt = evt || el.dataset.californiaNavigation;
    });

    this.standardFeedback.forEach(el => {
      let tryIndex = parseInt(el.dataset.californiaQuizFeedbackAfterTry || "", 10);
      tryIndex = isNaN(tryIndex) ? 0 : tryIndex;
      if (tryIndex >= this.numberOfTries) return;

      el.classList.add("active");
      if (el.dataset.autoCorrect == "true") {
        this.autoCorrect(el.dataset.autoCorrectDelay || "0");
      }
      if (el.nodeName == "AUDIO") {
        (el as HTMLAudioElement).currentTime = 0;
        (el as HTMLAudioElement).play();
      }
      evt = evt || el.dataset.californiaNavigation;
    });

    if (evt) {
      const commParts: any = evt.split("|");
      Vue.prototype.$Navigation.FromExternalCommand(commParts[0], commParts[1]);
    }
  }

  private dropHandler() {
    if (this.DragDropCore.CanSubmit() && this.DragDropCore.AutoSubmit) {
      this.onSubmitClick();
    } else {
      this.toggleSubmit(this.DragDropCore.CanSubmit());
    }
  }

  private showAnswerClickHandler() {
    this.showMeMode = true;
    this.toggleShowAnswer();
    this.DragDropCore.ShowCorrectAnswer.bind(this.DragDropCore)();
  }

  private tryAgainClickHandler(e: any) {
    this.stopAllSounds();

    this.allFeedback.forEach(el => {
      el.classList.remove("active");
    });

    this.standardFeedback.forEach(el => {
      el.classList.remove("active");
    });

    this.defaultFeedback.forEach(el => {
      el.classList.add("active");
    });
    this.DragDropCore.Reset();
    this.showMeMode = false;
    this.toggleShowAnswer();
    this.DragDropCore.Enable();
  }

  private autoCorrect(delay: string) {
    this.setAsCorrect();
    setTimeout(() => {
      this.onShowAnswerClick();
    }, parseFloat(delay) * 1000);
  }

  private submitClickHandler() {
    if (this.DragDropCore.IsCorrect()) {
      this.displayCorrect();
    } else {
      this.displayInCorrect();
    }
    this.DragDropCore.Disable();
    this.toggleSubmit(false);
  }

  private toggleSubmit(onoff: boolean = false) {
    this.submitButtons.forEach((btn: HTMLElement) => {
      if (onoff && !this.showMeMode) {
        btn.classList.remove("disabled");
        btn.removeAttribute("disabled");
      } else {
        btn.classList.add("disabled");
        btn.setAttribute("disabled", "disabled");
      }
    });
  }

  private toggleShowAnswer(onoff: boolean = false) {
    this.showAnswerButtons.forEach((btn: HTMLElement) => {
      if (onoff || !this.showMeMode) {
        btn.classList.remove("disabled");
        btn.removeAttribute("disabled");
      } else {
        btn.classList.add("disabled");
        btn.setAttribute("disabled", "disabled");
      }
    });
  }

  private attachEvents() {
    this.submitButtons.forEach((el: HTMLElement) => {
      el.addEventListener("click", this.onSubmitClick);
    });

    this.showAnswerButtons.forEach((el: HTMLElement) => {
      el.addEventListener("click", this.onShowAnswerClick);
    });

    this.tryAgainButtons.forEach((el: HTMLElement) => {
      el.addEventListener("click", this.onTryAgainClick);
    });

    EventBus.$off("dragDropQuizDrop", this.ondragDropQuizDrop);
    EventBus.$on("dragDropQuizDrop", this.ondragDropQuizDrop);
  }

  public RemoveEvents() {}

  public Destroy() {
    EventBus.$off("dragDropQuizDrop", this.ondragDropQuizDrop);
    this.DragDropCore.Destroy();
  }
}
