import * as Core from "california_core";
import Vue from "vue";
import { Data } from "../../California.Data";
import Router from "vue-router";
import SlideHome from "../../Vue.Components/SlideShow/Screens/Home.vue";
import Slide404 from "../../Vue.Components/SlideShow/Screens/404.vue";
import SlideCollection from "../../Vue.Components/SlideShow/Screens/Collection.vue";
import SlideItem from "../../Vue.Components/SlideShow/Screens/Item.vue";
import EventBus from "../../Vue.EventBus";

export class RouterConfig {
  public Create(): Router {
    Vue.use(Router);

    let slideshowRouter = new Router({
      linkActiveClass: "",
      linkExactActiveClass: "",
      routes: [
        {
          path: "/home/:command?",
          name: "home",
          component: SlideHome,
          alias: "/"
        },
        {
          path: "/404",
          name: "404",
          component: Slide404,
          alias: "/"
        },
        {
          path: "/collection/:id",
          name: "collection",
          component: SlideCollection
        },
        {
          path: "/item/:id",
          name: "item",
          component: SlideItem
        }
      ]
    });

    // const waitForPreloader = function(duration: number): Promise<boolean> {
    //   return new Promise(resolve => {
    //     setTimeout(() => {
    //       resolve(true);
    //     }, duration);
    //   });
    // };

    slideshowRouter.beforeEach((to, from, next) => {
      let navObj: object | undefined;
      if (to.params.id) {
        let item = Data.Course.GetItemByID(to.params.id);
        if (item && item.Settings && item.Settings.Redirect) {
          item = Data.Course.GetItemByID(item.Settings.Redirect);
          if (item) {
            navObj = {
              name: item.ChildCount > 0 ? "collection" : "item",
              params: { id: item.ID }
            };
          }
        }
      }
      EventBus.$emit("NavigationStarted");
      next(navObj);
    });

    slideshowRouter.afterEach(() => {
      EventBus.$emit("NavigationDone", Vue.prototype.$CaliforniaCourse.CurrentItem);
    });

    return slideshowRouter;
  }
}
