import * as Core from 'california_core'
import Vue from 'vue';
declare var __California_Options__: any;


export class Data {

  private static model = new Core.Application();

  private static framesLoaded = (elem: HTMLElement, selector: string, loadedSelector: string) => {
    return (
      elem.querySelectorAll(loadedSelector).length >=
      elem.querySelectorAll(selector).length
    );
  };

  public static Course: Core.Structure;

  public static LoadCourse(url: string, filter: Array<string> = []): Promise<object> {

    return new Promise<object>((resolve, reject) => {

      this.model.LoadStructure(url, filter)
        .then(() => {
          this.Course = this.model.Course
          resolve()
        })
        .catch((reason: any) => {
          throw new Error(reason)
        });

    })

  }

  public static LoadComponentFolder(ctx: __WebpackModuleApi.RequireContext): { [k: string]: any } {

    let Components: { [k: string]: any } = {}
    ctx.keys().forEach((key?: string) => { // see: https://webpack.github.io/docs/context.html#context-module-api
      key = key || "";
      let componentName = key.match(/\/([^\/]+?)\.vue$/) || []
      Components[componentName[1]] = ctx(key).default
    })
    return Components;
  }

  public static ParseDataURL(item: Core.StructureItem, url: string = ""): object {
    let urlObj = {
      contenttype: "cali-" + item.ContentType,
      url: url
    }
    if (url.indexOf(":") != -1) {
      let parts = url.split(":")
      urlObj.contenttype = "cali-" + parts[0]
      urlObj.url = parts[1]
    }
    return urlObj;
  }

  public static PreloadNodes(elem: HTMLElement, selector: string, loadedSelector: string, callback: Function) {
    let loadTimer = setInterval(() => {
      if (this.framesLoaded(elem, selector, loadedSelector)) {
        clearInterval(loadTimer);
        callback();
      }
    }, 5);
  }

}

