import { StructureItem } from "../Data";

export class NavigationResult{
    public Result : ResultCode;
    public Item: StructureItem

    constructor(result : ResultCode, item: StructureItem = null){
        this.Result = result;
        this.Item = item;
    }

}
export enum ResultCode {

    OK = 'OK',
    NotFound = 'NotFound',
    Locked = 'Locked'

}