import EventBus from "../../Vue.EventBus";

export class CaliDragDropQuizControlsDragDrop {
  private element: HTMLElement;

  private dragArea: HTMLElement;
  private draggableElements: Array<HTMLElement> = [];
  private dropTargets: Array<HTMLElement> = [];
  private dropAnswerTargets: Array<HTMLElement> = [];
  private dragGhost: HTMLElement;
  private draggedItem: HTMLElement | null = null;
  private currentlyOver: HTMLElement | null = null;
  private onDragStart: any;
  private onDrag: any;
  private onDragEnd: any;
  public AutoSubmit: boolean = false;

  constructor(element: HTMLElement) {
    this.element = element;
    this.dragArea = this.element.querySelector("[data-california-drag-area]") as HTMLElement;
    this.AutoSubmit = this.dragArea.dataset.californiaAutoSubmit == "true" || false;

    this.dragArea.dataset.californiaDragDropQuizArea = "true";
    this.dropAnswerTargets = Array.from(
      this.dragArea.querySelectorAll("[data-california-drop-target]")
    );

    this.draggableElements = Array.from(
      this.element.querySelectorAll("[data-california-drag-item]")
    );
    this.draggableElements.forEach((el: HTMLElement) => {
      this.createHomeContianer(el);
    });
    this.dropTargets = Array.from(this.dragArea.querySelectorAll("[data-california-drop-target]"));

    this.dragGhost = this.createGhost();

    this.onDragStart = this.dragStart.bind(this);
    this.onDrag = this.areaDrag.bind(this);
    this.onDragEnd = this.areaDragEnd.bind(this);

    this.attachEvents();
  }

  private returnHome(
    el: HTMLElement,
    targetElement: HTMLElement | null = null,
    x: number | null = null,
    y: number | null = null,
    speed: number = 0.8
  ) {
    const targetEl: HTMLElement =
      targetElement ||
      (this.dragArea.querySelector("#drag-home-" + el.dataset.californiaDragItem) as HTMLElement);
    if (!targetEl) return;

    const occupant: HTMLElement = targetEl.querySelector(
      "[data-california-drag-item]"
    ) as HTMLElement;
    if (occupant && !occupant.classList.contains("dragging") && occupant != el && !targetElement) {
      this.returnHome(occupant, null, x, y, speed);
    }

    const ghost = this.createGhost();
    ghost.innerHTML = el.innerHTML;
    ghost.style.width = el.scrollWidth + "px";
    ghost.style.height = el.scrollHeight + "px";

    const startPos = this.totalOffset(el);
    ghost.style.top = (y === null ? startPos.y : y) + "px";
    ghost.style.left = (x === null ? startPos.x : x) + "px";
    ghost.classList.add("active");
    ghost.classList.add("animated");

    this.dragArea.appendChild(ghost);

    el.style.display = "none";

    const targetX = this.totalOffset(targetEl).x;
    const targetY = this.totalOffset(targetEl).y;

    const homeAnimation = window.setInterval(() => {
      let xDiff = (targetX - ghost.offsetLeft) * speed;
      let yDiff = (targetY - ghost.offsetTop) * speed;
      ghost.style.top = ghost.offsetTop + yDiff + "px";
      ghost.style.left = ghost.offsetLeft + xDiff + "px";

      xDiff = targetX - ghost.offsetLeft;
      yDiff = targetY - ghost.offsetTop;

      const dist = Math.abs(xDiff) * Math.abs(xDiff) + Math.abs(yDiff) * Math.abs(yDiff);

      if (dist < 10) {
        window.clearInterval(homeAnimation);
        targetEl.appendChild(el);
        el.style.display = "";
        targetEl.classList.remove("taken");
        if (ghost.parentElement) {
          ghost.parentElement.removeChild(ghost);
        }
        EventBus.$emit("dragDropQuizDrop");
      }
    }, 30);
  }

  private createHomeContianer(el: HTMLElement) {
    if (!el.parentElement) return;
    const homeContainer = document.createElement("div");
    homeContainer.setAttribute("id", "drag-home-" + el.dataset.californiaDragItem);
    homeContainer.classList.add("drag-item-placeholder");
    homeContainer.dataset.californiaDropTarget = "home-" + el.dataset.californiaDragItem;
    el.parentElement.insertBefore(homeContainer, el);
    homeContainer.appendChild(el);
  }

  private createGhost() {
    const ghost: HTMLElement = document.createElement("div");
    ghost.classList.add("drag-ghost-item");
    ghost.style.position = "absolute";
    ghost.style.zIndex = "9999";
    this.dragArea.appendChild(ghost);
    return ghost;
  }

  private totalOffset(item: HTMLElement): { x: number; y: number } {
    let x: number = item.offsetLeft;
    let y: number = item.offsetTop;

    if (item === this.dragArea) {
      return {
        x: x,
        y: y
      };
    }

    let parent = item.parentElement;
    while (parent && parent != this.dragArea) {
      x += parent.offsetLeft;
      y += parent.offsetTop;
      parent = parent.parentElement;
    }

    return {
      x: x,
      y: y
    };
  }

  private placeGhost(item: HTMLElement, parent: HTMLElement, e: any) {
    let offset: { x: number; y: number } = { x: 0, y: 0 };
    if (parent !== this.dragArea) {
      offset = this.totalOffset(parent);
    }
    const mouseOffset: { x: number; y: number } = {
      x: parseFloat(item.dataset.mouseOffsetX || "0"),
      y: parseFloat(item.dataset.mouseOffsetY || "0")
    };

    let ex = e.offsetX;
    let ey = e.offsetY;

    if (e.touches) {
      var rect = e.target.getBoundingClientRect();
      ex = e.targetTouches[0].pageX - rect.left;
      ey = e.targetTouches[0].pageY - rect.top;
    }

    item.style.top = ey + offset.y - mouseOffset.y + "px";
    item.style.left = ex + offset.x - mouseOffset.x + "px";
  }

  private findTarget(): HTMLElement | null {
    let dropTarget = null;
    let highestOverlap = 0;
    this.dropTargets.forEach((el: HTMLElement) => {
      const elOffset = this.totalOffset(el);
      const elMaxX = elOffset.x + el.scrollWidth;
      const elMaxY = elOffset.y + el.scrollHeight;
      const ghostMaxX = this.dragGhost.offsetLeft + this.dragGhost.scrollWidth;
      const ghostMaxY = this.dragGhost.offsetTop + this.dragGhost.scrollHeight;
      const x_overlap = Math.max(
        0,
        Math.min(elMaxX, ghostMaxX) - Math.max(elOffset.x, this.dragGhost.offsetLeft)
      );
      const y_overlap = Math.max(
        0,
        Math.min(elMaxY, ghostMaxY) - Math.max(elOffset.y, this.dragGhost.offsetTop)
      );
      const overlap = x_overlap * y_overlap;
      if (overlap > highestOverlap) {
        dropTarget = el;
        highestOverlap = overlap;
      }
    });

    return dropTarget;
  }

  private clearDropTargets() {
    this.dropTargets.forEach((el: HTMLElement) => {
      el.classList.remove("drag-item-over");
    });
  }

  private dragStart(e: any) {
    let item: HTMLElement | null = e.target as HTMLElement;

    while (item && !item.dataset.californiaDragItem) {
      item = item.parentElement;
    }

    if (!item) return;
    if (item.classList.contains("disabled")) return;

    e.preventDefault();

    this.dragGhost.innerHTML = item.innerHTML;
    this.dragGhost.style.width = item.scrollWidth + "px";
    this.dragGhost.style.height = item.scrollHeight + "px";

    let ex = e.offsetX;
    let ey = e.offsetY;

    if (e.touches) {
      var rect = e.target.getBoundingClientRect();
      ex = e.targetTouches[0].pageX - rect.left;
      ey = e.targetTouches[0].pageY - rect.top;
    }

    this.dragGhost.dataset.mouseOffsetX = ex.toString();
    this.dragGhost.dataset.mouseOffsetY = ey.toString();
    this.placeGhost(this.dragGhost, item, e);
    this.dragGhost.classList.add("active");
    item.classList.add("dragging");
    this.draggedItem = item;
  }

  private areaDrag(e: MouseEvent) {
    if (!this.dragGhost.classList.contains("active")) return;
    e.preventDefault();
    const target = e.target as HTMLElement;
    this.placeGhost(this.dragGhost, target, e);
    this.clearDropTargets();
    const dropTarget = this.findTarget();
    if (dropTarget) {
      dropTarget.classList.add("drag-item-over");
      this.currentlyOver = dropTarget;
    }
  }

  private areaDragEnd(e: MouseEvent) {
    if (!this.dragGhost.classList.contains("active")) return;

    this.dragGhost.classList.remove("active");
    if (this.currentlyOver && this.draggedItem) {
      if (
        this.currentlyOver.dataset.californiaDropBounceIncorrect &&
        !this.containsCorrect(this.currentlyOver, this.draggedItem)
      ) {
        const offset = this.totalOffset(this.currentlyOver);
        this.returnHome(this.draggedItem, null, offset.x, offset.y);
        return;
      }

      const occupant: HTMLElement = this.currentlyOver.querySelector(
        "[data-california-drag-item]"
      ) as HTMLElement;
      if (
        occupant &&
        "home-" + occupant.dataset.californiaDragItem ==
          this.currentlyOver.dataset.californiaDropTarget
      ) {
        const offset = this.totalOffset(this.currentlyOver);
        this.returnHome(this.draggedItem, null, offset.x, offset.y);
      } else if (occupant) {
        this.returnHome(occupant);
        this.currentlyOver.appendChild(this.draggedItem);
      } else {
        this.currentlyOver.appendChild(this.draggedItem);
      }
    }
    this.currentlyOver = null;
    this.clearDropTargets();
    if (this.draggedItem) {
      this.draggedItem.classList.remove("dragging");
      this.draggedItem = null;
    }

    EventBus.$emit("dragDropQuizDrop");
  }

  private findAHome(el: HTMLElement): HTMLElement | null {
    for (let i = 0; i < this.dropTargets.length; i++) {
      const target = this.dropTargets[i];
      const corrects: Array<string> = (target.dataset.californiaDropTarget || "").split(",");
      const occupant: HTMLElement = target.querySelector(".drag-alternative") as HTMLElement;
      if (
        !target.classList.contains("taken") &&
        corrects.indexOf(el.dataset.californiaDragItem || "null") != -1 &&
        (occupant && corrects.indexOf(occupant.dataset.californiaDragItem || "null") == -1)
      ) {
        target.classList.add("taken");
        return target;
      }
    }
    return null;
  }

  private attachEvents() {
    this.draggableElements.forEach((el: HTMLElement) => {
      el.addEventListener("touchstart", this.onDragStart);
      el.addEventListener("mousedown", this.onDragStart);
    });

    this.dragArea.addEventListener("touchmove", this.onDrag);
    this.dragArea.addEventListener("mousemove", this.onDrag);

    document.addEventListener("touchend", this.onDragEnd);
    document.addEventListener("mouseup", this.onDragEnd);
  }

  public Reset() {
    this.dropAnswerTargets.forEach((el: HTMLElement) => {
      const droppedItem: HTMLElement = el.querySelector(
        "[data-california-drag-item]"
      ) as HTMLElement;
      if (droppedItem && !this.containsCorrect(el)) {
        this.returnHome(droppedItem);
      }
    });
  }

  public ShowCorrectAnswer() {
    let correctPlacements: Array<HTMLElement | null> = [];
    this.draggableElements.forEach((el: HTMLElement, index: number) => {
      if (this.placedCorrect(el)) return;
      const home = this.findAHome(el);
      correctPlacements[index] = home || null;
    });
    for (let i = 0; i < correctPlacements.length; i++) {
      const home = correctPlacements[i];
      if (home) {
        this.returnHome(this.draggableElements[i], home, null, null, 0.2);
      }
    }
  }

  public CanSubmit(): boolean {
    let droppedNum: number = 0;
    this.dropAnswerTargets.forEach((el: HTMLElement) => {
      const hasDropItem = el.querySelector("[data-california-drag-item]");
      if (hasDropItem) {
        droppedNum += 1;
      }
    });

    return droppedNum === this.dropAnswerTargets.length;
  }

  public Disable() {
    this.draggableElements.forEach((el: HTMLElement) => {
      el.classList.add("disabled");
    });
  }

  public Enable() {
    this.draggableElements.forEach((el: HTMLElement) => {
      el.classList.remove("disabled");
    });
  }

  private containsCorrect(target: HTMLElement, dropped: HTMLElement | null = null): boolean {
    const droppedItem: HTMLElement =
      dropped || (target.querySelector("[data-california-drag-item]") as HTMLElement);
    if (
      droppedItem &&
      target.dataset.californiaDropTarget &&
      droppedItem.dataset.californiaDragItem
    ) {
      return (
        target.dataset.californiaDropTarget
          .split(",")
          .indexOf(droppedItem.dataset.californiaDragItem) != -1
      );
    }
    return false;
  }

  private parentDropTarget(dropped: HTMLElement): HTMLElement | null {
    let parent = dropped.parentElement;
    while (parent) {
      if (parent.classList.contains("drop-target")) {
        return parent;
      }
      parent = parent.parentElement;
    }
    return null;
  }

  private placedCorrect(dropped: HTMLElement, target: HTMLElement | null = null): boolean {
    const targetItem: HTMLElement | null = target || this.parentDropTarget(dropped);
    if (
      targetItem &&
      targetItem.dataset.californiaDropTarget &&
      dropped.dataset.californiaDragItem
    ) {
      return (
        targetItem.dataset.californiaDropTarget
          .split(",")
          .indexOf(dropped.dataset.californiaDragItem) != -1
      );
    }
    return false;
  }

  public IsCorrect(): boolean {
    let correctDrops: number = 0;
    this.dropAnswerTargets.forEach((el: HTMLElement) => {
      if (this.containsCorrect(el)) {
        correctDrops += 1;
      }
    });

    return correctDrops === this.dropAnswerTargets.length;
  }
  public Destroy() {}
}
