<template>
  <router-link
    tag="button"
    class="cali-button"
    :title="LinkItem.Name"
    :class="{'cali-contains-selected': containsSelected, 'cali-selected': isSelected, 'cali-locked': isLocked}"
    :disabled="isLocked"
    :data-target-id="LinkItem.ID"
    :data-target-progress="itemProgress"
    :to="{ name: routeURL, params: { id: routeID }}"
  >
    <span class="label" v-html="LinkItem.Name"></span>
    <span v-if="Action" class="action" v-html="Action"></span>
  </router-link>
</template>

<script>
export default {
  name: "navigation-to-child",
  props: ["LinkItem", "Action"],
  data() {
    return {
      CaliforniaCourse: this.$CaliforniaCourse
    };
  },
  computed: {
    isLocked() {
      let islocked = this.CaliforniaCourse.GetItemByID(
        this.LinkItem.ID
      ).IsLocked();
      if (islocked) return true;

      let parent = this.LinkItem.Parent;
      while (parent) {
        if (this.CaliforniaCourse.GetItemByID(parent.ID).IsLocked()) {
          return true;
        }
        parent = parent.Parent;
      }
      return false;
    },
    itemProgress() {

      return this.LinkItem.Progress;

      // TODO: This is not logical. I don't know why this logic exits
      // return this.LinkItem.Children.length > 0
      //   ? this.LinkItem.Progress
      //   : this.LinkItem.Parent.Progress;
    },
    routeURL() {
      // return this.LinkItem.ChildCount < 1 || this.isLevelOne ? "item" : "collection";
      return this.LinkItem.ChildCount < 1 ? "item" : "collection";
    },
    routeID() {
      // return this.isLevelOne ? this.LinkItem.Children[0].ID : this.LinkItem.ID;
      return this.LinkItem.ID;
    },
    isLevelOne() {
      return (
        this.LinkItem.Parent &&
        this.LinkItem.Parent.ID == CaliforniaCourse.Root.ID
      );
    },
    isSelected() {
      if (!this.CaliforniaCourse.CurrentItem) return false;
      return this.CaliforniaCourse.CurrentItem.ID == this.LinkItem.ID;
    },
    containsSelected() {
      if (!this.CaliforniaCourse.CurrentItem) return false;

      let parent = this.CaliforniaCourse.ContextItem;
      while (parent && parent.ID !== this.CaliforniaCourse.Root) {
        if (parent.ID == this.LinkItem.ID) {
          return true;
        }
        parent = parent.Parent;
      }
      return false;
    }
  }
};
</script>

<style lang="less">
.cali-button.cali-navigation-to-child > .action {
  display: none;
}
</style>
