<template>
      <select class="california-ui-language-select" @change="setGlobalLocale">
        <template v-for="lang in Locales">
          <option v-if="UIStrings[lang.short]"
            :key="lang.short"
            :value="lang.short"
            :selected="lang.short == $i18n.locale"
            v-html="lang.native">
          </option>
        </template>
    </select>
</template>

<script>
import { Localization } from "../../../../California.Localization";

export default {
  name: "language-selector",
  data() {
    return {
      UIStrings: Localization.UIStrings,
      Locales: Localization.Locales
    };
  },
  computed: {},
  methods: {
    setGlobalLocale(e) {
      this.$i18n.locale = e.srcElement.selectedOptions[0].value.toLowerCase();
    }
  }
};
</script>
