import * as Core from "california_core";
import Vue from "vue";
import Progress from "../../California.Progress";

export class CaliQuizControls {
  private item: Core.StructureItem;
  private element: HTMLElement;
  private sounds: Array<HTMLAudioElement> = [];

  private alternativeButtons: Array<HTMLElement> = [];
  private textInputs: Array<HTMLElement> = [];
  private correctAlternativeButtons: Array<HTMLElement> = [];
  private correctFeedback: Array<HTMLElement> = [];
  private incorrectFeedback: Array<HTMLElement> = [];
  private defaultFeedback: Array<HTMLElement> = [];
  private standardFeedback: Array<HTMLElement> = [];
  private allFeedback: Array<HTMLElement> = [];
  private passedFeedback: Array<HTMLElement> = [];
  private failedFeedback: Array<HTMLElement> = [];
  private scoreFeedback: Array<HTMLElement> = [];
  private maxScoreFeedback: Array<HTMLElement> = [];
  private passingScoreFeedback: Array<HTMLElement> = [];
  private submitButtons: Array<HTMLElement> = [];
  private retryButtons: Array<HTMLElement> = [];
  private allInteractions: Array<HTMLElement> = [];
  private correctAlternatives: number = 0;
  private score: number = 1;
  private onClick: any;
  private onKeyUp: any;
  private questionIndex: number = 0;
  private questions: Array<HTMLElement> = [];
  private currentElement: HTMLElement;
  private numberOfTries: number = 0;

  constructor(element: HTMLElement, item: Core.StructureItem) {
    this.element = element;
    this.currentElement = element;
    this.item = item;
    this.score = this.collectScore();
    this.onClick = this.OnClickAlternative.bind(this);
    this.onKeyUp = this.OnInputKeyUp.bind(this);
    this.numberOfTries = 0;
    this.setupQuestion();
  }

  private collectScore(): number {
    let scoreElements = Array.from(
      this.currentElement.querySelectorAll(`[data-california-quiz-score]`)
    );

    let score = 0;

    if (scoreElements.length > 0) {
      scoreElements.forEach(el => {
        score += parseFloat(el.getAttribute("data-california-quiz-score") || "0");
      });
      return score;
    } else if (this.questions.length > 0) {
      this.questions.forEach(el => {
        score += parseFloat(el.dataset.californiaQuizUserScore || "0");
      });
      return score;
    } else {
      return 1;
    }
  }

  private stopAllSounds() {
    this.sounds.forEach((sound: HTMLAudioElement) => {
      sound.pause();
      sound.currentTime = 0;
    });
  }

  private autoCorrect() {
    this.alternativeButtons.forEach((el: any) => {
      this.unSelectItem(el);
    });

    this.correctAlternativeButtons.forEach((el: any) => {
      el.classList.add("correction-key");
      el.checked = true;
      const checkBoxes: Array<HTMLElement> = Array.from(
        el.querySelectorAll(`input[type="checkbox"]`)
      );
      checkBoxes.forEach((inpt: any) => {
        inpt.classList.add("correction-key");
        inpt.checked = true;
      });
    });
    this.setItemScore();
  }

  private setItemScore() {
    this.item.Settings.Score = Math.max(this.score, this.item.Settings.Score || 0);
    Vue.prototype.$CaliforniaCourse.SetItem(this.item);
    Progress.SetProgress();
  }

  private displayCorrect() {
    this.stopAllSounds();
    this.defaultFeedback.forEach(el => {
      el.classList.remove("active");
    });

    this.incorrectFeedback.forEach(el => {
      el.classList.remove("active");
    });

    let evt: any = null;

    this.correctFeedback.forEach(el => {
      el.classList.add("active");
      if (el.nodeName == "AUDIO") {
        (el as HTMLAudioElement).currentTime = 0;
        (el as HTMLAudioElement).play();
      }
      evt = evt || el.dataset.californiaNavigation;
    });

    if (this.questionIndex < this.questions.length - 1) return;

    this.standardFeedback.forEach(el => {
      el.classList.add("active");
      if (el.nodeName == "AUDIO") {
        (el as HTMLAudioElement).currentTime = 0;
        (el as HTMLAudioElement).play();
      }
      evt = evt || el.dataset.californiaNavigation;
    });

    this.setItemScore();
    const nextButtons: Array<HTMLElement> = Array.from(
      document.querySelectorAll(".cali-button.next")
    );
    nextButtons.forEach(el => {
      el.classList.add("hint");
      let durationVal = window.getComputedStyle(el).animationDuration || "0";
      let duration = parseFloat(durationVal) * 1000;
      setTimeout(() => {
        el.classList.remove("hint");
      }, duration);
    });

    if (evt) {
      const commParts: any = evt.split("|");
      Vue.prototype.$Navigation.FromExternalCommand(commParts[0], commParts[1]);
    }
  }

  private selectedFeedbackIndex: number = -1;

  private displayInCorrect() {
    this.stopAllSounds();
    this.defaultFeedback.forEach(el => {
      el.classList.remove("active");
    });

    this.correctFeedback.forEach(el => {
      el.classList.remove("active");
    });

    let evt: any = null;

    this.incorrectFeedback.forEach(el => {
      let tryIndex = parseInt(el.dataset.californiaQuizFeedbackAfterTry || "", 10);
      tryIndex = isNaN(tryIndex) ? 0 : tryIndex;
      if(tryIndex > 0 && tryIndex >= this.numberOfTries) return;

      const elIndex: number = parseInt(el.dataset.californiaQuizFeedbackIndex || "", 10);
      if (isNaN(elIndex) || (!isNaN(elIndex) && elIndex == this.selectedFeedbackIndex)) {
        el.classList.add("active");
      }
      if (el.dataset.autoCorrect == "true") {
        this.autoCorrect();
      }
      if (el.nodeName == "AUDIO") {
        (el as HTMLAudioElement).currentTime = 0;
        (el as HTMLAudioElement).play();
      }
      evt = evt || el.dataset.californiaNavigation;
    });

    if (this.questionIndex < this.questions.length - 1) return;

    this.standardFeedback.forEach(el => {
      let tryIndex = parseInt(el.dataset.californiaQuizFeedbackAfterTry || "", 10);
      tryIndex = isNaN(tryIndex) ? 0 : tryIndex;
      if(tryIndex > 0 && tryIndex >= this.numberOfTries) return;
      
      el.classList.add("active");
      if (el.dataset.autoCorrect == "true") {
        this.autoCorrect();
      }
      if (el.nodeName == "AUDIO") {
        (el as HTMLAudioElement).currentTime = 0;
        (el as HTMLAudioElement).play();
      }
      evt = evt || el.dataset.californiaNavigation;
    });

    if (evt) {
      const commParts: any = evt.split("|");
      Vue.prototype.$Navigation.FromExternalCommand(commParts[0], commParts[1]);
    }
  }

  private renderResults() {
    const isLocalTest = this.item.Settings.ScoreParent == "page";

    let localMaxScore = this.item.Settings.MaxScore || this.questions.length;
    let localPassingScale = this.item.Settings.PassingScale || 1;

    const userScore: number = isLocalTest ? this.score : Progress.GetTotalScore();
    const passingScore: number = isLocalTest
      ? localMaxScore * localPassingScale
      : Progress.GetPassingScore();

    this.scoreFeedback.forEach(el => {
      el.innerHTML = userScore.toString();
    });

    this.maxScoreFeedback.forEach(el => {
      el.innerHTML = isLocalTest ? localMaxScore : Progress.GetMaxScore().toString();
    });

    this.passingScoreFeedback.forEach(el => {
      el.innerHTML = passingScore.toString();
    });

    if (userScore >= passingScore) {
      this.passedFeedback.forEach(el => {
        el.classList.add("active");
      });

      this.failedFeedback.forEach(el => {
        el.classList.remove("active");
      });
    } else {
      this.passedFeedback.forEach(el => {
        el.classList.remove("active");
      });

      this.failedFeedback.forEach(el => {
        el.classList.add("active");
      });
    }
  }

  private changeQuestion() {
    this.questionIndex++;
    this.applyQuestionDOM();
  }

  private removeQuestion() {
    this.questions.forEach(q => {
      q.classList.remove("active");
    });
  }

  private applyQuestionDOM() {
    this.removeQuestionEvents();

    this.removeQuestion();

    this.currentElement = this.questions[this.questionIndex] || this.element;

    this.currentElement.classList.add("active");

    this.alternativeButtons = Array.from(
      this.currentElement.querySelectorAll("[data-california-quiz-alternative]")
    );
    this.correctAlternativeButtons = Array.from(
      this.currentElement.querySelectorAll(
        `[data-california-quiz-alternative][data-california-quiz-correct="true"]`
      )
    );
    this.textInputs = Array.from(
      this.currentElement.querySelectorAll("[data-california-quiz-input]")
    );

    this.correctFeedback = Array.from(
      this.currentElement.querySelectorAll(`[data-california-quiz-feedback="correct"]`)
    );
    this.incorrectFeedback = Array.from(
      this.currentElement.querySelectorAll(`[data-california-quiz-feedback="incorrect"]`)
    );
    this.allFeedback = this.correctFeedback.concat(this.incorrectFeedback);

    let alts: Array<number> = [];
    this.correctAlternativeButtons.forEach((btn: HTMLElement) => {
      const index = parseInt(btn.dataset.californiaQuizAlternative || "", 10);
      if (alts.indexOf(index) == -1) {
        alts.push(index);
      }
    });
    this.correctAlternatives = alts.length;

    this.attachQuestionEvents();
  }

  private setupQuestion() {
    this.sounds = Array.from(document.querySelectorAll("audio"));
    this.sounds = this.sounds.concat(Array.from(document.querySelectorAll("video")));

    this.questions = Array.from(document.querySelectorAll("[data-california-question]"));

    this.applyQuestionDOM();

    this.defaultFeedback = Array.from(
      document.querySelectorAll(`[data-california-quiz-feedback="default"]`)
    );
    this.defaultFeedback.forEach(el => {
      el.classList.add("active");
    });

    this.standardFeedback = Array.from(
      document.querySelectorAll(`[data-california-quiz-feedback="standard"]`)
    );

    this.passedFeedback = Array.from(
      document.querySelectorAll(`[data-california-quiz-result="passed"]`)
    );
    this.failedFeedback = Array.from(
      document.querySelectorAll(`[data-california-quiz-result="failed"]`)
    );
    this.scoreFeedback = Array.from(document.querySelectorAll(`[data-california-quiz-score]`));

    this.maxScoreFeedback = Array.from(
      document.querySelectorAll(`[data-california-quiz-max-score]`)
    );
    this.passingScoreFeedback = Array.from(
      document.querySelectorAll(`[data-california-quiz-passing-score]`)
    );

    this.submitButtons = Array.from(this.element.querySelectorAll("[data-california-quiz-submit]"));
    this.submitButtons.forEach(btn => {
      btn.classList.add("disabled");
      btn.setAttribute("disabled", "disabled");
    });

    this.retryButtons = Array.from(document.querySelectorAll("[data-california-quiz-retry]"));
    this.allInteractions = this.alternativeButtons.concat(this.submitButtons);

    if (this.correctAlternativeButtons.length < 1) {
      this.renderResults();
    }
  }

  private attachQuestionEvents() {
    this.alternativeButtons.forEach(el => {
      el.addEventListener("click", this.onClick);
    });

    this.textInputs.forEach(el => {
      el.addEventListener("keyup", this.onKeyUp);
    });
  }

  private removeQuestionEvents() {
    this.alternativeButtons.forEach(el => {
      el.removeEventListener("click", this.onClick);
    });
    this.textInputs.forEach(el => {
      el.removeEventListener("keyup", this.onKeyUp);
    });
  }

  private toggleSubmitButtons(onOff: boolean) {
    this.submitButtons.forEach(btn => {
      if (onOff) {
        btn.classList.remove("disabled");
        btn.removeAttribute("disabled");
      } else {
        btn.classList.add("disabled");
        btn.setAttribute("disabled", "disabled");
      }
    });
  }

  public AttachEvents() {
    this.submitButtons.forEach(el => {
      el.addEventListener("click", this.OnClickSubmit.bind(this));
    });
    this.retryButtons.forEach(el => {
      el.addEventListener("click", this.OnClickRetry.bind(this));
    });
  }

  public RemoveEvents() {
    this.removeQuestionEvents();

    this.submitButtons.forEach(el => {
      el.removeEventListener("click", this.OnClickSubmit.bind(this));
    });
    this.retryButtons.forEach(el => {
      el.removeEventListener("click", this.OnClickRetry.bind(this));
    });
  }

  public Destroy(){
    this.RemoveEvents();
    this.alternativeButtons = [];
    this.textInputs = [];
  }

  public OnClickRetry(e: any) {
    this.stopAllSounds();

    this.allFeedback.forEach(el => {
      el.classList.remove("active");
    });

    this.standardFeedback.forEach(el => {
      el.classList.remove("active");
    });

    this.defaultFeedback.forEach(el => {
      el.classList.add("active");
    });

    this.allInteractions.forEach((el: any) => {
      el.classList.remove("selected");
      el.classList.remove("disabled");
      el.classList.remove("correction-key");
      el.removeAttribute("disabled");
      const checkBoxes: Array<HTMLInputElement> = Array.from(
        el.querySelectorAll(`input[type="checkbox"]`)
      );
      checkBoxes.forEach((inpt: any) => {
        inpt.classList.remove("selected");
        inpt.classList.remove("disabled");
        inpt.classList.remove("correction-key");
        inpt.removeAttribute("disabled");
        inpt.checked = false;
      });
    });

    this.submitButtons.forEach(btn => {
      btn.classList.add("disabled");
    });
  }

  private selectItem(el: any) {
    this.selectedFeedbackIndex = parseInt(el.dataset.californiaQuizFeedbackIndex || "", 10);
    el.classList.add("selected");
    el.checked = true;
    const checkBoxes: Array<HTMLElement> = Array.from(
      el.querySelectorAll(`input[type="checkbox"]`)
    );
    checkBoxes.forEach((inpt: any) => {
      inpt.classList.add("selected");
      inpt.checked = true;
    });
  }
  private unSelectItem(el: any) {
    el.classList.remove("selected");
    el.checked = false;
    const checkBoxes: Array<HTMLElement> = Array.from(
      el.querySelectorAll(`input[type="checkbox"]`)
    );
    checkBoxes.forEach((inpt: any) => {
      inpt.classList.remove("selected");
      inpt.checked = false;
    });
  }

  public OnInputKeyUp(e: any) {
    let target = e.target;
    this.toggleSubmitButtons(target.value.length > 0);
  }

  public OnClickAlternative(e: any) {
    let target = e.target;
    let altIndex = target.dataset.californiaQuizAlternative;
    while (!altIndex && target) {
      altIndex = target.dataset.californiaQuizAlternative;
      if (!altIndex) target = target.parentElement;
    }

    if (target.classList.contains("disabled")) return;
    e.preventDefault();

    if (!altIndex) {
      return;
    }

    if (this.correctAlternatives == 1) {
      this.alternativeButtons.forEach(btn => {
        if (btn.dataset.californiaQuizAlternative != altIndex) {
          this.unSelectItem(btn);
        }
      });
    }

    const Alternatives: Array<HTMLElement> = Array.from(
      this.currentElement.querySelectorAll(`[data-california-quiz-alternative="${altIndex}"]`)
    );

    Alternatives.forEach(el => {
      if (el.classList.contains("selected")) {
        this.unSelectItem(el);
      } else {
        this.selectItem(el);
      }
    });

    const isOneSelected =
      this.currentElement.querySelectorAll(`[data-california-quiz-alternative].selected`).length >
      0;
    this.toggleSubmitButtons(isOneSelected);
  }

  public OnClickSubmit(e: any) {
    this.RemoveEvents();
    this.setupQuestion();

    const correctItems = this.currentElement.querySelectorAll(
      `[data-california-quiz-correct="true"]`
    );
    const selectedItems = this.currentElement.querySelectorAll(
      `[data-california-quiz-alternative].selected`
    );
    const selectedCorrectItems = this.currentElement.querySelectorAll(
      `[data-california-quiz-correct="true"].selected`
    );

    const isAllCorrectSelected = selectedCorrectItems.length == correctItems.length;
    const isNoMoreSelected = selectedCorrectItems.length == selectedItems.length;

    this.allInteractions.forEach(el => {
      el.classList.add("disabled");
      el.setAttribute("disabled", "disabled");
      const inputs: Array<HTMLElement> = Array.from(el.querySelectorAll("input"));
      inputs.forEach(inpt => {
        inpt.setAttribute("disabled", "disabled");
      });
    });

    let isTextInputCorrect = true;
    for (let i = 0; i < this.textInputs.length; i++) {
      const txtInpt: HTMLInputElement | null = this.textInputs[i].querySelector(
        `input[type="text"]`
      );

      const inpt = txtInpt && txtInpt.value.toLowerCase().replace(/\,/gi, ".");

      const correctValue: string = this.textInputs[i].dataset.californiaQuizInputCorrectValue || "";
      const corr = correctValue.toLowerCase().replace(/\,/gi, ".");

      if (inpt !== corr) {
        isTextInputCorrect = false;
        break;
      }
    }

    if (
      (correctItems.length > 0 && isAllCorrectSelected && isNoMoreSelected) ||
      (this.textInputs.length > 0 && isTextInputCorrect)
    ) {
      if (this.questions[this.questionIndex]) {
        this.questions[this.questionIndex].dataset.californiaQuizUserScore = "1";
      }
      this.score = this.collectScore();
      this.setItemScore();
      this.displayCorrect();

    } else {
      if (this.questions[this.questionIndex]) {
        this.questions[this.questionIndex].dataset.californiaQuizUserScore = "0";
      }
      this.displayInCorrect();
    }
    if (this.questionIndex < this.questions.length - 1) {
      this.changeQuestion();
    } else {
      this.removeQuestion();
      this.renderResults();
    }
  }
}
