<template>
  <section
    class="cali-html"
    :data-california-id="selectedItem.ID"
    ref="content"
    @click="handleClick"
  ></section>
</template>

<script>
import Vue from "vue";
import { CaliHTMLControls } from "./controls";
import { Data } from "../../California.Data";
import EventBus from "../../Vue.EventBus";

export default {
  name: "cali-html",
  props: {
    dataIndex: {
      default: 0,
      type: Number
    },
    selectedItem: {
      default: null,
      type: Object
    },
    htmlContent: {
      default: null,
      type: String
    },
    isPopUp: {
      default: false,
      type: Boolean
    }
  },
  data() {
    return {};
  },
  computed: {},
  methods: {
    finalize: function(data) {
      this.$el.innerHTML = data;
      this.execScripts();
      this.$nextTick().then(() => {
        const subPageObj = this.htmlProtocol.SetupParts(this.$refs.content);
        if (subPageObj) {
          this.$Navigation.SetSubPages(
            subPageObj.pages,
            subPageObj.unique[subPageObj.unique.length - 1],
            subPageObj.unique[0]
          );
        }
        this.htmlProtocol.DisplayProgressContent();
        if (!this.isPopUp) {
          EventBus.$emit("HTMLLoaded", { index: this.dataIndex, data: data });
        }
        this.$Navigation.ToSubPage(0);
        this.$el.dataset.loaded = "true";
      });
    },

    RenderHTMLFile: function() {
      if (this.htmlContent) {
        this.htmlProtocol
          .Render("", this.$refs.content, this.htmlContent)
          .then(data => {
            this.finalize(data);
          });
      } else {
        this.htmlProtocol
          .Render(this.htmlSrc(), this.$refs.content)
          .then(data => {
            this.finalize(data);
          });
      }
    }
  },

  created() {
    this.htmlSrc = () => {
      return (
        this.$ContentFolder +
        Data.ParseDataURL(
          this.selectedItem,
          this.selectedItem.Data[this.dataIndex]
        ).url
      );
    };
    this.execScripts = () => {
      let contentNode = this.$el;
      let scripts = contentNode.querySelectorAll("script");
      for (let i = 0; i < scripts.length; i++) {
        let newScript = document.createElement("script");
        let scriptContent = document.createTextNode(scripts[i].innerHTML);
        newScript.appendChild(scriptContent);
        scripts[i].parentNode.insertBefore(newScript, scripts[i]);
      }
    };
    this.handleClick = e => {
      if (e.target.hasAttribute("disabled")) return;

      const isSelected =
        e.target.classList.contains("california-clicked-item") ||
        e.target.parentNode.classList.contains("california-clicked-item");

      let toggleCommand =
        e.target.dataset.californiaNavigationToggle ||
        e.target.parentNode.dataset.californiaNavigationToggle;

      let navCommand =
        e.target.dataset.californiaNavigation ||
        e.target.parentNode.dataset.californiaNavigation;

      if (isSelected && toggleCommand) {
        navCommand = toggleCommand;
      }

      if (navCommand) {
        let commParts = navCommand.split("|");
        this.$Navigation.FromExternalCommand(commParts[0], commParts[1]);
      }
    };

    this.onSubPageChange = i => {
      this.htmlProtocol.DisplaySubPage(i);
    };
    this.htmlProtocol = new CaliHTMLControls(this.selectedItem);
  },
  mounted() {
    this.RenderHTMLFile();
    EventBus.$on("ToSubPage", this.onSubPageChange);
  },
  updated() {},
  beforeDestroy() {
    EventBus.$off("ToSubPage", this.onSubPageChange);
    this.htmlProtocol.Destroy();
  },
  watch: {
    selectedItem: function(newVal, oldVal) {
      this.RenderHTMLFile();
    },
    "selectedItem.Progress": function() {
      this.htmlProtocol.DisplayProgressContent();
    }
  }
};
</script>

<style lang="less">
.california-sub-page-hidden {
  display: none;
}
.california-progress-hidden {
  display: none;
}
</style>
