<template>
    
    <router-link
      tag="button"
      class="cali-button cali-navigation-to-home"
      :title="itemName"
      :disabled="isHome || !loaded"
      :to="routeURL"
    >
      <span class="label" v-t="{path:'message.HOME_BUTTON_LABEL', locale: $i18n.locale}"></span>
    </router-link>

    
</template>

<script>
import EventBus from "../../../../Vue.EventBus";

export default {
  name: "navigation-to-home",
  data() {
    return {
      CaliforniaCourse: this.$CaliforniaCourse,
      loaded: false
    };
  },
  created() {
    this.loaded = false;
    EventBus.$on("NavigationStarted", () => {
      this.loaded = false;
    });
    EventBus.$on("ContentReady", () => {
      this.loaded = true;
    });
  },
  computed: {
    isHome() {
      return this.CaliforniaCourse.NavigationBase.ID == this.CaliforniaCourse.CurrentItem.ID;
    },
    itemName() {
      return this.CaliforniaCourse.NavigationBase.Name;
    },
    routeURL() {
      let hasChildren = this.CaliforniaCourse.NavigationBase.ChildCount > 0;
      let routeComponent = hasChildren ? "collection" : "item";
      return { name: routeComponent, params: { id: this.CaliforniaCourse.NavigationBase.ID } };
    }
  }
};
</script>
