import { Data } from "../../California.Data";
import Progress from "../../California.Progress";
import Vue from "vue";
import * as Core from "california_core";
import { CaliMediaControlLayout } from "./controls-layout";
import EventBus from "../../Vue.EventBus";

export class CaliMediaControlEvents {
  public static onCanPlayThrough() {
    const _this: any = this;
    if (!_this.element.classList.contains("cali-chroma-key-video")) {
      CaliMediaControlLayout.Resize(_this.item, _this.element, _this.mediaElement);
      CaliMediaControlLayout.Place(_this.item, _this.element, _this.mediaElement);
      _this.element.dataset.loaded = "true";
    } else {
      if (_this.mediaElement.getAttribute("autoplay")) {
        _this.mediaElement.play();
      }
    }
    _this.mediaElement.removeEventListener("canplaythrough", this.onCanPlayThrough);
  }

  public static onFirstPlay() {
    const _this: any = this;
    if (!_this.element.classList.contains("cali-chroma-key-video")) {
      CaliMediaControlLayout.Resize(_this.item, _this.element, _this.mediaElement);
      CaliMediaControlLayout.Place(_this.item, _this.element, _this.mediaElement);
      _this.element.dataset.loaded = "true";
    }
    _this.mediaElement.removeEventListener("play", this.onFirstPlay);
  }

  private static onEndedCommand(item: Core.StructureItem) {
    if (!item.Settings.Aftermedia) return;
    let command = "";
    let val = "";

    if (typeof item.Settings.Aftermedia == "string") {
      command = item.Settings.Aftermedia;
    } else {
      command = item.Settings.Aftermedia.command;
      val = item.Settings.Aftermedia.value;
    }

    Vue.prototype.$Navigation.FromExternalCommand(command, val);
  }

  public static onEnded() {
    const _this: any = this;
    if (_this.item.Settings.Progresstype == "mediaend") {
      _this.item.Progress = 1;
      Data.Course.SetItem(_this.item);
      Progress.SetProgress();
    }
    CaliMediaControlEvents.onEndedCommand(_this.item);
  }

  public static onResize() {
    const _this: any = this;
    clearTimeout(_this.resizeTimer);
    _this.resizeTimer = setTimeout(() => {
      CaliMediaControlLayout.Resize(_this.item, _this.element, _this.mediaElement);
      CaliMediaControlLayout.Place(_this.item, _this.element, _this.mediaElement);
      EventBus.$emit("subtitlecuechange", {
        element: _this.element,
        mediaElement: _this.mediaElement
      });
    }, 10);
  }
}
