
export class StructureItem {
    public ID: string;
    public Name: string;
    public Description: string;
    public RefID: string;
    public Parent: StructureItem;
    public ParentIDs: Array<string>
    public Children: Array<StructureItem>;
    public ChildCount: number;
    public TotalChildCount: number;
    public Visited: boolean;
    public Progress: number;
    public SubProgress: number;
    public ResumeIndex: number;
    public Settings: { [key: string]: any };
    public Index: number;
    public FlatIndex: number;
    public Data: Array<string>;
    public ContentType: string;

    constructor(options: any = {}) {
        this.ID = options.ID || `${Math.random()}-${Math.random()}`;
        this.Name = options.Name || 'Unnamed';
        this.Description = options.Description || "";
        this.RefID = options.RefID || null;
        this.Parent = options.Parent || null;
        this.ParentIDs = [];
        this.Children = options.Children || [];
        this.ChildCount = 0;
        this.TotalChildCount = 0;
        this.Visited = false;
        this.Progress = options.Progress || 0;
        this.SubProgress = options.SubProgress || -1;
        this.Settings = Object.assign({}, options.Settings);
        this.Index = options.Index || 0;
        this.FlatIndex =  options.FlatIndex || 0;
        this.Data = options.Data instanceof Array ? options.Data : (options.Data ? [options.Data] : null);
        this.ContentType = options.ContentType || 'iframe';
    }

    public FirstChild(): StructureItem {
        return this.Children[0];
    }

    public Before(): StructureItem {
        if (!this.Parent) return null;
        return this.Parent.Children[this.Index - 1];
    }

    public After(): StructureItem {
        if (!this.Parent) return null;
        return this.Parent.Children[this.Index + 1];
    }

    public Unlock() {
        this.Settings.Locked = false;
    }

    public Lock() {
        this.Settings.Locked = true;
    }

    public EvaluateLock() {
        let lock = this.Settings.Locked;
        let prev = this.Before();

        if (!prev) {
            this.Unlock()
            return false;
        }

        let porgressLimit = 1;
        let progresstype = prev.Settings.Progresstype;
        let progress = prev.Progress;
        if (progresstype && progresstype.indexOf(":") != -1) {
          const parts = progresstype.split(":");
          progresstype = parts[0];
          porgressLimit = parseFloat(parts[1].toString());
        }

        switch (lock) {
            case "sequential":
                if (progress >= porgressLimit){ this.Unlock();}
                break
            case "manual":
                break
            default:
                if (progress >= porgressLimit) this.Unlock();
        }

        return this.Settings.Locked;
    }

    public IsLocked(): boolean {
        this.EvaluateLock()
        return this.Settings.Locked || false;
    }

};