
<template>
  <section
    :class="{'active':modalVisible}"
    @click="hideModal"
    class="cali-popup-background"
    ref="popBG"
  >
    <section class="cali-popup-window" @click="preventClick">
      <button
        class="cali-button cali-close-popup"
        :title="$t('message.EXIT_BUTTON_LABEL', $i18n.locale)"
        @click="hideModal"
      >
        <span class="label" v-html="$t('message.EXIT_BUTTON_LABEL', $i18n.locale)"></span>
      </button>
      <section v-if="ViewItem" class="cali-popup-item" ref="popItem">
        <template v-for="(url, i) in ViewItem.Data">
          <component
            :is="selectComponent(ViewItem, url)"
            v-bind="{selectedItem: ViewItem, dataIndex: i, dataLength: ViewItem.Data.length, isPopUp: true}"
            :data-cali-ViewItem="ViewItem.ID"
            :data-cali-data-type="selectComponent(ViewItem, url)"
            class="cali-component"
            :class="[{'locked': ViewItem.IsLocked() }, ViewItem.Settings.Template]"
            data-cali-index="0"
            :key="ViewItem.ID + '-' + i "
          />
        </template>
      </section>
    </section>
  </section>
</template>

<script>
import EventBus from "../../../../Vue.EventBus";
import { Data } from "../../../../California.Data";
import { setTimeout } from "timers";

let comps = Object.assign(
  {},
  Data.LoadComponentFolder(
    require.context("../../../../California.ContentTypes", true, /\.vue$/)
  )
);

export default {
  name: "modal-iframe",
  props: {},
  components: comps,
  data() {
    return {
      modalVisible: false,
      ViewItem: null
    };
  },
  computed: {},
  created() {
    EventBus.$on("showmodal", this.showModal);
    EventBus.$on("hidemodal", this.hideModal);
  },
  methods: {
    selectComponent(child, url) {
      return Data.ParseDataURL(child, url).contenttype;
    },
    getIndex() {
      this.ChildIndex += 1;
      return this.ChildIndex - 1;
    },
    showModal(e) {
      this.ViewItem = e;
      this.modalVisible = true;
      this.$Navigation.inPopUp = true;
    },
    preventClick(e) {
      e.stopPropagation();
    },
    hideModal(e) {
      e.stopPropagation();
      this.$refs.popBG.classList.remove("active");
      this.modalVisible = false;
      let wait = Math.max(
        parseFloat(
          window.getComputedStyle(this.$refs.popItem).transitionDuration
        ),
        parseFloat(window.getComputedStyle(this.$refs.popBG).transitionDuration)
      );
      setTimeout(() => {
        this.ViewItem = null;
        this.$Navigation.inPopUp = false;
      }, wait * 1000);
    }
  },
  mounted() {},
  beforeDestroy() {
    EventBus.$off("showmodal", this.showModal);
    EventBus.$off("hidemodal", this.hideModal);
  }
};
</script>

<style lang="less" scoped>
.cali-popup-background {
  position: absolute;
  width: 100%;
  height: 0;
  top: -100%;
  left: 0;
  z-index: 9998;
  background-color: rgba(0, 0, 0, 0.8);
  .cali-popup-window {
    width: 50%;
    height: 50%;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border: 1px solid #000;
    padding: 10px;
    .cali-popup-item {
      display: block;
      position: relative;
      width: 100%;
      height: 100%;
    }
  }
}
&.active {
  height: 100%;
  top: 0;
}
</style>
