export class ContentResize {

    public static GetPropForDataIndex(size: any, index: number): { [k: string]: any } {
        if (!(size instanceof Array)) return size;
        if (index < 0 || index >= size.length) index = size.length - 1;
        return size[index]
    }

}

export enum ContentResizeModes {
    Content = 'content',
    Parent = 'parent',
    Fixed = 'fixed'
}