<template>
  <ul>
    <template v-for="child in ParentItem.Children">
      <li
        v-if="!child.Settings.Hidden && !child.Settings.ExcludeFromMenu"
        :key="child.ID"
        :data-california-id="child.ID"
        :class="{'cali-has-sub-menu': (child.ChildCount > 0)}"
      >
        <navigation-to-child v-if="!child.Settings.Hidden" :LinkItem="child"/>
        <navigation-tree v-if="child.ChildCount > 0 && !child.Settings.Hidden" :ParentItem="child"></navigation-tree>
      </li>
    </template>
  </ul>
</template>

<script>
import NavigationToChild from "./ToChild";
import NavigationTree from "./Tree";

export default {
  name: "navigation-tree",
  props: ["ParentItem"],
  components: {
    NavigationToChild,
    NavigationTree
  }
};
</script>
