import * as Core from "california_core";
import { ContentResize, ContentResizeModes } from "../../California.ContentSize";
import Vue from "vue";
import EventBus from "../../Vue.EventBus";
import { Data } from "../../California.Data";
import { Localization } from "../../California.Localization";

export class CaliHTMLControls {
  private fileReader: Core.IO.Reader;
  private item: Core.StructureItem;
  private element: HTMLElement | null;
  private OnResize: any;

  constructor(item: Core.StructureItem) {
    this.fileReader = new Core.IO.Reader();
    this.item = item;
    this.element = null;
    this.OnResize = this.onResizeHandler.bind(this);
  }

  private relativePath(url: string) {
    let path = url;
    return path.substr(0, path.lastIndexOf("/"));
  }

  private resizeToFixed(docElement: HTMLElement) {
    let size = this.item.Settings.Size;

    if (window.innerWidth < 769 && this.item.Settings.SmallSize) {
      size = this.item.Settings.SmallSize;
    }

    let index = parseInt(docElement.dataset.caliIndex || "", 10);
    if (!isNaN(index)) size = ContentResize.GetPropForDataIndex(size, index);
    docElement.style.display = "inline-block";
    docElement.style.width = size.width;
    docElement.style.height = size.height;
  }

  private resizeToContent(docElement: HTMLElement) {
    docElement.style.width = "auto";
    docElement.style.height = "auto";
  }

  private resizeToParent(docElement: HTMLElement) {
    if (!docElement.parentElement) return;
    docElement.style.width = docElement.parentElement.offsetWidth + "px";
    docElement.style.height = docElement.parentElement.offsetHeight + "px";
  }

  private applyStyle(docElement: HTMLDocument, style: string) {
    let styleElem: HTMLStyleElement = document.createElement("style");
    styleElem.type = "text/css";
    styleElem.appendChild(document.createTextNode(style));
    docElement.body.insertBefore(styleElem, docElement.body.firstChild);
  }

  private resize(docElement: HTMLElement) {
    const mode = this.item.Settings.ResizeMode
      ? this.item.Settings.ResizeMode.toLowerCase()
      : ContentResizeModes.Parent;

    switch (mode) {
      case ContentResizeModes.Content:
        this.resizeToContent(docElement);
        break;
      case ContentResizeModes.Parent:
        this.resizeToParent(docElement);
        break;
      case ContentResizeModes.Fixed:
        this.resizeToFixed(docElement);
        break;
    }
  }

  private addCaliforniaData(docElement: HTMLElement) {
    if (!docElement.firstChild) return;
    const progData = docElement.querySelectorAll("[data-california-progress-item]");
    for (let i = 0; i < progData.length; i++) {
      const el = progData[i] as HTMLElement;
      const courseItem = Data.Course.GetItemByID(el.dataset.californiaProgressItem || "");
      if (courseItem) {
        el.dataset.progress = courseItem.Progress.toString();
        if (courseItem.Settings.Locked) {
          el.dataset.locked = courseItem.Settings.Locked.toString();
        }
      }
    }
  }

  private place(docElement: HTMLElement) {
    const index = parseInt(docElement.dataset.caliIndex || "", 10);
    let position = !isNaN(index)
      ? ContentResize.GetPropForDataIndex(this.item.Settings.Position, index)
      : null;
    if (window.innerWidth < 769 && this.item.Settings.SmallPosition) {
      position = ContentResize.GetPropForDataIndex(this.item.Settings.SmallPosition, index);
    }

    if (position) {
      docElement.style.position = "absolute";
      docElement.style.top = position.y;
      docElement.style.left = position.x;
    }
  }

  private parseHTMLData(url: string, data: string) {
    let parser = new DOMParser();
    let doc = parser.parseFromString(data, "text/html");
    let allSrc = doc.querySelectorAll("[src]");

    for (let i = 0; i < allSrc.length; i++) {
      let oldSrc = allSrc[i].getAttribute("src");
      allSrc[i].setAttribute("src", this.relativePath(url) + "/" + oldSrc);
    }

    let allStyle = doc.querySelectorAll("[style]");
    for (let i = 0; i < allStyle.length; i++) {
      let oldStyle = allStyle[i].getAttribute("style") || "";
      oldStyle = oldStyle.replace(/url\((.*?)\)/gi, this.cleanCSSUrl);
      oldStyle = oldStyle.replace(/{CSSURL}/gi, this.relativePath(url));
      allStyle[i].setAttribute("style", oldStyle);
    }

    return doc;
  }

  private cleanCSSUrl(match: any): string {
    match = match.replace(/\"/g, "");
    match = match.replace(/\'/g, "");
    return match.replace(/url\(/g, "url({CSSURL}/");
  }

  private addscript(docElement: HTMLDocument, script: string) {
    let scriptElem: HTMLScriptElement = document.createElement("script");
    scriptElem.appendChild(document.createTextNode(script));
    docElement.body.appendChild(scriptElem);
  }

  private async loadScripts(url: string, doc: HTMLDocument, htmlElement: HTMLElement) {
    if (!doc.head) return;
    if (!doc.body) return;

    let allScripts: Array<HTMLElement> = Array.from(doc.head.querySelectorAll(`script`));
    allScripts = allScripts.concat(Array.from(doc.body.querySelectorAll(`script`)));

    for (let i = 0; i < allScripts.length; i++) {
      let scriptSrc = allScripts[i].getAttribute("src");
      if (!scriptSrc || scriptSrc.length < 1) continue;
      await this.fileReader.Text(scriptSrc).then((scriptData: string) => {
        this.addscript(doc, scriptData);
        const parent: HTMLElement | null = allScripts[i].parentElement;
        if (parent) {
          parent.removeChild(allScripts[i]);
        }
      });
    }
  }

  private async loadCSS(url: string, doc: HTMLDocument, htmlElement: HTMLElement) {
    if (!doc.head) return;

    let allCss = doc.head.querySelectorAll(`link[rel="stylesheet"]`);

    for (let i = allCss.length - 1; i > -1; i--) {
      let cssHref = allCss[i].getAttribute("href");
      if (!cssHref || cssHref.length < 1) continue;

      const cssURL = this.relativePath(url);
      await this.fileReader.Text(cssURL + "/" + cssHref).then((cssData: string) => {
        cssData = cssData.replace(/url\((.*?)\)/gi, this.cleanCSSUrl);
        cssData = cssData.replace(/{CSSURL}/gi, cssURL);
        this.applyStyle(doc, cssData);
      });
    }
  }

  public SetupParts(htmlElement: Element): { pages: Array<Element>; unique: Array<number> } | null {
    if (!htmlElement) return null;
    const pages: Array<HTMLElement> = Array.from(
      htmlElement.querySelectorAll(`*[data-california-sub-page]`)
    );
    let uniquePages: Array<number> = [0];

    pages.forEach((page: HTMLElement) => {
      page.classList.add("california-sub-page-hidden");
      const index = parseInt(page.dataset.californiaSubPage || "", 10);
      if (uniquePages.indexOf(index) == -1) {
        uniquePages.push(index);
      }
    });

    return {
      pages: Array.from(pages),
      unique: pages.length > 0 ? uniquePages.sort((a, b) => a - b) : []
    };
  }

  public DisplayProgressContent(subpageIndex: number) {
    if (!this.element) return;
    const whenCompleted: Array<HTMLElement> = Array.from(
      this.element.querySelectorAll(`*[data-california-show-when-completed]`)
    );
    whenCompleted.forEach((page: HTMLElement) => {
      const val: string = page.dataset.californiaShowWhenCompleted || "";
      const end: string = page.dataset.californiaHideWhenCompleted || "";

      const treshold: number = val == "true" ? 1 : parseFloat(val);
      const upperTreshold: number = isNaN(parseFloat(end))
        ? Number.MAX_SAFE_INTEGER
        : parseFloat(end);

      if (
        page.classList.contains("california-sub-page-active") &&
        this.item.Progress < upperTreshold
      ) {
        page.classList.remove("california-progress-hidden");
      } else {
        page.classList.add("california-progress-hidden");
      }

      if (this.item.Progress >= treshold && this.item.Progress < upperTreshold) {
        page.classList.remove("california-progress-hidden");
        page.classList.remove("california-sub-page-hidden");
        EventBus.$emit("PorgressContentDisplayed", page);

        if (page.dataset.californiaSubPage && !page.dataset.californiaPreventSubPageNav) {
          const toPage = parseInt(page.dataset.californiaSubPage);
          if (toPage != Vue.prototype.$Navigation.SubPageLocation && toPage > subpageIndex) {
            Vue.prototype.$Navigation.ToSubPage(toPage);
          }
        }
      }
    });
  }

  public DisplaySubPage(index: number) {
    if (!this.element) return;

    const subPages: Array<HTMLElement> = Array.from(
      this.element.querySelectorAll(`[data-california-sub-page]`)
    );

    subPages.forEach((page: HTMLElement) => {
      const keepOpen = page.dataset.californiaSubPageKeepOpen;
      let openUntil: number =
        keepOpen == "true" ? Number.MAX_SAFE_INTEGER : parseInt(keepOpen || "", 10);
      const pageIndex = parseInt(page.dataset.californiaSubPage || "", 10);
      if (isNaN(openUntil) && index != pageIndex) {
        page.classList.add("california-sub-page-hidden");
        page.classList.remove("california-sub-page-active");
      } else if (!isNaN(openUntil) && (index < pageIndex || index > openUntil)) {
        page.classList.add("california-sub-page-hidden");
        page.classList.remove("california-sub-page-active");
      } else {
        page.classList.remove("california-sub-page-hidden");
        page.classList.add("california-sub-page-active");
        if (this.element) this.element.classList.remove("empty");
      }
    });

    this.DisplayProgressContent(index);

    const clickedItems: Array<HTMLElement> = Array.from(
      this.element.querySelectorAll(`.california-clicked-item`)
    );
    clickedItems.forEach(clickedItem => {
      clickedItem.classList.add("california-visited-item");
      clickedItem.classList.remove("california-clicked-item");
    });

    const pageClickers: Array<HTMLElement> = Array.from(
      this.element.querySelectorAll(`[data-california-navigation="navToSubPage|${index}"]`)
    );
    if (pageClickers)
      pageClickers.forEach(pageClicker => {
        pageClicker.classList.add("california-clicked-item");
      });

    let totalHeight: number = 0;
    const childNodes: Array<Element> = Array.from(this.element.children);

    childNodes.forEach((el: Element) => {
      totalHeight += el.getBoundingClientRect().height;
    });

    if (subPages.length > 0 && totalHeight == 0) {
      this.element.classList.add("empty");
    }
  }

  private onResizeHandler() {
    if (this.element){
      this.resize(this.element);
      this.place(this.element);
    } 
  }

  public Destroy() {
    EventBus.$off("windowResize", this.OnResize);
  }

  public Render(url: string, htmlElement: HTMLElement, htmlContent: string = ""): Promise<string> {
    this.element = htmlElement;

    EventBus.$on("windowResize", this.OnResize);

    if (htmlContent.length > 0) {
      this.resize(htmlElement);
      this.place(htmlElement);
      return new Promise(resolve => {
        resolve(htmlContent);
      });
    }

    const localizedURL = Localization.LocalizeURL(url);

    return new Promise((resolve, reject) => {
      this.fileReader
        .Exists(localizedURL)
        .then((exists: boolean) => {
          if (exists) {
            return this.fileReader.Text(localizedURL);
          } else {
            return this.fileReader.Text(url);
          }
        })
        .then((data: string) => {
          const doc = this.parseHTMLData(url, data);
          this.addCaliforniaData(doc.body);
          return doc;
        })
        .then(async (doc: any) => {
          await this.loadCSS(url, doc, htmlElement);
          return doc;
        })
        .then(async (doc: any) => {
          await this.loadScripts(url, doc, htmlElement);
          return doc;
        })
        .then((doc: any) => {
          this.resize(htmlElement);
          this.place(htmlElement);
          resolve(doc.body.innerHTML);
        });
    });
  }
}
