<template>
  <section v-if="CaliforniaCourse.CurrentItem.ChildCount < 1" class="cali-item">
    <section class="cali-component-area">
      <template v-for="(url, i) in ViewItem.Data">
        <component
          v-if="ViewItem.Data"
          :is="selectComponent(ViewItem, url)"
          v-bind="{selectedItem: ViewItem, dataIndex: i, dataLength: ViewItem.Data.length}"
          :data-cali-ViewItem="ViewItem.ID"
          :data-cali-data-type="selectComponent(ViewItem, url)"
          class="cali-component"
          :class="[{'locked': ViewItem.IsLocked() }, ViewItem.Settings.Template]"
          :data-cali-index="getIndex()"
          :key="ViewItem.ID + '-' + i "
        />
      </template>
    </section>
  </section>
</template>

<script>
import Vue from "vue";
import { Data } from "../../../California.Data";
let comps = Object.assign(
  {},
  Data.LoadComponentFolder(
    require.context("../../../California.ContentTypes", true, /\.vue$/)
  )
);

export default {
  name: "item",
  components: comps,
  data() {
    return {
      CaliforniaCourse: this.$CaliforniaCourse
    };
  },
  beforeRouteUpdate: (to, from, next) => {
    Vue.prototype.$Navigation.FromRoute(to, from, next);
  },
  beforeRouteEnter: (to, from, next) => {
    Vue.prototype.$Navigation.FromRoute(to, from, next);
  },
  created() {
    this.ChildIndex = 0;
  },
  mounted() {
    this.ChildIndex = 0;
  },
  updated() {
    this.ChildIndex = 0;
  },
  computed: {
    ViewItem() {
      return this.CaliforniaCourse.CurrentItem;
    }
  },
  methods: {
    selectComponent(child, url) {
      return Data.ParseDataURL(child, url).contenttype;
    },
    getIndex() {
      this.ChildIndex += 1;
      return this.ChildIndex - 1;
    }
  },
  watch: {
    ViewItem: function(newVal, oldVal) {
      this.ChildIndex = 0;
    }
  }
};
</script>

<style lang="less">
.cali-component-area {
  position: relative;
}
</style>
