import { Structure } from '../Data/Structure';
import { StructureItem } from '../Data';
import { NavigationResult, ResultCode } from './NavigationResult';


export class Navigator {

    constructor(structure: Structure) {

        if (!structure) {
            throw new Error('Navigator() needs a Structure object as constructor parameter');
        }
        this.structure = structure;
        return this;
    }

    private structure: Structure;

    private canNavigate(id: string): ResultCode {
        let navItem = this.structure.GetItemByID(id);
        if (!navItem) return ResultCode.NotFound;

        let locked = navItem.IsLocked();

        while (!locked && navItem.Parent) {
            if (navItem.IsLocked()) {
                locked = true
                break
            }
            navItem = navItem.Parent
        }

        if (locked) return ResultCode.Locked;

        return ResultCode.OK;
    }

    public Position: number = 0;

    public Navigate(parent: StructureItem, position: number): Promise<NavigationResult> {

        let navID;
        if (!parent && !position) {
            navID = this.structure.Root.ID
        }
        else if (parent) {
            navID = parent.Children[position].ID;
        }

        let canNav = this.canNavigate(navID)
        let navItem = this.structure.GetItemByID(navID);

        this.Position = navItem ? navItem.Index : -1;

        if (canNav == ResultCode.OK || canNav == ResultCode.Locked) {
            this.structure.SetItem(navItem);
        }

        return new Promise<NavigationResult>((resolve, reject) => {
            resolve(new NavigationResult(
                canNav,
                (canNav ==  ResultCode.OK || canNav == ResultCode.Locked ? navItem : null)
            ))
        })

    }

    public Next(): Promise<NavigationResult> {
        return new Promise<NavigationResult>(resolve => {
            this.Navigate(
                this.structure.CurrentItem.Parent,
                this.structure.CurrentItem.Index + 1
            ).then(result => {
                resolve(result);
            });
        })
    }

    public Previous(): Promise<NavigationResult> {
        return new Promise<NavigationResult>(resolve => {
            this.Navigate(
                this.structure.CurrentItem.Parent,
                this.structure.CurrentItem.Index - 1
            ).then(result => {
                resolve(result);
            });
        })
    }

    public ToParent(): Promise<NavigationResult> {
        return new Promise<NavigationResult>(resolve => {
            var item = this.structure.CurrentItem.Parent
            this.Navigate(item, item.Index).then(result => {
                resolve(result);
            });
        })
    }

    public ToFirstChild(): Promise<NavigationResult> {
        return new Promise<NavigationResult>(resolve => {
            this.Navigate(this.structure.CurrentItem, 0).then(result => {
                resolve(result);
            });
        })
    }

    public GetNextItem(): StructureItem {
        let nextItem = this.structure.CurrentItem.Settings.NextItem;
        if (nextItem) {
            switch (nextItem.toLowerCase()) {
                case "parent":
                    return this.structure.GetItemByID(
                        this.structure.CurrentItem.Parent.ID
                    );
                case "home":
                    return this.structure.GetItemByID(
                        this.structure.NavigationBase.ID
                    );
                default:
                    return this.structure.GetItemByID(nextItem);
            }
        }
        return this.structure.GetFlatList()[
            this.structure.FlatListPosition + 1
        ];
    };

    public GetPreviousItem(): StructureItem {
        let prevItem = this.structure.CurrentItem.Settings.PreviousItem;
        if (prevItem) {
            switch (prevItem.toLowerCase()) {
                case "parent":
                    return this.structure.GetItemByID(
                        this.structure.CurrentItem.Parent.ID
                    );
                case "home":
                    return this.structure.GetItemByID(
                        this.structure.NavigationBase.ID
                    );
                default:
                    return this.structure.GetItemByID(prevItem);
            }
        }
        return this.structure.GetFlatList()[
            this.structure.FlatListPosition - 1
        ];
    };

    public NavigateToID(id: string): Promise<NavigationResult> {
        return new Promise<NavigationResult>(resolve => {
            var item = this.structure.GetItemByID(id);
            if (!item) {
                resolve(new NavigationResult(ResultCode.NotFound, null))
            }
            else {
                this.Navigate(item.Parent, item.Index).then(result => {
                    resolve(result);
                });
            }
        })
    }

}