import { XHR } from "../Net/XHR";

export class Reader {
  public JSON(url: string): Promise<object> {
    return new Promise((resolve, reject) => {
      new XHR()
        .Get(url)
        .then(data => {
          try {
            var jsonResponse = JSON.parse(data);
            resolve(jsonResponse);
          } catch (err) {
            reject(new Error(err));
          }
        })
        .catch(reason => {
          reject(reason);
        });
    });
  }

  public Text(url: string): Promise<object> {
    return new Promise((resolve, reject) => {
      new XHR()
        .Get(url)
        .then(data => {
          resolve(data);
        })
        .catch(reason => {
          reject(reason);
        });
    });
  }

  public Exists(url: string): Promise<boolean> {
    return new Promise((resolve, reject) => {
      new XHR()
        .Get(url, "HEAD")
        .then(data => {
          resolve(true);
        })
        .catch(reason => {
          resolve(false);
        });
    });
  }
}
