import VHO from "./scripts/course-specific/VHO";
import IFNE from "./scripts/course-specific/IFNE";
const win = window as any;
const California = win.California;
let ifne: any;

const init = function() {
  const isVHO = California.Course.Root.ID == "541e3a7a-aa87-44d0-afbc-666d52656b4b";
  const isIFNE = California.Course.Root.ID == "67b02d1c-0df4-447c-b992-e02cea22b8fd";

  if (isVHO) {
    const vho = new VHO();
    vho.ChapterSelector();
    win.California.Events.$on("subtitlecuechange", vho.OnSubtitleCueChange);
  } else if (isIFNE) {
    if (!ifne) ifne = new IFNE();
    attachIFNEEvents();
  }
  win.California.Events.$off("ContentReady", init);
  win.California.Events.$off("HTMLLoaded", init);
};

const onNavDone = function(){
  win.California.Events.$off("NavigationDone", onNavDone);
  win.California.Events.$on("ContentReady", attachIFNEEvents);
  win.California.Events.$on("HTMLLoaded", attachIFNEEvents);
}

const attachIFNEEvents = function() {
  win.California.Events.$on("PorgressContentDisplayed", function(e: HTMLElement) {
    ifne.OnPorgressContentDisplayed(e);
  });
  win.California.Events.$on("ToSubPage", function(e: number) {
    ifne.OnSubPageChange(e);
  });
  win.California.Events.$on("NavigationStarted", function(e: number) {
    ifne.HideMenu();
    ifne.ResetSwipe();
  });

  win.California.Events.$on("NavigationDone", onNavDone);

  win.California.Events.$on("ContentReady", function() {
    ifne.ResizeContent();
  });

  ifne.ResizeContent();
  ifne.MobileHomeButton();
  ifne.AddGalleryButton();

  win.California.Events.$off("ContentReady", attachIFNEEvents);
  win.California.Events.$off("HTMLLoaded", attachIFNEEvents);

};

window.addEventListener("CaliforniaStartup", () => {
  win.California.Events.$on("ContentReady", init);
  win.California.Events.$on("HTMLLoaded", init);
});
