<template>
  <cali-html
    v-bind="{selectedItem: selectedItem, dataIndex: dataIndex, htmlContent: htmlContent}"
    :data-cali-ViewItem="selectedItem.ID"
    data-cali-data-type="html"
    class="cali-component"
    :data-cali-index="dataIndex"
  ></cali-html>
</template>

<script>
import { CaliQuizControls } from "./controls";
import caliHtml from "../CaliHtml/CaliHtml.vue";
import EventBus from "../../Vue.EventBus";

export default {
  components: {
    caliHtml
  },
  name: "cali-quiz",
  props: {
    dataIndex: {
      default: 0,
      type: Number
    },
    selectedItem: {
      default: null,
      type: Object
    },
    htmlContent: {
      default: null,
      type: String
    }
  },
  data() {
    return {};
  },
  computed: {},
  methods: {
    setProtocol() {
      if (this.loadedHTML && this.loadedHTML.index == this.dataIndex) {
        this.htmlQuizProtocol = new CaliQuizControls(
          this.$el,
          this.selectedItem
        );
        this.htmlQuizProtocol.AttachEvents();
      }
    },
    onHTMLLoaded(e) {
      this.loadedHTML = e;
      const preloaded = document.querySelector(".cali-preloader.removed");
      if (!preloaded) {
        EventBus.$off("ContentReady", this.setProtocol);
        EventBus.$on("ContentReady", this.setProtocol);
      } else {
        this.setProtocol();
      }
    },
    Cleanup() {
      if (this.htmlQuizProtocol) {
        this.htmlQuizProtocol.Destroy();
        this.htmlQuizProtocol = null;
      }
    }
  },
  created() {
    this.loadedHTML = null;
  },
  mounted() {
    EventBus.$off("HTMLLoaded", this.onHTMLLoaded);
    EventBus.$on("HTMLLoaded", this.onHTMLLoaded);
  },
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {
    EventBus.$off("HTMLLoaded", this.onHTMLLoaded);
    this.Cleanup();
  },
  destroyed() {}
};
</script>

<style lang="less">
[data-california-quiz-feedback]:not(.active),
[data-california-quiz-result]:not(.active) {
  display: none;
}
</style>
